import {Meteor} from 'meteor/meteor';
import {Sb_CloseSavingReact} from "../../imports/collections/sb_closeSaving";

if (Meteor.isServer) {
    Meteor.publish('sb_closeSavingReact', function () {
        if (this.userId) {
            return Sb_CloseSavingReact.find({});
        }
        return this.ready();

    });
}
