import {Meteor} from 'meteor/meteor';
import {Sb_ClientReact} from "../../imports/collections/sb_client";
import {Sb_RepaymentSchedule} from "../../imports/collections/sb_repaymentSchedule";
import {Sb_Disbursement} from "../../imports/collections/sb_disbursement";
import {CountsCollectionSheet} from "../../imports/collections/sb_repaymentSchedule";

if (Meteor.isServer) {
    Meteor.publish('sb_loanCollectionSheet', function () {
        if (this.userId) {
            let self = this;
            let selector = {};
            selector.isPaid = false;
            selector.date = {$lte: moment().format("YYYY-MM-DD")};
            let disbursementList = Sb_Disbursement.find({status: "Active"}).map(o => o._id);
            selector.loanId = {$in: disbursementList};
            let collect = Sb_RepaymentSchedule.aggregate([
                    {
                        $match: selector
                    },
                    {
                        $lookup:
                            {
                                from: "sb_disbursement",
                                localField: "loanId",
                                foreignField: "_id",
                                as: "disbursementDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$disbursementDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: {"disbursementDoc.status": "Active"}
                    },
                    {
                        $group: {
                            _id: {
                                branchId: "$branchId",
                                loanId: "$loanId",
                            },
                            count: {$sum: 1}
                        }
                    }, {
                        $group: {
                            _id: "$_id.branchId",
                            count: {$sum: 1}
                        }
                    },

                ],
                {
                    allowDiskUse: true
                });
            //console.log(collect);
            /*collect.forEach((obj) => {
                self.added('counts', obj._id, {
                    count: (obj && obj.count || 0)
                });
            })*/
            collect.forEach((obj) => {
                CountsCollectionSheet.remove({_id: obj._id});
            })
            if (collect.length > 0) {
                CountsCollectionSheet.rawCollection().insert(collect);
            } else {
                CountsCollectionSheet.remove({});
            }
        }
        return this.ready();

    });


    Meteor.publish('countsCollectionSheet', function () {
        if (this.userId) {
            return CountsCollectionSheet.find();

        }
        return this.ready();

    });
}