import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_Group, Sb_GroupReact, Sb_GroupAudit} from "../../imports/collections/sb_group"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchGroup({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        phoneNumber: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        note: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        address: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        nationId: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        order: parseFloat(q)
                    }, {
                        checkIn: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            data.content = Sb_Group.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_Group.find(selector).count();
            return data;
        }
    },
    sb_insertGroup(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let groupDoc = Sb_Group.findOne({branchId: doc.branchId}, {sort: {order: -1}});
                doc.order = (groupDoc && groupDoc.order || 0) + 1;
                let id = Sb_Group.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_GroupReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateGroup(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Group.findOne({_id: id});
                let isUpdated = Sb_Group.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_GroupReact, id, Sb_GroupAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeGroup(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_Group.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_GroupReact, doc._id, Sb_GroupAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findGroupById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};

                selector._id = id;
                return Sb_Group.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findGroupByIdList(idList, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = {$in: idList};
                return Sb_Group.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchGroupOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }


                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {_id: q}
                    ];
                }

                return Sb_Group.find(selector, {sort: {createdAt: -1},limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + (obj.date || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_Group._ensureIndex({
    name: 1,
    phoneNumber: 1,
    date: 1,
    address: 1,
    note: 1,
    branchId: 1
}, {unique: 1, name: "Sb_GroupUnique"});