import {Mongo} from "meteor/mongo";

export const Sb_Survey = new Mongo.Collection("sb_survey");
Sb_Survey.schema = new SimpleSchema({
    coId: {
        type: String,
    },
    date: {
        type: String,
    },
    name: {
        type: String,
        label: "Name"
    },
    gender: {
        type: String,
    },
    note: {
        type: String,
        optional: true
    },
    age: {
        type: String,
    },
    job: {
        type: String,
    },
    familyMemberEarnMoney: {
        type: Number,
    },
    phoneNumber: {
        type: String,
    },
    facebookName: {
        type: String,
        optional: true
    },
    provinceId: {
        type: String,
        optional: true
    },
    districtId: {
        type: String,
        optional: true
    },
    communeId: {
        type: String,
        optional: true
    },
    villageId: {
        type: String,
        optional: true
    },
    provinceName: {
        type: String,
        optional: true
    },
    districtName: {
        type: String,
        optional: true
    },
    communeName: {
        type: String,
        optional: true
    },
    villageName: {
        type: String,
        optional: true
    },
    address: {
        type: String,
        optional: true
    },
    homeNo: {
        type: String,
        optional: true
    },
    groupNo: {
        type: String,
        optional: true
    },
    owingCompany: {
        type: String,
        optional: true

    },

    loanAmount: {
        type: String,
        optional: true

    },

    term: {
        type: String,
        optional: true
    },

    loanOutstanding: {
        type: String,
        optional: true
    },
    termOutstanding: {
        type: String,
        optional: true
    },

    collateral: {
        type: String,
        optional: true

    },

    usedToLate: {
        type: Boolean,
        optional: true,
        defaultValue: false

    },
    reasonLate: {
        type: String,
        optional: true

    },
    applyLoanToCompanyCurrentMonth: {
        type: String,
        optional: true
    },

    usedToWriteOff: {
        type: Boolean,
        optional: true,
        defaultValue: false

    },
    likeCurrentCompany: {
        type: Boolean,
        optional: true,
        defaultValue: true

    },
    wantOtherLoan: {
        type: Boolean,
        optional: true,
        defaultValue: true
    },
    chooseOurService: {
        type: String,
        optional: true
    },
    reasonChooseOurService: {
        type: String,
        optional: true
    },


    isAgreeWithConsultant: {
        type: Boolean,
        optional: true,
        defaultValue: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Survey.attachSchema(Sb_Survey.schema);


