import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_LoanProduct, Sb_LoanProductReact, Sb_LoanProductAudit} from "../../imports/collections/sb_loanProduct"
import {Sb_Client} from "../../imports/collections/sb_client";
import {Sb_Penalty} from "../../imports/collections/sb_penalty";
import {Sb_PenaltyClosing} from "../../imports/collections/sb_penaltyClosing";
import {Branch} from "../../imports/collections/branch";
import numeral from "numeral";
import axios from "axios";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchLoanProduct({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    let penaltyList = Sb_Penalty.find({
                            name: {
                                $regex: reg,
                                $options: 'mi'
                            }
                        }, {_id: true},
                        {
                            $limit: options.limit
                        },
                        {
                            $skip: options.skip
                        }).fetch().map((obj) => {
                        return obj._id;
                    });
                    let penaltyClosingList = Sb_PenaltyClosing.find({
                            name: {
                                $regex: reg,
                                $options: 'mi'
                            }
                        }, {_id: true},
                        {
                            $limit: options.limit
                        },
                        {
                            $skip: options.skip
                        }).fetch().map((obj) => {
                        return obj._id;
                    });


                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        rate: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        description: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        methodType: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        rateType: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        penaltyId: {
                            $in: penaltyList
                        }
                    }, {
                        penaltyClosingId: {
                            $in: penaltyClosingList
                        }
                    }];
                }
            }
            selector.branchId = branchId;

            data.content = Sb_LoanProduct.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "sb_penalty",
                            localField: "penaltyId",
                            foreignField: "_id",
                            as: "penaltyDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$penaltyDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "sb_penaltyClosing",
                            localField: "penaltyClosingId",
                            foreignField: "_id",
                            as: "penaltyClosingDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$penaltyClosingDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_LoanProduct.find(selector).count();
            return data;
        }
    },
    sb_insertLoanProduct(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {

                let id = Sb_LoanProduct.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_LoanProductReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateLoanProduct(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_LoanProduct.findOne({_id: id});
                let isUpdated = Sb_LoanProduct.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_LoanProductReact, id, Sb_LoanProductAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeLoanProduct(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_LoanProduct.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_LoanProductReact, doc._id, Sb_LoanProductAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findLoanProduct(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector.status = true;
                return Sb_LoanProduct.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findLoanProductById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_LoanProduct.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchLoanProductOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                if (q && q !== "" && q !== undefined && q !== null) {
                    //q = q.split("|")[0];
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {_id: {$regex: reg}},
                        {name: {$regex: reg}},
                    ];
                }

                selector.status = true;

                return Sb_LoanProduct.aggregate([
                    {$match: selector},
                    {$limit: 500},

                ]).map(obj => ({
                    label: obj.name + " : " + obj.rateType + " : " + obj.rate + "%",
                    value: obj._id,
                    isSpecialLoan: obj.isSpecialLoan || false,
                    isCustomInterestRate: obj.isCustomInterestRate || false,
                    isCustomPrinciple: obj.isCustomPrinciple || false,
                    interestList: obj.interestList || []
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_LoanProduct._ensureIndex({
    rate: 1,
    rateType: 1,
    methodType: 1,
    productDate: 1,
    status: 1,
    isSpecialLoan: 1,
    isCustomPrinciple: 1,
    branchId: 1
}, {unique: 1, name: "Sb_LoanProductUnique"});