import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_loanContractReport(doc, accessToken, language, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {};
            let companyDoc = Company.findOne({});
            let branchDoc = Branch.findOne({_id: branchId});
            let dataHtml = "";
            if (branchDoc && branchDoc.contract) {
                dataHtml = branchDoc.contract;

                /*{{coName}} {{position}} {{clientName}} {{clientPhone}} {{loanAmount}} {{loanAmountChar}}  {{currency}} {{currencyKH}}  {{branchName}} {{cardType}}
                "{{clientIdCard}} {{joinAccountName}}   {{joinIdCardType}}  {{joinIdCard}} {{joinAccountPhone}}  {{joinAccountRelationship}}
                 {{rate}}   {{operationFeePercent}}
                "{{term}} {{termType}} {{numOfBorrowingDay}} {{startDate}} {{endDate}} {{purposeLoan}} {{clientNameAndJoinAccountName}}*/

                let totalBorrowDay = moment(doc.maturityDate).diff(doc.disbursementDate, "days");
                let clientNameAndJoinAccountName = (doc.clientDoc && doc.clientDoc.name || "");
                if (doc.clientDoc && doc.clientDoc.joinAccountName && doc.clientDoc.joinAccountName !== "") {
                    clientNameAndJoinAccountName += "  ឈ្មោះ " + doc.clientDoc.joinAccountName;
                }

                dataHtml = dataHtml.split("{{coName}}").join(`<strong>` + (doc.staffDoc && doc.staffDoc.name || "") + `</strong>`);
                dataHtml = dataHtml.split("{{position}}").join(`<strong>` + (doc.staffDoc && doc.staffDoc.position || "") + `</strong>`);
                dataHtml = dataHtml.split("{{clientName}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.name || "") + `</strong>`);
                dataHtml = dataHtml.split("{{gender}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.gender ==="1" ? "ប្រុស" : "ស្រី") + `</strong>`);
                dataHtml = dataHtml.split("{{dob}}").join(`<strong>` + (GlobalFn.formatDate(doc.clientDoc && doc.clientDoc.dob)) + `</strong>`);
                dataHtml = dataHtml.split("{{facebook}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.facebookName || "") +" : " +(doc.clientDoc && doc.clientDoc.facebookId || "")+ `</strong>`);
                dataHtml = dataHtml.split("{{job}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.job || ".......") + `</strong>`);
                dataHtml = dataHtml.split("{{clientAddress}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.address || ".......") + `</strong>`);
                dataHtml = dataHtml.split("{{desc}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.desc || "") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantor}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.name || "") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorGender}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.gender=="1" ? "ប្រុស" : "ស្រី") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorDob}}").join(`<strong>` + (GlobalFn.formatDate(doc.guarantorDoc && doc.guarantorDoc.dob || "")) + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorPhoneNumber}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.phoneNumber || "") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorJob}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.job || ".......") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorAddress}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.address || ".......") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorIdCard}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.nationId || "") + `</strong>`);
                dataHtml = dataHtml.split("{{guarantorFacebook}}").join(`<strong>` + (doc.guarantorDoc && doc.guarantorDoc.facebookName || "") + `</strong>`);

                dataHtml = dataHtml.split("{{clientNameAndJoinAccountName}}").join(`<strong>` + clientNameAndJoinAccountName + `</strong>`);
                dataHtml = dataHtml.split("{{branchName}}").join(`<strong>` + (branchDoc && branchDoc.name || "") + `</strong>`);
                dataHtml = dataHtml.split("{{clientPhone}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.phoneNumber || "") + `</strong>`);
                dataHtml = dataHtml.split("{{clientIdCard}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.nationId || "") + `</strong>`);
                dataHtml = dataHtml.split("{{cardType}}").join(`<strong>` + GlobalFn.getNationType(doc.clientDoc.type) + `</strong>`);

                dataHtml = dataHtml.split("{{joinAccountName}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.joinAccountName || ".........") + `</strong>`);
                dataHtml = dataHtml.split("{{joinIdCardType}}").join(`<strong>` + GlobalFn.getNationType(doc.clientDoc.joinAccountType) + `</strong>`);
                dataHtml = dataHtml.split("{{joinIdCard}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.joinAccountNationId || ".........") + `</strong>`);
                dataHtml = dataHtml.split("{{joinAccountPhone}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.joinAccountPhoneNumber || ".........") + `</strong>`);
                dataHtml = dataHtml.split("{{joinAccountRelationship}}").join(`<strong>` + (doc.clientDoc && doc.clientDoc.joinAccountRelationship || "........") + `</strong>`);

                dataHtml = dataHtml.split("{{price}}").join(`<strong>` + GlobalFn.seperateNumber(doc.price) + `</strong>`);
                dataHtml = dataHtml.split("{{paid}}").join(`<strong>` + GlobalFn.seperateNumber(doc.paid) + `</strong>`);
                dataHtml = dataHtml.split("{{loanAmount}}").join(`<strong>` + GlobalFn.seperateNumber(doc.loanAmount) + `</strong>`);
                dataHtml = dataHtml.split("{{operationFeePercent}}").join(`<strong>` + doc.operationFeeValue + `</strong>`);
                dataHtml = dataHtml.split("{{currency}}").join(`<strong>` + GlobalFn.getCurrencySymbol(doc.currencyId) + `</strong>`);
                dataHtml = dataHtml.split("{{currencyKH}}").join(`<strong>` + GlobalFn.getCurrencyKH(doc.currencyId, doc.loanAmount) + `</strong>`);
                dataHtml = dataHtml.split("{{loanAmountChar}}").join(`<strong>` + GlobalFn.numberInKhmer(doc.loanAmount) + `</strong>`);
                dataHtml = dataHtml.split("{{rate}}").join(`<strong>` + (doc.productDoc && doc.productDoc.rate || "") + `</strong>`);
                dataHtml = dataHtml.split("{{startDate}}").join(`<strong>` + GlobalFn.getDateKH(doc.disbursementDate) + `</strong>`);
                dataHtml = dataHtml.split("{{endDate}}").join(`<strong>` + GlobalFn.getDateKH(doc.maturityDate) + `</strong>`);
                dataHtml = dataHtml.split("{{numOfBorrowingDay}}").join(`<strong>` + totalBorrowDay + `</strong>`);
                dataHtml = dataHtml.split("{{purposeLoan}}").join(`<strong>` + (doc.purposeDoc && doc.purposeDoc.name || "") + `</strong>`);
                dataHtml = dataHtml.split("{{termType}}").join(`<strong>` + GlobalFn.getTermType(doc.productDoc.rateType) + `</strong>`);
                dataHtml = dataHtml.split("{{mortgage}}").join(`<strong>` + (doc.mortgage || "") + `</strong>`);
                dataHtml = dataHtml.split("{{model}}").join(`<strong>` + (doc.model || "") + `</strong>`);
                dataHtml = dataHtml.split("{{projectInterest}}").join(`<strong>` + (doc.projectInterest || "") + `</strong>`);
                dataHtml = dataHtml.split("{{projectOperation}}").join(`<strong>` + (doc.projectOperation || "") + `</strong>`);
                dataHtml = dataHtml.split("{{term}}").join(`<strong>` + (doc.term || "") + `</strong>`);
                dataHtml = dataHtml.split("{{loanAcc}}").join(`<strong>` + (doc.loanAcc || "") + `</strong>`);
                dataHtml = dataHtml.split("{{clientType}}").join(`<strong>` + (doc.clientTypeDoc && doc.clientTypeDoc.name || "") + `</strong>`);
            }
            data.dataHtml = dataHtml;
            return data;
        }
    }
})
