let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchRepaymentScheduleCCLReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            selector.branchId = params.branchId;
            let loanConfigDoc = Sb_LoanConfig.findOne(selector);
            let productDoc = Sb_LoanProduct.findOne({_id: params.productId});
            let projectInterest = calculateProjectInterest(params, productDoc);
            params.projectInterest = projectInterest;
            //Range Date
            let repaymentScheduleList = generateSchedulePayment(params, productDoc, "001");

            let companyDoc = Company.findOne({});

            //Range Date
            let balanceDue = params.loanAmount;

            if (repaymentScheduleList.length > 0) {
                data.currencyHeader = repaymentScheduleList[0].currencyId === "KHR" ? "ប្រាក់រៀល" : companyDoc.baseCurrency === "THB" ? "ប្រាក់បាត" : "ប្រាក់ដុល្លារ";
                data.currencySymbol = GlobalFn.getCurrencySymbol(repaymentScheduleList[0].currencyId);
            } else {
                data.currencyHeader = "";
                data.currencySymbol = "";
            }
            let repaymentHTML = "";
            let productId = "";
            let clientId = "";
            let displayFee = loanConfigDoc.isOperationFee === true ? "" : "none";
            if (repaymentScheduleList.length > 0) {
                repaymentScheduleList.forEach((obj) => {
                    let backgroundColor = math.round(obj.balanceUnpaid, 2) > 0 ? "transparent" : "yellow";
                    let tdth = math.round(obj.balanceUnpaid, 2) > 0 ? "td" : "th";
                    let colorRow = (math.round(obj.balanceUnpaid, 2) > 0 && (math.round(obj.balanceUnpaid, 2) < math.round(obj.amount, 2)) ? "red" : "#333");
                    if (obj.isAllowClosing === false) {
                        repaymentHTML += `
                    <tr style="background-color:${backgroundColor};color: ${colorRow}">
                            <td style="text-align: center !important;"><u>${obj.installment}</u></td>
                            <${tdth} style="text-align: left !important;">${GlobalFn.switchDay(obj.date)}   ${GlobalFn.formatDate(obj.date)}</${tdth}>
                        
                            <td style="text-align: left !important;">${obj.dayRange}</td>
                            <td>${GlobalFn.formatCurrency(obj.principle, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.interest, obj.currencyId)}</td>
                            <td style="display: ${displayFee}">${GlobalFn.formatCurrency(obj.operationFee || 0, obj.currencyId)}</td>
                            <th>${GlobalFn.formatCurrency(obj.amount, obj.currencyId)}</th>
                            <td>${GlobalFn.formatCurrency(balanceDue - obj.principle, obj.currencyId)}</td>
                    </tr>
            
            `;


                    } else {

                        repaymentHTML += `
                    <tr style="background-color: ${backgroundColor};color: ${colorRow}">
                            <td style="text-align: center !important;">${obj.installment}</td>
                            <${tdth} style="text-align: left !important;">${GlobalFn.switchDay(obj.date)}   ${GlobalFn.formatDate(obj.date)}</${tdth}>
                            <td style="text-align: left !important;">${obj.dayRange}</td>
                            <td>${GlobalFn.formatCurrency(obj.principle, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.interest, obj.currencyId)}</td>
                            <td style="display: ${displayFee}">${GlobalFn.formatCurrency(obj.operationFee || 0, obj.currencyId)}</td>
                            <th>${GlobalFn.formatCurrency(obj.amount, obj.currencyId)}</th>
                            <td>${GlobalFn.formatCurrency(balanceDue - obj.principle, obj.currencyId)}</td>
                    </tr>
            
            `;
                    }

                    balanceDue = GlobalFn.roundCurrency(balanceDue - obj.principle, obj.currencyId, obj.branchId);
                    productId = obj.productId;
                    clientId = obj.clientId;
                })
            }


            data.dataHtml = repaymentHTML;
            data.productDoc = productDoc;
            data.disbursementDoc = params;
            return data;
        }
    }
})

let generateSchedulePayment = function (disbursementDoc, productDoc, id) {
    let disDoc = disbursementDoc;
    let setup = Branch.findOne({_id: disbursementDoc.branchId});
    let escapeDayList = Sb_Holiday.find({}).fetch();

    let penaltyClosingDoc = Sb_PenaltyClosing.findOne({_id: productDoc.penaltyClosingId});
    let installmentAllowClosing = math.round((disbursementDoc.term / disbursementDoc.repaidFrequency) * (penaltyClosingDoc.installmentTermLessThan / 100), 0);
    let paidDate = escapeDayFn(setup, escapeDayList, disbursementDoc.startPaidDate);
    let paidDateNotChange = disbursementDoc.startPaidDate;
    let numDay = moment(paidDate, "YYYY-MM-DD").diff(disbursementDoc.disbursementDate, 'days');

    let repaymentScheduleList = [];
    let operationFeeAmount = 0;

    if (disbursementDoc.operationFeeType === "Amount") {
        operationFeeAmount = math.round(disbursementDoc.operationFeeAmount || 0, 3);
        operationFeeAmount = GlobalFn.roundCurrency(operationFeeAmount, disbursementDoc.currencyId, disbursementDoc.branchId);
    }
    let projectOperation = 0;
    disbursementDoc.startInstallment = (disbursementDoc.startInstallment || 0) + 1;
    let numInstallmentPaid = disbursementDoc.numInstallmentPaid || 0;
    let totalPrinciplePaidOld = 0;

    if (productDoc.methodType === "Straight Line") {
        let oldPrinciple = 0;
        if (disbursementDoc.repaidPrinciple === 1 && (disbursementDoc.startInstallment === 1 || disbursementDoc.startInstallment === undefined)) {
            let amount = (disbursementDoc.loanAmount + disbursementDoc.projectInterest) / (disbursementDoc.term / disbursementDoc.repaidFrequency);
            let principle = math.round(disbursementDoc.loanAmount / ((disbursementDoc.term / disbursementDoc.repaidFrequency)), 3);
            principle = GlobalFn.roundCurrency(principle, disbursementDoc.currencyId, disbursementDoc.branchId);

            let ofVal = disbursementDoc.loanAmount;
            for (let i = 1; i <= disbursementDoc.term / disbursementDoc.repaidFrequency; i++) {
                let repaymentScheduleDoc = {};

                if (disbursementDoc.operationFeeType === "Percent") {
                    if (disbursementDoc.operationFeeOf === "Loan Amount") {
                        ofVal = disbursementDoc.loanAmount;
                    } else if (disbursementDoc.operationFeeOf === "Loan Outstanding") {

                        ofVal = ofVal - oldPrinciple;
                    } else if (disbursementDoc.operationFeeOf === "Principle") {
                        ofVal = principle;
                    }
                    operationFeeAmount = GlobalFn.roundCurrency(ofVal * (disbursementDoc.operationFeeValue / 100), disbursementDoc.currencyId, disbursementDoc.branchId);
                }


                if (i === 1) {
                    repaymentScheduleDoc._id = id + i;
                    repaymentScheduleDoc.installment = i;
                    repaymentScheduleDoc.date = paidDate;
                    repaymentScheduleDoc.amount = (disbursementDoc.loanAmount + disbursementDoc.projectInterest) - (GlobalFn.roundCurrency(amount, disbursementDoc.currencyId, disbursementDoc.branchId) * ((disbursementDoc.term / disbursementDoc.repaidFrequency) - 1));
                    repaymentScheduleDoc.principle = disbursementDoc.loanAmount - (GlobalFn.roundCurrency(principle * ((disbursementDoc.term / disbursementDoc.repaidFrequency) - 1), disbursementDoc.currencyId, disbursementDoc.branchId));
                    repaymentScheduleDoc.interest = GlobalFn.roundCurrency(repaymentScheduleDoc.amount - repaymentScheduleDoc.principle, disbursementDoc.currencyId, disbursementDoc.branchId);
                    repaymentScheduleDoc.operationFee = operationFeeAmount;
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
                    repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;

                    repaymentScheduleDoc.loanId = id;
                    repaymentScheduleDoc.clientId = disbursementDoc.clientId;
                    repaymentScheduleDoc.productId = disbursementDoc.productId;
                    repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
                    repaymentScheduleDoc.branchId = disbursementDoc.branchId;

                    repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
                    repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
                    repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;

                    repaymentScheduleDoc.dayRange = numDay;

                    totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;


                } else {
                    repaymentScheduleDoc._id = id + i;
                    repaymentScheduleDoc.installment = i;
                    repaymentScheduleDoc.date = paidDate;
                    repaymentScheduleDoc.amount = GlobalFn.roundCurrency(amount, disbursementDoc.currencyId, disbursementDoc.branchId);
                    repaymentScheduleDoc.principle = GlobalFn.roundCurrency(principle, disbursementDoc.currencyId, disbursementDoc.branchId);
                    repaymentScheduleDoc.interest = repaymentScheduleDoc.amount - repaymentScheduleDoc.principle;
                    repaymentScheduleDoc.operationFee = operationFeeAmount;
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
                    repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;


                    repaymentScheduleDoc.loanId = id;
                    repaymentScheduleDoc.clientId = disbursementDoc.clientId;
                    repaymentScheduleDoc.productId = disbursementDoc.productId;
                    repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
                    repaymentScheduleDoc.branchId = disbursementDoc.branchId;

                    repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
                    repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
                    repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.dayRange = numDay;

                    totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;


                }


                let rawPaidDate = paidDate;
                if (productDoc.rateType === "Monthly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "month");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Weekly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, 7 * i * disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Daily") {
                    paidDate = GlobalFn.addDay(paidDate, disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Yearly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "year");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                }

                projectOperation += repaymentScheduleDoc.operationFee;

                oldPrinciple = repaymentScheduleDoc.principle;
                repaymentScheduleList.push(repaymentScheduleDoc);
            }
        } else {

            if ((disbursementDoc.startInstallment || 1) <= disbursementDoc.repaidPrinciple) {
                disbursementDoc.startInstallment = 1;
            }
            let interest = GlobalFn.roundCurrency(math.round(disbursementDoc.projectInterest / (disbursementDoc.term / disbursementDoc.repaidFrequency), 0), disbursementDoc.currencyId, disbursementDoc.branchId);
            let principle = GlobalFn.roundCurrency(math.round(disbursementDoc.loanAmount / (((disbursementDoc.term - (disbursementDoc.startInstallment || 1) + 1) / disbursementDoc.repaidFrequency)), 3), disbursementDoc.currencyId, disbursementDoc.branchId);
            let ofVal = disbursementDoc.loanAmount;

            let firstPrint = 0;
            for (let i = 1; i <= disbursementDoc.term / disbursementDoc.repaidFrequency; i++) {
                let repaymentScheduleDoc = {};

                if (disbursementDoc.operationFeeType === "Percent") {
                    if (disbursementDoc.operationFeeOf === "Loan Amount") {
                        ofVal = disbursementDoc.loanAmount;
                    } else if (disbursementDoc.operationFeeOf === "Loan Outstanding") {

                        ofVal = ofVal - oldPrinciple;
                    } else if (disbursementDoc.operationFeeOf === "Principle") {
                        ofVal = principle;
                    }
                    operationFeeAmount = GlobalFn.roundCurrency(ofVal * (disbursementDoc.operationFeeValue / 100), disbursementDoc.currencyId, disbursementDoc.branchId);
                }


                if (i === 1) {
                    repaymentScheduleDoc._id = id + i;
                    repaymentScheduleDoc.installment = i;
                    repaymentScheduleDoc.date = paidDate;
                    if (i % disbursementDoc.repaidPrinciple === 0 && i > 1 && i > ((disbursementDoc.startInstallment || 1) - 1)) {
                        if (i === disbursementDoc.repaidPrinciple || firstPrint === 0) {
                            repaymentScheduleDoc.principle = disbursementDoc.loanAmount - (GlobalFn.roundCurrency(principle * (((disbursementDoc.term - (disbursementDoc.startInstallment || 1) + 1) / disbursementDoc.repaidFrequency) - 1), disbursementDoc.currencyId, disbursementDoc.branchId)) + GlobalFn.roundCurrency(principle * (disbursementDoc.repaidPrinciple - 1), disbursementDoc.currencyId, disbursementDoc.branchId);
                            firstPrint++;
                        } else {
                            repaymentScheduleDoc.principle = GlobalFn.roundCurrency(principle * disbursementDoc.repaidPrinciple, disbursementDoc.currencyId, disbursementDoc.branchId);
                        }
                    } else {
                        repaymentScheduleDoc.principle = 0;
                    }

                    repaymentScheduleDoc.interest = disbursementDoc.projectInterest - interest * ((disbursementDoc.term / disbursementDoc.repaidFrequency) - 1);
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.principle + repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFee = operationFeeAmount;
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
                    repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;

                    repaymentScheduleDoc.loanId = id;
                    repaymentScheduleDoc.clientId = disbursementDoc.clientId;
                    repaymentScheduleDoc.productId = disbursementDoc.productId;
                    repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
                    repaymentScheduleDoc.branchId = disbursementDoc.branchId;
                    repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
                    repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
                    repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.dayRange = numDay;

                    totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;


                } else {

                    repaymentScheduleDoc._id = id + i;
                    repaymentScheduleDoc.installment = i;
                    repaymentScheduleDoc.date = paidDate
                    if (i % disbursementDoc.repaidPrinciple === 0 && i > 1 && i > ((disbursementDoc.startInstallment || 1) - 1)) {
                        if (i === disbursementDoc.repaidPrinciple || firstPrint === 0) {
                            repaymentScheduleDoc.principle = disbursementDoc.loanAmount - (GlobalFn.roundCurrency(principle * (((disbursementDoc.term - (disbursementDoc.startInstallment || 1) + 1) / disbursementDoc.repaidFrequency) - 1), disbursementDoc.currencyId, disbursementDoc.branchId)) + GlobalFn.roundCurrency(principle * (disbursementDoc.repaidPrinciple - 1), disbursementDoc.currencyId, disbursementDoc.branchId);
                            firstPrint++;

                        } else {
                            repaymentScheduleDoc.principle = GlobalFn.roundCurrency(principle * disbursementDoc.repaidPrinciple, disbursementDoc.currencyId, disbursementDoc.branchId);
                        }
                    } else {
                        repaymentScheduleDoc.principle = 0;
                    }

                    repaymentScheduleDoc.interest = GlobalFn.roundCurrency(interest, disbursementDoc.currencyId, disbursementDoc.branchId);
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.principle + repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFee = operationFeeAmount;
                    repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
                    repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;


                    repaymentScheduleDoc.loanId = id;
                    repaymentScheduleDoc.clientId = disbursementDoc.clientId;
                    repaymentScheduleDoc.productId = disbursementDoc.productId;
                    repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
                    repaymentScheduleDoc.branchId = disbursementDoc.branchId;

                    repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
                    repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
                    repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
                    repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;
                    repaymentScheduleDoc.dayRange = numDay;

                    totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;

                }


                let rawPaidDate = paidDate;
                if (productDoc.rateType === "Monthly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "month");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Weekly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, 7 * i * disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Daily") {
                    paidDate = GlobalFn.addDay(paidDate, disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else if (productDoc.rateType === "Yearly") {
                    paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "year");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                }

                oldPrinciple = repaymentScheduleDoc.principle;
                projectOperation += repaymentScheduleDoc.operationFee;
                repaymentScheduleList.push(repaymentScheduleDoc);
            }
        }


        return repaymentScheduleList;

    } else if (productDoc.methodType === "Declining Balance") {
        let balance = disbursementDoc.loanAmount;
        let principle = disbursementDoc.loanAmount / ((disbursementDoc.term / disbursementDoc.repaidFrequency) - (disbursementDoc.startInstallment || 1) + 1);
        let projectInterest = 0;

        let ofVal = disbursementDoc.loanAmount;
        let oldPrinciple = 0;
        for (let i = 1; i <= disbursementDoc.term / disbursementDoc.repaidFrequency; i++) {
            let repaymentScheduleDoc = {};
            if (disbursementDoc.operationFeeType === "Percent") {
                if (disbursementDoc.operationFeeOf === "Loan Amount") {
                    ofVal = disbursementDoc.loanAmount;
                } else if (disbursementDoc.operationFeeOf === "Loan Outstanding") {
                    ofVal = ofVal - oldPrinciple;
                } else if (disbursementDoc.operationFeeOf === "Principle") {
                    ofVal = principle;
                }
                operationFeeAmount = GlobalFn.roundCurrency(ofVal * (disbursementDoc.operationFeeValue / 100), disbursementDoc.currencyId, disbursementDoc.branchId);
            }

            let interest = balance * (productDoc.rate / 100);
            if (productDoc.rateType === "Monthly") {
                interest = (interest / 30) * numDay;
            } else if (productDoc.rateType === "Weekly") {
                interest = (interest / 7) * numDay;
            } else if (productDoc.rateType === "Daily") {
                interest = (interest / 1) * numDay;
            } else if (productDoc.rateType === "Yearly") {
                interest = (interest / 365) * numDay;
            }

            if (disbursementDoc.currencyId === "KHR") {
                interest = GlobalFn.roundCurrency(interest, disbursementDoc.currencyId, disbursementDoc.branchId);
            }

            repaymentScheduleDoc._id = id + i;
            repaymentScheduleDoc.installment = i;


            repaymentScheduleDoc.date = paidDate
            if ((disbursementDoc.repaidPrinciple === 1 || (i % disbursementDoc.repaidPrinciple === 0 && i > 1)) && (i > ((disbursementDoc.startInstallment || 1) - 1))) {
                repaymentScheduleDoc.principle = GlobalFn.roundCurrency(principle * disbursementDoc.repaidPrinciple, disbursementDoc.currencyId, disbursementDoc.branchId);

                if (i === disbursementDoc.term / disbursementDoc.repaidFrequency) {
                    repaymentScheduleDoc.principle = balance;
                }
            } else {
                repaymentScheduleDoc.principle = 0;
            }

            repaymentScheduleDoc.interest = GlobalFn.roundCurrency(interest, disbursementDoc.currencyId, disbursementDoc.branchId);
            repaymentScheduleDoc.amount = GlobalFn.roundCurrencyNature(repaymentScheduleDoc.principle + repaymentScheduleDoc.interest, disbursementDoc.currencyId, disbursementDoc.branchId);
            repaymentScheduleDoc.interest = repaymentScheduleDoc.amount - repaymentScheduleDoc.principle;
            repaymentScheduleDoc.operationFee = operationFeeAmount;
            repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;

            repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
            repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;


            repaymentScheduleDoc.loanId = id;
            repaymentScheduleDoc.clientId = disbursementDoc.clientId;
            repaymentScheduleDoc.productId = disbursementDoc.productId;
            repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
            repaymentScheduleDoc.branchId = disbursementDoc.branchId;

            repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
            repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
            repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
            repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;
            repaymentScheduleDoc.dayRange = numDay;

            totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;

            let rawPaidDate = paidDate;
            if (productDoc.rateType === "Monthly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "month");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            } else if (productDoc.rateType === "Weekly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, 7 * i * disbursementDoc.repaidFrequency, "days");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            } else if (productDoc.rateType === "Daily") {
                paidDate = GlobalFn.addDay(paidDate, disbursementDoc.repaidFrequency, "days");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            } else if (productDoc.rateType === "Yearly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "year");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            }

            balance -= repaymentScheduleDoc.principle;
            projectInterest += repaymentScheduleDoc.interest;
            projectOperation += repaymentScheduleDoc.operationFee;

            oldPrinciple = repaymentScheduleDoc.principle;
            repaymentScheduleList.push(repaymentScheduleDoc);


        }
        return repaymentScheduleList;

    } else if (productDoc.methodType === "Annuity") {
        let balance = disbursementDoc.loanAmount;
        let principalInstallmentAmountPerLine = ((productDoc.rate / 100) * disbursementDoc.repaidFrequency * disbursementDoc.loanAmount) / (1 - Math.pow(1 + (productDoc.rate / 100) * disbursementDoc.repaidFrequency, -(disbursementDoc.term / disbursementDoc.repaidFrequency)));
        principalInstallmentAmountPerLine = GlobalFn.roundCurrency(principalInstallmentAmountPerLine, disbursementDoc.currencyId, disbursementDoc.branchId);
        let projectInterest = 0;
        if (disbursementDoc.currencyId === "USD" || disbursementDoc.currencyId === "THB") {
            principalInstallmentAmountPerLine = math.round(principalInstallmentAmountPerLine, 0);
        } else if (disbursementDoc.currencyId === "KHR") {
            principalInstallmentAmountPerLine = GlobalFn.roundCurrency(principalInstallmentAmountPerLine, disbursementDoc.currencyId, disbursementDoc.branchId);
        }

        let ofVal = disbursementDoc.loanAmount;
        let oldPrinciple = 0;
        for (let i = 1; i <= disbursementDoc.term / disbursementDoc.repaidFrequency; i++) {
            let repaymentScheduleDoc = {};


            let interest = balance * (productDoc.rate / 100);
            if (productDoc.rateType === "Monthly") {
                interest = (interest / 30) * numDay;
            } else if (productDoc.rateType === "Weekly") {
                interest = (interest / 7) * numDay;
            } else if (productDoc.rateType === "Daily") {
                interest = (interest / 1) * numDay;
            } else if (productDoc.rateType === "Yearly") {
                interest = (interest / 365) * numDay;
            }

            if (disbursementDoc.currencyId === "KHR") {
                interest = GlobalFn.roundCurrency(interest, disbursementDoc.currencyId, disbursementDoc.branchId);
            }
            repaymentScheduleDoc._id = id + i;
            repaymentScheduleDoc.installment = i;


            repaymentScheduleDoc.date = paidDate
            repaymentScheduleDoc.amount = principalInstallmentAmountPerLine;
            repaymentScheduleDoc.interest = interest;
            repaymentScheduleDoc.principle = i === (disbursementDoc.term / disbursementDoc.repaidFrequency) ? balance : repaymentScheduleDoc.amount - repaymentScheduleDoc.interest;
            repaymentScheduleDoc.amount = i === (disbursementDoc.term / disbursementDoc.repaidFrequency) ? repaymentScheduleDoc.principle + repaymentScheduleDoc.interest : repaymentScheduleDoc.amount;

            if (disbursementDoc.currencyId === "KHR") {
                repaymentScheduleDoc.amount = GlobalFn.roundCurrency(repaymentScheduleDoc.amount, disbursementDoc.currencyId, disbursementDoc.branchId);
                repaymentScheduleDoc.interest = repaymentScheduleDoc.amount - repaymentScheduleDoc.principle;
            }

            if (disbursementDoc.operationFeeType === "Percent") {
                if (disbursementDoc.operationFeeOf === "Loan Amount") {
                    ofVal = disbursementDoc.loanAmount;
                } else if (disbursementDoc.operationFeeOf === "Loan Outstanding") {
                    ofVal = ofVal - oldPrinciple;
                } else if (disbursementDoc.operationFeeOf === "Principle") {
                    ofVal = repaymentScheduleDoc.principle;
                }
                operationFeeAmount = GlobalFn.roundCurrency(ofVal * (disbursementDoc.operationFeeValue / 100), disbursementDoc.currencyId, disbursementDoc.branchId);
            }

            repaymentScheduleDoc.operationFee = operationFeeAmount;
            repaymentScheduleDoc.amount = repaymentScheduleDoc.amount + repaymentScheduleDoc.operationFee;

            repaymentScheduleDoc.isPaid = i <= numInstallmentPaid ? true : false;
            repaymentScheduleDoc.isAllowClosing = i > installmentAllowClosing;


            repaymentScheduleDoc.loanId = id;
            repaymentScheduleDoc.clientId = disbursementDoc.clientId;
            repaymentScheduleDoc.productId = disbursementDoc.productId;
            repaymentScheduleDoc.currencyId = disbursementDoc.currencyId;
            repaymentScheduleDoc.branchId = disbursementDoc.branchId;

            repaymentScheduleDoc.balanceUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.amount;
            repaymentScheduleDoc.principleUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.principle;
            repaymentScheduleDoc.interestUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.interest;
            repaymentScheduleDoc.operationFeeUnpaid = i <= numInstallmentPaid ? 0 : repaymentScheduleDoc.operationFee;
            repaymentScheduleDoc.dayRange = numDay;

            totalPrinciplePaidOld += i <= numInstallmentPaid ? repaymentScheduleDoc.principle : 0;

            let rawPaidDate = paidDate;
            if (productDoc.rateType === "Monthly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "month");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            } else if (productDoc.rateType === "Weekly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, 7 * i * disbursementDoc.repaidFrequency, "days");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            } else if (productDoc.rateType === "Daily") {
                if (disbursementDoc.repaidFrequency > 3) {
                    paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                } else {
                    paidDate = GlobalFn.addDay(paidDate, disbursementDoc.repaidFrequency, "days");
                    paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                    numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                }
            } else if (productDoc.rateType === "Yearly") {
                paidDate = GlobalFn.addDay(paidDateNotChange, i * disbursementDoc.repaidFrequency, "year");
                paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
            }

            balance -= repaymentScheduleDoc.principle;
            projectInterest += repaymentScheduleDoc.interest;
            projectOperation += repaymentScheduleDoc.operationFee;

            oldPrinciple = repaymentScheduleDoc.principle;
            repaymentScheduleList.push(repaymentScheduleDoc);


        }
        return repaymentScheduleList;

    }
}


let escapeDayFn = function (setup, escapeList, repayDate) {
    let yearName = moment(repayDate, "YYYY-MM-DD").format("YYYY");

    let findEscape = escapeList.find(function (element) {
        return element.year === yearName;
    });


    if (setup.escapeType === "No Escape") {
        return repayDate;
    } else if (setup.escapeType === "Before Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeBeforeDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else if (setup.escapeType === "After Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeAfterDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else {
        return repayDate;
    }
}

let changeDayDown = function (day) {
    if (day === 0) {
        return 6;
    } else {
        day = day - 1;
        return day;
    }
}
let changeDayUp = function (day) {
    if (day === 0) {
        return 1;
    } else if (day === 6) {
        return 0;
    } else {
        day = day + 1;
        return day;
    }
}

let escapeAfterDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, 1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();

    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayUp(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, changeWeekendDay, "days");
    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
};

let escapeBeforeDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, -1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();
    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayDown(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, -changeWeekendDay, "days");

    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
}


let calculateProjectInterest = function (disbursementDoc, productDoc) {
    let projectInterest = 0;

    if (productDoc.methodType === "Straight Line") {
        projectInterest = disbursementDoc.loanAmount * (productDoc.rate / 100) * disbursementDoc.term;
    } else {
        return 0;
    }


    return GlobalFn.roundCurrency(projectInterest, disbursementDoc.currencyId, disbursementDoc.branchId);
}

