import {Mongo} from "meteor/mongo";

export const Sb_Category = new Mongo.Collection("sb_category");
Sb_Category.schema = new SimpleSchema({
    code: {
        type: String
    },
    name: {
        type: String
    },
    description: {
        type: String,
        optional: true
    },
    status: {
        type: Boolean,
        optional: true,
        defaultValue: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Category.attachSchema(Sb_Category.schema);


