import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import {Sb_TargetPlan} from "../../../imports/collections/sb_targetPlan";
import {Sb_Staff} from "../../../imports/collections/sb_staff";
import {Sb_CoPlan} from "../../../imports/collections/sb_coPlan";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchCoTargetPlanReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let selectorPlan = {};
            let selectorCo = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                selectorPlan.branchId = {$in: params.branch || []};
                selectorCo.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                selectorPlan.branchId = {$in: userDoc.branch || []};
                selectorCo.branchId = {$in: userDoc.branch || []};
            }


            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({status: true});


            selector.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};

            selector.disbursementDate = {
                $gte: moment(params.year, "YYYY-MM").format("YYYY") + "-01-01",
                $lte: params.year + "-31"
            };
            let companyDoc = Company.findOne({});
            selectorPlan.year = moment(params.year, "YYYY-MM").format("YYYY");
            let targetPlanList = Sb_CoPlan.find(selectorPlan).fetch();
            let coefficientValue = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$loanAmount',
                baseCurrency: companyDoc.baseCurrency
            });

            //Monthly
            let dataList = Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $project: {
                        date: {
                            $dateFromString: {
                                dateString: "$disbursementDate"
                            }
                        },
                        loanAmount: 1,
                        currencyId: 1,
                        coId: 1,
                    }
                },
                {
                    $project: {
                        month: {$month: "$date"},
                        year: {$year: "$date"},
                        coId: 1,
                        loanAmount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValue.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValue.KHR, coefficientValue.THB]}
                            }
                        }
                    }
                }, {
                    $group: {
                        _id: {
                            month: "$month",
                            year: "$year",
                            coId: "$coId"
                        },
                        loanAmount: {$sum: "$loanAmount"}
                    }
                },
                {
                    $sort: {
                        "_id.year": 1,
                        "_id.month": 1,
                    }
                }
            ], {allowDiskUse: true});
            let dataHtml = "";
            let i = 1;
            let coList = Sb_Staff.find(selectorCo).fetch();
            let lastMonth = parseInt(moment(params.year, "YYYY-MM").format("MM"));
            coList.forEach((obj) => {

                let pDoc = targetPlanList.find(d => d.coId === obj._id);

                dataHtml += `
                        <tr>
                            <td>${i}</td>
                            <td>${obj.name}</td>
                            <th>${GlobalFn.seperateNumber(pDoc && pDoc.monthly || 0)}</th>
                    `;
                let quarter1 = 0;
                let quarter2 = 0;
                let quarter3 = 0;
                let quarter4 = 0;
                for (let j = 1; j <= lastMonth; j++) {
                    let dDoc = dataList.find(d => d._id.coId === obj._id && d._id.month === j);
                    dataHtml += `
                        <td>${GlobalFn.seperateNumber(dDoc && dDoc.loanAmount || 0)}</td>
                        <th>${((pDoc && pDoc.monthly || 0) === 0) ? 0 : Math.round(((dDoc && dDoc.loanAmount || 0) / (pDoc && pDoc.monthly || 0)) * 10000) / 100}%</th>
                    `;

                    if (j <= 3) {
                        quarter1 += (dDoc && dDoc.loanAmount || 0);
                    } else if (j <= 6) {
                        quarter2 += (dDoc && dDoc.loanAmount || 0);
                    } else if (j <= 9) {
                        quarter3 += (dDoc && dDoc.loanAmount || 0);
                    } else if (j <= 12) {
                        quarter4 += (dDoc && dDoc.loanAmount || 0);
                    }
                }

                dataHtml += `
                    <th>${GlobalFn.seperateNumber(pDoc && pDoc.quarter || 0)}</th>
                    
                    <td>${GlobalFn.seperateNumber(quarter1 || 0)}</td>
                    <th>${((pDoc && pDoc.quarter || 0) === 0) ? 0 : Math.round(((quarter1 || 0) / (pDoc && pDoc.quarter || 0)) * 10000) / 100}%</th>
   
                    <td>${GlobalFn.seperateNumber(quarter2 || 0)}</td>
                    <th>${((pDoc && pDoc.quarter || 0) === 0) ? 0 : Math.round(((quarter2 || 0) / (pDoc && pDoc.quarter || 0)) * 10000) / 100}%</th>
   
                    <td>${GlobalFn.seperateNumber(quarter3 || 0)}</td>
                    <th>${((pDoc && pDoc.quarter || 0) === 0) ? 0 : Math.round(((quarter3 || 0) / (pDoc && pDoc.quarter || 0)) * 10000) / 100}%</th>
   
                    <td>${GlobalFn.seperateNumber(quarter4 || 0)}</td>
                    <th>${((pDoc && pDoc.quarter || 0) === 0) ? 0 : Math.round(((quarter4 || 0) / (pDoc && pDoc.quarter || 0)) * 10000) / 100}%</th>

                `;

                let semester1 = quarter1 + quarter2;
                let semester2 = quarter3 + quarter4;

                dataHtml += `
                    <th>${GlobalFn.seperateNumber(pDoc && pDoc.semester || 0)}</th>
                    
                    <td>${GlobalFn.seperateNumber(semester1 || 0)}</td>
                    <th>${((pDoc && pDoc.semester || 0) === 0) ? 0 : Math.round(((semester1 || 0) / (pDoc && pDoc.semester || 0)) * 10000) / 100}%</th>
   
                    <td>${GlobalFn.seperateNumber(semester2 || 0)}</td>
                    <th>${((pDoc && pDoc.semester || 0) === 0) ? 0 : Math.round(((semester2 || 0) / (pDoc && pDoc.semester || 0)) * 10000) / 100}%</th>
   
             
                `;

                let yearly = semester1 + semester2;
                dataHtml += `
                    <th>${GlobalFn.seperateNumber(pDoc && pDoc.yearly || 0)}</th>
                    
                    <td>${GlobalFn.seperateNumber(yearly || 0)}</td>
                    <th>${((pDoc && pDoc.yearly || 0) === 0) ? 0 : Math.round(((yearly || 0) / (pDoc && pDoc.yearly || 0)) * 10000) / 100}%</th>
   
                `;


                dataHtml += `</tr>`;
                i++;
            })

            data.dataHtml = dataHtml;
            data.lastMonth = lastMonth;
            return data;
        }
    }
})
