import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Branch} from "../../../imports/collections/branch";
import {Exchange} from "../../../imports/collections/exchange";
import numeral from "numeral"
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import {Sb_LoanProduct} from "../../../imports/collections/sb_loanProduct";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchLoanOutstandingReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }
            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});
            let companyDoc = Company.findOne({});

            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;
            }


            selector.disbursementDate = {$lte: params.dateAs};

            selector.$and = [
                {
                    $or: [
                        {status: "Active"},
                        {
                            payOffDate: {
                                $gt: params.dateAs,
                            }
                        },
                        {
                            writeOffDate: {
                                $gt: params.dateAs,
                            }
                        }
                    ]
                },
                {
                    $or: [
                        {status: {$ne: "Write Off"}},
                        {
                            writeOffDate: {
                                $gt: params.dateAs,
                            }
                        }
                    ]
                },
                {
                    status: {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]}
                }
            ];

            if (params.productId && params.productId !== "") {
                selector.productId = params.productId;
            }


            if (params.productTypeId && params.productTypeId !== "") {
                let productList = Sb_LoanProduct.find({productTypeId: params.productTypeId}).map((d) => d._id);
                selector.productId = {$in: productList};
            }

            let dataList = Sb_Disbursement.aggregate([
                    {
                        $match: selector
                    },
                    {
                        $lookup:
                            {
                                from: "sb_repayment",
                                localField: "_id",
                                foreignField: "disbursementId",
                                as: "repaymentDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$repaymentDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $group: {
                            _id: {
                                _id: "$_id"
                            },
                            clientId: {$last: "$clientId"},
                            currencyId: {$last: "$currencyId"},
                            projectInterest: {$last: "$projectInterest"},
                            productId: {$last: "$productId"},
                            disbursementDate: {$last: "$disbursementDate"},
                            maturityDate: {$last: "$maturityDate"},
                            term: {$last: "$term"},
                            repaidFrequency: {$last: "$repaidFrequency"},
                            coId: {$last: "$coId"},
                            guarantorId: {$last: "$guarantorId"},
                            loanAcc: {$last: "$loanAcc"},
                            loanAmount: {$last: "$loanAmount"},
                            repaymentIdList: {$push: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, "$repaymentDoc._id", null]}},
                            totalPrinciple: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, "$repaymentDoc.principlePaid", 0]}},
                            totalInterest: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, "$repaymentDoc.interestPaid", 0]}},
                            totalOperationFee: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, "$repaymentDoc.operationFeePaid", 0]}},
                            totalPenalty: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, "$repaymentDoc.penaltyPaid", 0]}},
                            totalFee: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateAs]}, {$cond: [{$eq: ["$repaymentDoc.type", "Fee"]}, "$repaymentDoc.paid", 0]}, 0]}},
                        }
                    },

                    {
                        $lookup:
                            {
                                from: "sb_repaymentSchedule",
                                localField: "_id._id",
                                foreignField: "loanId",
                                as: "repaymentScheduleDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$repaymentScheduleDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },

                    {
                        $unwind: {
                            path: "$repaymentScheduleDoc.paid",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $group: {
                            _id: {
                                _id: "$_id._id"
                            },
                            clientId: {$last: "$clientId"},
                            currencyId: {$last: "$currencyId"},
                            projectInterest: {$last: "$projectInterest"},
                            productId: {$last: "$productId"},
                            disbursementDate: {$last: "$disbursementDate"},
                            maturityDate: {$last: "$maturityDate"},
                            term: {$last: "$term"},
                            repaidFrequency: {$last: "$repaidFrequency"},
                            coId: {$last: "$coId"},
                            guarantorId: {$last: "$guarantorId"},
                            loanAcc: {$last: "$loanAcc"},
                            loanAmount: {$last: "$loanAmount"},
                            totalPrinciple: {$last: "$totalPrinciple"},
                            totalInterest: {$last: "$totalInterest"},
                            totalPenalty: {$last: "$totalPenalty"},
                            totalOperationFee: {$last: "$totalOperationFee"},
                            totalFee: {$last: "$totalFee"},
                            installmentRemain: {
                                $sum: {$cond: [{$eq: ["$repaymentScheduleDoc.isPaid", false]}, 1, 0]}
                            },
                            prepayPrinciple: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateAs]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.principlePaid", 0]}},
                            prepayInterest: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateAs]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.interestPaid", 0]}},
                            prepayOperationFee: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateAs]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.operationFeePaid", 0]}},
                        }
                    },


                    {
                        $lookup:
                            {
                                from: "sb_client",
                                localField: "clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "sb_loanProduct",
                                localField: "productId",
                                foreignField: "_id",
                                as: "productDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$productDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }, {
                        $lookup:
                            {
                                from: "sb_staff",
                                localField: "coId",
                                foreignField: "_id",
                                as: "creditOfficerDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$creditOfficerDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }, {
                        $lookup:
                            {
                                from: "sb_guarantor",
                                localField: "guarantorId",
                                foreignField: "_id",
                                as: "guarantorDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$guarantorDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $sort: {
                            loanAcc: 1
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });

            let dataHtml = "";

            let totalPrincipleUSD = 0;
            let totalProjectInterestUSD = 0;
            let totalPrinciplePaidUSD = 0;
            let totalInterestPaidUSD = 0;
            let totalOperationFeePaidUSD = 0;
            let totalFeePaidUSD = 0;
            let totalPenaltyPaidUSD = 0;
            let totalUSD = 0;
            let totalIntReUSD = 0;

            let totalPrincipleTHB = 0;
            let totalProjectInterestTHB = 0;
            let totalPrinciplePaidTHB = 0;
            let totalInterestPaidTHB = 0;
            let totalOperationFeePaidTHB = 0;
            let totalFeePaidTHB = 0;
            let totalPenaltyPaidTHB = 0;
            let totalTHB = 0;
            let totalIntReTHB = 0;

            let totalPrincipleKHR = 0;
            let totalProjectInterestKHR = 0;

            let totalPrinciplePaidKHR = 0;
            let totalInterestPaidKHR = 0;
            let totalOperationFeePaidKHR = 0;
            let totalFeePaidKHR = 0;
            let totalPenaltyPaidKHR = 0;
            let totalKHR = 0;
            let totalIntReKHR = 0;

            let totalPrinciple = 0;
            let totalProjectInterest = 0;
            let totalPrinciplePaid = 0;
            let totalInterestPaid = 0;
            let totalOperationFeePaid = 0;
            let totalFeePaid = 0;
            let totalPenaltyPaid = 0;
            let total = 0;
            let totalIntRe = 0;
            let displayFee = configDoc.isOperationFee === true ? "" : "none";
            let isShowTotal = configDoc.isShowTotal === true ? "" : "none";
            let isNotPenalty = configDoc.isNotPenalty === true ? "none" : "";

            let i = 1;


            dataList.forEach((obj) => {
                if (obj && (obj.loanAmount - obj.totalPrinciple + (params.minusPrepay ? obj.prepayPrinciple : 0)) > 0) {
                    dataHtml += `
                            <tr>
                               <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                           
                            <td style="text-align: left !important;">${moment(obj.disbursementDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatDate(obj.maturityDate)}</td>
                            <td style="text-align: left !important;">${obj.installmentRemain} [${obj.term / obj.repaidFrequency}]</td>
                            <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.rate || ""} %</td>
                           
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${isShowTotal}">${GlobalFn.formatCurrencyLast(obj.projectInterest, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${isShowTotal}">${GlobalFn.formatCurrencyLast(obj.projectInterest + obj.loanAmount, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.totalPrinciple - (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.totalInterest - (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${isShowTotal}">${GlobalFn.formatCurrencyLast((obj.totalInterest - (params.minusPrepay ? obj.prepayInterest : 0)) + (obj.totalPrinciple - (params.minusPrepay ? obj.prepayPrinciple : 0)), obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${displayFee}">${GlobalFn.formatCurrencyLast(obj.totalOperationFee - (params.minusPrepay ? obj.prepayOperationFee : 0), obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.totalFee, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${isNotPenalty}">${GlobalFn.formatCurrencyLast(obj.totalPenalty, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${isShowTotal}">${GlobalFn.formatCurrencyLast(obj.projectInterest - obj.totalInterest + (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)}</td>
                             <td style="text-align: left !important;">${obj.currencyId}</td>
                            <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.rateType}</td>
                                                        <td style="text-align: left !important;">${obj.clientDoc.phoneNumber || ""}</td>

                    <td style="text-align: left !important;">${obj.clientDoc.address || ""}</td>
                            <td style="text-align: left !important;">${obj.guarantorDoc && obj.guarantorDoc.name || ""}</td>
                            <td style="text-align: left !important;">${obj.guarantorDoc && obj.guarantorDoc.phoneNumber || ""}</td>
                            <td style="text-align: left !important;">${obj.guarantorDoc && obj.guarantorDoc.address || ""}</td>
                            </tr>
                        `;
                    i++;

                    if (obj.currencyId === "USD") {
                        totalPrincipleUSD += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalProjectInterestUSD += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalPrinciplePaidUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalPrinciple - (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalInterestPaidUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalInterest - (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeePaidUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFee - (params.minusPrepay ? obj.prepayOperationFee : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeePaidUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyPaidUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalPenalty, obj.currencyId, companyDoc, branchDoc)).value();
                        totalUSD += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalIntReUSD += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest - obj.totalInterest + (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();
                    } else if (obj.currencyId === "KHR") {
                        totalPrincipleKHR += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalProjectInterestKHR += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest, obj.currencyId, companyDoc, branchDoc)).value();

                        totalPrinciplePaidKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalPrinciple - (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalInterestPaidKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalInterest - (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeePaidKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFee - (params.minusPrepay ? obj.prepayOperationFee : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeePaidKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyPaidKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalPenalty, obj.currencyId, companyDoc, branchDoc)).value();
                        totalKHR += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalIntReKHR += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest - obj.totalInterest + (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();

                    } else if (obj.currencyId === "THB") {
                        totalPrincipleTHB += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalProjectInterestTHB += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest, obj.currencyId, companyDoc, branchDoc)).value();

                        totalPrinciplePaidTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalPrinciple - (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalInterestPaidTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalInterest - (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeePaidTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFee - (params.minusPrepay ? obj.prepayOperationFee : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeePaidTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyPaidTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalPenalty, obj.currencyId, companyDoc, branchDoc)).value();
                        totalTHB += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + (params.minusPrepay ? obj.prepayPrinciple : 0), obj.currencyId, companyDoc, branchDoc)).value();
                        totalIntReTHB += numeral(GlobalFn.formatCurrencyLast(obj.projectInterest - obj.totalInterest + (params.minusPrepay ? obj.prepayInterest : 0), obj.currencyId, companyDoc, branchDoc)).value();

                    }

                }
            })


            totalProjectInterest = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalProjectInterestUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalProjectInterestTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalProjectInterestKHR, exchange);
            totalPrinciple = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPrincipleUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPrincipleTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPrincipleKHR, exchange);
            totalPrinciplePaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPrinciplePaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPrinciplePaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPrinciplePaidKHR, exchange);
            totalInterestPaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInterestPaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInterestPaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInterestPaidKHR, exchange);
            totalOperationFeePaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalOperationFeePaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalOperationFeePaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalOperationFeePaidKHR, exchange);
            totalFeePaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFeePaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFeePaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFeePaidKHR, exchange);
            totalPenaltyPaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPenaltyPaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPenaltyPaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPenaltyPaidKHR, exchange);
            total = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalKHR, exchange);
            totalIntRe = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalIntReUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalIntReTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalIntReKHR, exchange);


            dataHtml += `
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleUSD, "USD")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestUSD, "USD")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestUSD + totalPrincipleUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalPrinciplePaidUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestPaidUSD, "USD")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalInterestPaidUSD + totalPrinciplePaidUSD, "USD")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeePaidUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeePaidUSD, "USD")}</td>
                    <td style="display: ${isNotPenalty}">${GlobalFn.formatCurrency(totalPenaltyPaidUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalUSD, "USD")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalIntReUSD, "USD")}</td>
                    <td colspan="7"></td>
                  
                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleKHR, "KHR")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestKHR, "KHR")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestKHR + totalPrincipleKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalPrinciplePaidKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestPaidKHR, "KHR")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalInterestPaidKHR + totalPrinciplePaidKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeePaidKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeePaidKHR, "KHR")}</td>
                    <td style="display: ${isNotPenalty}">${GlobalFn.formatCurrency(totalPenaltyPaidKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalKHR, "KHR")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalIntReKHR, "KHR")}</td>
                                      <td colspan="7"></td>

                </tr> <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleTHB, "THB")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestTHB, "THB")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterestTHB + totalPrincipleTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalPrinciplePaidTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestPaidTHB, "THB")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalInterestPaidTHB + totalPrinciplePaidTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeePaidTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeePaidTHB, "THB")}</td>
                    <td style="display: ${isNotPenalty}">${GlobalFn.formatCurrency(totalPenaltyPaidTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalTHB, "THB")}</td>
                    <td style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalIntReTHB, "THB")}</td>
                                      <td colspan="7"></td>

                </tr> 
                <tr>
                    <th colspan="7" style="text-align: right">${t[language]['grandTotal']}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciple, companyDoc.baseCurrency)}</th>
                    <th style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterest, companyDoc.baseCurrency)}</th>
                    <th style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalProjectInterest + totalPrinciple, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciplePaid, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalInterestPaid, companyDoc.baseCurrency)}</th>
                    <th style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalInterestPaid + totalPrinciplePaid, companyDoc.baseCurrency)}</th>
                    <th style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeePaid, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalFeePaid, companyDoc.baseCurrency)}</th>
                    <th style="display: ${isNotPenalty}">${GlobalFn.formatCurrency(totalPenaltyPaid, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(total, companyDoc.baseCurrency)}</th>
                    <th style="display: ${isShowTotal}">${GlobalFn.formatCurrency(totalIntRe, companyDoc.baseCurrency)}</th>
                                      <td colspan="7"></td>

                </tr>
        `;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
