import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Constants, t} from "../../imports/libs/constant";

import {Company} from "../../imports/collections/company";

import {Sb_Disbursement} from "../../imports/collections/sb_disbursement";
import {Branch} from "../../imports/collections/branch";
import {Exchange} from "../../imports/collections/exchange";
import numeral from "numeral"
import {Sb_LoanConfig} from "../../imports/collections/sb_loanConfig";
import {Sb_RepaymentSchedule} from "../../imports/collections/sb_repaymentSchedule";
import {Sb_Repayment} from "../../imports/collections/sb_repayment";
import {Sb_Staff} from "../../imports/collections/sb_staff";
import math from "mathjs"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchBatchPaymentList(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let selectorRepaymentSchedule = {};
            let selectorPayOff = {};
            let selectorDisbursementList = {};
            let selectorDisbursement = {};

            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorRepaymentSchedule.branchId = {$in: params.branch || []};
                selectorPayOff.branchId = {$in: params.branch || []};
                selectorDisbursementList.branchId = {$in: params.branch || []};
                selectorDisbursement["disbursementDoc.branchId"] = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorRepaymentSchedule.branchId = {$in: userDoc.branch || []};
                selectorPayOff.branchId = {$in: userDoc.branch || []};
                selectorDisbursementList.branchId = {$in: userDoc.branch || []};
                selectorDisbursement["disbursementDoc.branchId"] = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }

            selectorRepaymentSchedule.isPaid = false;
            selectorRepaymentSchedule.date = {$lte: params.dateAs};


            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let coDoc = Sb_Staff.findOne({_id: params.staffId});


            let exchange = Exchange.findOne({_id: params.exchangeId});
            let companyDoc = Company.findOne({});

            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;

                selectorDisbursement["disbursementDoc.coId"] = params.staffId;

                selectorDisbursementList.coId = params.staffId;
                selectorDisbursementList.status = {$nin: ["Write Off", "Pay Off", "Processing", "Rejected", "Analyst Approved", "CFO Approved", "Reschedule"]};
                let disbursementList = Sb_Disbursement.find(selectorDisbursementList).map((obj) => {
                    return obj._id;
                })


                selectorRepaymentSchedule.loanId = {$in: disbursementList};
                selectorPayOff.loanId = {$in: disbursementList};
            } else {
                selectorDisbursementList.status = {$nin: ["Write Off", "Pay Off", "Processing", "Rejected", "Analyst Approved", "CFO Approved", "Reschedule"]};
                let disbursementList = Sb_Disbursement.find(selectorDisbursementList).map((obj) => {
                    return obj._id;
                })

                selectorRepaymentSchedule.loanId = {$in: disbursementList};
                selectorPayOff.loanId = {$in: disbursementList};
            }
            //Range Date
            let loanCollectionSheetList = Sb_RepaymentSchedule.aggregate([

                    {
                        $match: selectorRepaymentSchedule
                    },
                    {
                        $sort: {
                            installment: 1
                        }
                    },
                    {
                        $group: {
                            _id: {
                                loanId: '$loanId',
                                clientId: "$clientId"
                            },
                            firstScheduleDate: {$first: "$date"},
                            principleUnpaid: {$sum: "$principleUnpaid"},
                            interestUnpaid: {$sum: "$interestUnpaid"},
                            operationFeeUnpaid: {$sum: "$operationFeeUnpaid"},
                            balanceUnpaid: {$sum: "$balanceUnpaid"},
                            balanceUnpaidLast: {$last: "$balanceUnpaid"},
                            balanceUnpaidFirst: {$first: "$balanceUnpaid"},
                        }
                    },

                    {
                        $lookup:
                            {
                                from: "sb_disbursement",
                                localField: "_id.loanId",
                                foreignField: "_id",
                                as: "disbursementDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$disbursementDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: selectorDisbursement
                    },

                    {
                        $lookup:
                            {
                                from: "sb_client",
                                localField: "_id.clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $sort: {
                            "clientDoc.name": 1
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });

            return loanCollectionSheetList;
        }
    },
    sb_insertBatchPayment(list, date, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let i = 1;
            try {
                list.forEach((d) => {
                    let loanRepaymentDoc = {
                        disbursementId: d._id.loanId,
                        currencyId: d.disbursementDoc.currencyId,
                        repaymentDate: date,
                        penalty: 0,
                        penaltyPaid: 0,
                        voucher: i,
                        note: "",
                        type: "Repayment",
                        dayLate: 0,
                        savingAmount: 0,
                        paidFromSaving: 0,
                        interestReminderCharge: 0,
                        interestReminderChargePaid: 0,
                        interestPaid: 0,
                        operationFeePaid: 0,
                        clientId: d._id.clientId,
                        paymentTypeId: "",
                        paid: math.round(d.balanceUnpaid, 3),
                        totalPaid: math.round(d.balanceUnpaid, 3),
                        branchId: d.disbursementDoc.branchId
                    };

                    Meteor.call("sb_insertRepayment", loanRepaymentDoc, Constants.secret);
                    i++;
                })
                return true;
            } catch (err) {
                throw new Meteor.Error(err.message);
            }
        }
    }
})
