import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_Staff, Sb_StaffReact, Sb_StaffAudit} from "../../imports/collections/sb_staff"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchStaff({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1, name: 1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        startDate: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        dob: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        address: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        phoneNumber: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        email: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        status: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        description: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            data.content = Sb_Staff.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_Staff.find(selector).count();
            return data;
        }
    },
    sb_insertStaff(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_Staff.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_StaffReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateStaff(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Staff.findOne({_id: id});
                let isUpdated = Sb_Staff.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_StaffReact, id, Sb_StaffAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeStaff(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_Staff.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_StaffReact, doc._id, Sb_StaffAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findStaff(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector.status = true;
                return Sb_Staff.find(selector, {sort: {name: 1}}).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchStaffOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {_id: q}
                    ];
                }
                selector.status = true;
                return Sb_Staff.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + (obj.dob ? " : " + (obj.dob || "") : "") + (obj.phoneNumber ? " : " + obj.phoneNumber : ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_Staff._ensureIndex({
    name: 1,
    status: 1,
    dob: 1,
    startDate: 1,
    phoneNumber: 1,
    branchId: 1
}, {unique: 1, name: "Sb_StaffUnique"});