import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_FixedAsset} from "../../../imports/collections/acc_fixedAsset";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchFixedAssetReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};


            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};

            }


            let companyDoc = Company.findOne({});

            data.currencyHeader = companyDoc.baseCurrency;
            let baseCurrency = companyDoc.baseCurrency;
            if (params.currencyId !== "" && params.currencyId !== undefined) {
                selector.currencyId = params.currencyId;
                baseCurrency = params.currencyId;
                data.currencyHeader = params.currencyId;
            }

            let dataHtml = "";

            if (params.dateAs !== undefined) {
                selector.buyDate = {
                    $lte: params.dateAs
                };
            }


            selector.isDep = false;

            let fixedAssetList = Acc_FixedAsset.aggregate([
                {$match: selector},
                {
                    $lookup: {
                        from: "acc_chartAccount",
                        localField: "account",
                        foreignField: "_id",
                        as: "accountDoc"
                    }
                },
                {
                    $unwind: {path: "$accountDoc", preserveNullAndEmptyArrays: true}
                }
            ]);


            let accountShow = "";
            if (fixedAssetList.length > 0) {
                fixedAssetList.sort(compareASD);
                let i = 0;
                let totalAmount = 0;
                let totalCumDeprec = 0;
                let totalDepExp = 0;
                let totalNetBookValue = 0;


                let exchangeDoc = Exchange.findOne({_id: params.exchangeId});
                fixedAssetList.forEach(function (obj) {
                    let cumDeprec = 0;
                    let monthDep = 0;
                    let depExp = 0;

                    obj.transaction.forEach(function (ob) {
                        let depMonth = ob.maxMonth < companyDoc.depreciationPerTime ? ob.maxMonth : companyDoc.depreciationPerTime;
                        if (ob.month > 0) {
                            monthDep += ob.month;
                            cumDeprec += ob.month * ob.perMonth;
                            depExp = depMonth * ob.perMonth;
                        }
                    })
                    if (accountShow !== obj.account && i > 1) {
                        dataHtml += `
                        <tr>
                        <td colspan='4' style='border-bottom: none' align='center'>Total</td>
                        <td>${GlobalFn.formatCurrency(totalAmount, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency)}</td>
                        <td colspan='2'></td>
                        <td>${GlobalFn.formatCurrency(totalDepExp, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency)}</td>
                        <td>${GlobalFn.formatCurrency(totalCumDeprec, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency)}</td>
                        <td>${GlobalFn.formatCurrency(totalNetBookValue, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency)}</td>
                    `;

                    }

                    if (accountShow !== obj.account) {
                        totalAmount = 0;
                        totalCumDeprec = 0;
                        totalDepExp = 0;
                        totalNetBookValue = 0;

                        i = 1;
                        dataHtml += `
                        <tr style='background-color: lightgrey'>
                         <td colspan='10' style='border-bottom: none; text-align: left !important;'>${obj.accountDoc.code} : ${obj.accountDoc.name}</td>
                        </tr>
                    `;

                    }

                    dataHtml += "<tr><td>" + i + "</td><td style='text-align: left !important;'>" + obj.code + "</td><td>" + (obj.description || "") + "</td><td>" + moment(obj.buyDate).format("DD-MM-YYYY") + "</td><td>" + GlobalFn.formatCurrency(obj.value, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId) + "</td><td>" + obj.life + "ឆ្នាំ (" + (obj.life * 12) + "ខែ)" + "</td><td>" + monthDep + "</td><td>" + GlobalFn.formatCurrency(depExp, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId) + "</td><td>" + GlobalFn.formatCurrency(cumDeprec, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId) + "</td><td>" + GlobalFn.formatCurrency(obj.estSalvage, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId) + "</td></tr>";
                    accountShow = obj.account;
                    i++;

                    totalAmount += GlobalFn.exchange(obj.currencyId, companyDoc.baseCurrency, obj.value, exchangeDoc);
                    totalCumDeprec += GlobalFn.exchange(obj.currencyId, companyDoc.baseCurrency, cumDeprec, exchangeDoc);
                    totalDepExp += GlobalFn.exchange(obj.currencyId, companyDoc.baseCurrency, depExp, exchangeDoc);
                    totalNetBookValue += GlobalFn.exchange(obj.currencyId, companyDoc.baseCurrency, obj.estSalvage, exchangeDoc);
                })
                dataHtml += "<tr><td colspan='4'  align='center'>Total</td><td>" + GlobalFn.formatCurrency(totalAmount, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency) + "</td><td colspan='2'></td><td>" + GlobalFn.formatCurrency(totalDepExp, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency) + "</td><td>" + GlobalFn.formatCurrency(totalCumDeprec, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency) + "</td><td>" + GlobalFn.formatCurrency(totalNetBookValue, companyDoc.baseCurrency) + GlobalFn.getCurrencySymbol(companyDoc.baseCurrency) + "</td></tr>";
            }


            data.dataHtml = dataHtml;
            data.fixedAssetList = fixedAssetList;


            data.dataHtml = dataHtml;
            return data;
        }
    }
})


let compareASD = function (a, b) {
    if (a.accountDoc.code < b.accountDoc.code) {
        return -1;
    } else if (a.accountDoc.code > b.accountDoc.code) {
        return 1;
    } else {
        return 0;
    }
}
