import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Repayment} from "../../../imports/collections/sb_repayment";
import {Exchange} from "../../../imports/collections/exchange";
import numeral from "numeral"
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Sb_Client} from "../../../imports/collections/sb_client";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchRepaymentReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorDisburs = {};
            let selectorConfig = {};
            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selectorDisburs["disbursementDoc.coId"] = params.staffId;
            }

            if (params.paymentType && params.paymentType !== "") {
                selector.type = params.paymentType;

            } else {
                selector.type = {$ne: "Write Off"};
            }
            if (params.cashType && params.cashType !== "") {
                selector.paymentTypeId = params.cashType;

            }

            if (params.bpId && params.bpId !== "") {
                selectorDisburs["disbursementDoc.bpDoc.bpId"] = params.bpId;

            }

            selector.repaymentDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let companyDoc = Company.findOne({});

            let isLawFee = companyDoc.isFeeLaw ? "" : "none";
            let isInsuranceFee = companyDoc.isInsuranceFee ? "" : "none";
            let isSpecialLoan = companyDoc.isSpecialLoan ? "" : "none";

            let dataList = Sb_Repayment.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        repaymentDate: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_disbursement",
                        localField: "disbursementId",
                        foreignField: "_id",
                        as: "disbursementDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$disbursementDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {

                    $match: selectorDisburs

                },
                {
                    $lookup: {
                        from: "sb_client",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "disbursementDoc.coId",
                        foreignField: "_id",
                        as: "creditOfficerDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$creditOfficerDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_loanProduct",
                        localField: "disbursementDoc.productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "users",
                        localField: "createdUser",
                        foreignField: "_id",
                        as: "userDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$userDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },

            ], {allowDiskUse: true});


            let dataHtml = "";

            let totalPrincipleUSD = 0;
            let totalInterestUSD = 0;
            let totalOperationFeeUSD = 0;
            let totalFeeUSD = 0;
            let totalPenaltyUSD = 0;
            let totalFromSavingUSD = 0;
            let totalUSD = 0;
            let totalLawFeeUSD = 0;
            let totalROFeeUSD = 0;
            let totalBPFeeUSD = 0;
            let totalInsuranceFeeUSD = 0;


            let totalPrincipleTHB = 0;
            let totalInterestTHB = 0;
            let totalFeeTHB = 0;
            let totalOperationFeeTHB = 0;
            let totalPenaltyTHB = 0;
            let totalFromSavingTHB = 0;

            let totalTHB = 0;
            let totalLawFeeTHB = 0;
            let totalROFeeTHB = 0;
            let totalBPFeeTHB = 0;
            let totalInsuranceFeeTHB = 0;


            let totalPrincipleKHR = 0;
            let totalInterestKHR = 0;
            let totalFeeKHR = 0;
            let totalOperationFeeKHR = 0;
            let totalPenaltyKHR = 0;
            let totalFromSavingKHR = 0;

            let totalKHR = 0;
            let totalLawFeeKHR = 0;
            let totalROFeeKHR = 0;
            let totalBPFeeKHR = 0;
            let totalInsuranceFeeKHR = 0;

            let totalPrinciple = 0;
            let totalInterest = 0;
            let totalFee = 0;
            let totalOperationFee = 0;
            let totalPenalty = 0;
            let totalFromSaving = 0;

            let total = 0;
            let totalLawFee = 0;
            let totalROFee = 0;
            let totalBPFee = 0;
            let totalInsuranceFee = 0;

            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";
            let i = 1;
            // <td style="text-align: left !important;">${obj.voucher ? obj.voucher.split("-")[1].substr(4, obj.voucher.split("-")[1]-4) : ""}</td>

            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                    <tr>
                             <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.voucher}</td>
                            <td style="text-align: left !important;">${obj.disbursementDoc.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                             <td style="text-align: left !important;">${moment(obj.repaymentDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                                                         <td style="text-align: left !important;">${obj.type}</td>

                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.principlePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.interestPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${displayFee}">${GlobalFn.formatCurrencyLast(obj.operationFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.type === "Fee" ? obj.paid : 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${isInsuranceFee}">${GlobalFn.formatCurrencyLast(obj.insurancePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>

                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.penaltyPaid + obj.interestReminderChargePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${isLawFee}">${GlobalFn.formatCurrencyLast(obj.lawFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${isSpecialLoan}">${GlobalFn.formatCurrencyLast(obj.roFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${isSpecialLoan}">${GlobalFn.formatCurrencyLast(obj.bpFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${obj.note || ""}</td>
                            <td style="text-align: left !important;">${obj.currencyId}</td>
                            <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.rateType || ""}</td>

                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.disbursementDoc.loanAmount, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.disbursementDoc.projectInterest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                           
                            <td style="text-align: left !important;">${obj.clientDoc.address || ""}</td>

                            
                            <td>${obj.userDoc && obj.userDoc.username || ""}</td>
                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {
                        totalPrincipleUSD += numeral(GlobalFn.formatCurrencyLast(obj.principlePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestUSD += numeral(GlobalFn.formatCurrencyLast(obj.interestPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.operationFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.type === "Fee" ? obj.paid : 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyUSD += numeral(GlobalFn.formatCurrencyLast(obj.penaltyPaid + obj.interestReminderChargePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFromSavingUSD += numeral(GlobalFn.formatCurrencyLast(obj.paidFromSaving || 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalLawFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.lawFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalROFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.roFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalBPFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.bpFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInsuranceFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.insurancePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    } else if (obj.currencyId === "KHR") {
                        totalPrincipleKHR += numeral(GlobalFn.formatCurrencyLast(obj.principlePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestKHR += numeral(GlobalFn.formatCurrencyLast(obj.interestPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.operationFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.type === "Fee" ? obj.paid : 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyKHR += numeral(GlobalFn.formatCurrencyLast(obj.penaltyPaid + obj.interestReminderChargePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFromSavingKHR += numeral(GlobalFn.formatCurrencyLast(obj.paidFromSaving || 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalLawFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.lawFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalROFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.roFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalBPFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.bpFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInsuranceFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.insurancePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    } else if (obj.currencyId === "THB") {
                        totalPrincipleTHB += numeral(GlobalFn.formatCurrencyLast(obj.principlePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestTHB += numeral(GlobalFn.formatCurrencyLast(obj.interestPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.operationFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.type === "Fee" ? obj.paid : 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalPenaltyTHB += numeral(GlobalFn.formatCurrencyLast(obj.penaltyPaid + obj.interestReminderChargePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalFromSavingTHB += numeral(GlobalFn.formatCurrencyLast(obj.paidFromSaving || 0, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalPaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalLawFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.lawFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalROFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.roFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalBPFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.bpFeePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInsuranceFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.insurancePaid, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    }
                }
            })


            totalPrinciple = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPrincipleUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPrincipleTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPrincipleKHR, exchange);
            totalInterest = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInterestUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInterestTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInterestKHR, exchange);
            totalOperationFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalOperationFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalOperationFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalOperationFeeKHR, exchange);
            totalFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFeeKHR, exchange);
            totalPenalty = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPenaltyUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPenaltyTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPenaltyKHR, exchange);
            totalFromSaving = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFromSavingUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFromSavingTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFromSavingKHR, exchange);
            total = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalKHR, exchange);
            totalLawFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalLawFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalLawFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalLawFeeKHR, exchange);
            totalROFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalROFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalROFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalROFeeKHR, exchange);
            totalBPFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalBPFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalBPFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalBPFeeKHR, exchange);
            totalInsuranceFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInsuranceFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInsuranceFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInsuranceFeeKHR, exchange);

            dataHtml += `
                <tr>
                    <td colspan="6" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPrincipleUSD, "USD")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalInterestUSD, "USD")}</td>
                    <td style="display: ${displayFee};text-align: right">${GlobalFn.formatCurrency(totalOperationFeeUSD, "USD")}</td>
                    <td  style="text-align: right">${GlobalFn.formatCurrency(totalFeeUSD, "USD")}</td>
                    <td style="display: ${isInsuranceFee}">${GlobalFn.formatCurrency(totalInsuranceFeeUSD, "USD")}</td>

                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPenaltyUSD, "USD")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalUSD, "USD")}</td>
                    <td style="display: ${isLawFee}">${GlobalFn.formatCurrency(totalLawFeeUSD, "USD")}</td>
                    <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalROFeeUSD, "USD")}</td>
                    <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalBPFeeUSD, "USD")}</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                  
                </tr> 
                <tr>
                    <td colspan="6" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPrincipleKHR, "KHR")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalInterestKHR, "KHR")}</td>
                    <td style="display: ${displayFee};text-align: right">${GlobalFn.formatCurrency(totalOperationFeeKHR, "KHR")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalFeeKHR, "KHR")}</td>
                                        <td style="display: ${isInsuranceFee}">${GlobalFn.formatCurrency(totalInsuranceFeeKHR, "KHR")}</td>

                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPenaltyKHR, "KHR")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalKHR, "KHR")}</td>
                                        <td style="display: ${isLawFee}">${GlobalFn.formatCurrency(totalLawFeeKHR, "KHR")}</td>
                                        <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalROFeeKHR, "KHR")}</td>
                                        <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalBPFeeKHR, "KHR")}</td>

                                      <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>

                </tr> <tr>
                    <td colspan="6" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPrincipleTHB, "THB")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalInterestTHB, "THB")}</td>
                    <td style="display: ${displayFee};text-align: right">${GlobalFn.formatCurrency(totalOperationFeeTHB, "THB")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalFeeTHB, "THB")}</td>
                                        <td style="display: ${isInsuranceFee}">${GlobalFn.formatCurrency(totalInsuranceFeeTHB, "THB")}</td>

                    <td style="text-align: right">${GlobalFn.formatCurrency(totalPenaltyTHB, "THB")}</td>
                    <td style="text-align: right">${GlobalFn.formatCurrency(totalTHB, "THB")}</td>
                                        <td style="display: ${isLawFee}">${GlobalFn.formatCurrency(totalLawFeeTHB, "THB")}</td>
                                        <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalROFeeTHB, "THB")}</td>
                                        <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalBPFeeTHB, "THB")}</td>

                                      <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>

                </tr> 
                <tr>
                    <th colspan="6" style="text-align: right">${t[language]['grandTotal']}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciple, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalInterest, companyDoc.baseCurrency)}</th>
                    <th style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFee, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalFee, companyDoc.baseCurrency)}</th>
                                        <th style="display: ${isInsuranceFee}">${GlobalFn.formatCurrency(totalInsuranceFee, companyDoc.baseCurrency)}</th>

                    <th>${GlobalFn.formatCurrency(totalPenalty, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciple + totalInterest + totalFee + totalPenalty + totalOperationFee, companyDoc.baseCurrency)}</th>
                                                          <td style="display: ${isLawFee}">${GlobalFn.formatCurrency(totalLawFee, companyDoc.baseCurrency)}</td>
                                                          <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalROFee, companyDoc.baseCurrency)}</td>
                                                          <td style="display: ${isSpecialLoan}">${GlobalFn.formatCurrency(totalBPFee, companyDoc.baseCurrency)}</td>

                                      <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>

                </tr>
        `;


            data.dataHtml = dataHtml;
            return data;
        }
    },
    sb_fetchRepaymentInvoiceReport(params, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {};
            let repaymentDoc = Sb_Repayment.findOne({_id: params.repaymentId});
            if(repaymentDoc){
                let disDoc=Sb_Disbursement.findOne({_id:repaymentDoc.disbursementId});
                let clientDoc=Sb_Client.findOne({_id:repaymentDoc.clientId});
                data.disbursementDoc = disDoc;
                data.clientDoc = clientDoc;

            }
            data.repaymentDoc = repaymentDoc;
            return data;

        }
    }
})
