import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_Journal} from "../../../imports/collections/acc_journal";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Acc_ChartAccountBalance} from "../../../imports/collections/acc_chartAccountBalance";
import {Acc_ClosingEntry} from "../../../imports/collections/acc_closingEntry";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchTrialBalanceReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorClosingEntry = {};
            let selectorChartAccountBalance = {};

            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};

                selectorClosingEntry.branchId = {$in: params.branch || []};
                selectorChartAccountBalance.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorClosingEntry.branchId = {$in: userDoc.branch || []};
                selectorChartAccountBalance.branchId = {$in: userDoc.branch || []};
            }


            if (params.currencyId && params.currencyId !== "") {
                selector.currencyId = params.currencyId;
            }

            selector.journalDate = {$lte: params.dateAs};


            let companyDoc = Company.findOne({});

            data.currencyHeader = companyDoc.baseCurrency;
            let baseCurrency = companyDoc.baseCurrency;
            if (params.currencyId !== "") {
                selector.currencyId = params.currencyId;
                selectorChartAccountBalance.currencyId = params.currencyId;

                baseCurrency = params.currencyId;
                data.currencyHeader = params.currencyId;
            }
            if (params.dateAs !== undefined) {
                selectorClosingEntry.closeDate = {$lt: params.dateAs}
            }
            selectorClosingEntry.isDaily = {$ne: true};
            let closingEntry = Acc_ClosingEntry.findOne(selectorClosingEntry, {sort: {closeDate: -1}});
            if (closingEntry) {
                if (params.dateAs !== undefined) {
                    selector.journalDate = {
                        $lte: params.dateAs,
                        $gt: closingEntry.closeDate
                    };
                }
            } else {
                if (params.dateAs !== undefined) {
                    selector.journalDate = {
                        $lte: params.dateAs
                    };
                }
            }


            let exchange = Exchange.findOne({_id: params.exchangeId});


            let coefficientDrCr = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$transaction.drcr',
                baseCurrency: baseCurrency
            });
            let coefficientValue = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$value',
                baseCurrency: baseCurrency
            });

            //Range Date
            let journalList = Acc_Journal.aggregate([

                {
                    $match: selector
                },
                {
                    $project: {
                        _id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: "$transaction"
                    }

                },
                {
                    $unwind: {
                        path: "$transaction",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup:
                        {
                            from: "acc_chartAccount",
                            localField: "transaction.account",
                            foreignField: "_id",
                            as: "accountDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$accountDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }
                ,
                {
                    $project: {
                        id: "$_id",
                        currencyId: "$currencyId",
                        journalDate: "$journalDate",
                        total: "$total",
                        voucherId: "$voucherId",
                        memo: "$memo",
                        transaction: "$transaction",
                        copyTransaction: "$copyTransaction",
                        accountDoc: "$accountDoc",
                        drcr: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientDrCr.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientDrCr.KHR, coefficientDrCr.THB]}
                            }
                        },
                        dr: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientDrCr.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientDrCr.KHR, coefficientDrCr.THB]}
                            }
                        },
                        cr: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientDrCr.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientDrCr.KHR, coefficientDrCr.THB]}
                            }
                        }
                    }
                }, {
                    $project: {
                        id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: 1,
                        accountDoc: 1,
                        drcr: {$cond: [{$in: ["$accountDoc.accountTypeId", ["30", "32", "34", "36", "40", "41", "50", "51"]]}, {$multiply: ["$drcr", -1]}, "$drcr"]},
                    }
                },
                {$sort: {"accountDoc.code": -1}},
                {
                    $group: {
                        _id: {
                            accountDoc: "$accountDoc"
                        },
                        data: {$addToSet: "$$ROOT"},
                        total: {$sum: "$drcr"}
                    }
                }
            ], {allowDiskUse: true})

            //Closing Entry Date

            let chartAccountBalance = [];
            if (closingEntry) {
                selectorChartAccountBalance.month = closingEntry.month;
                selectorChartAccountBalance.year = closingEntry.year;


                chartAccountBalance = Acc_ChartAccountBalance.aggregate([
                    {$match: selectorChartAccountBalance},
                    {
                        $lookup:
                            {
                                from: "acc_chartAccount",
                                localField: "chartAccountId",
                                foreignField: "_id",
                                as: "accountDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$accountDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $project: {
                            chartAccountId: 1,
                            accountDoc: 1,
                            value: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientValue.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValue.KHR, coefficientValue.THB]}
                                }
                            }
                        }
                    },
                    {
                        $group: {
                            _id: {
                                chartAccountId: "$chartAccountId",
                                accountDoc: "$accountDoc"
                            },
                            value: {$sum: "$value"}
                        }
                    },
                    {
                        $project: {
                            _id: 0,
                            chartAccountId: "$_id.chartAccountId",
                            accountTypeId: "$_id.accountDoc.accountTypeId",
                            value: {$cond: [{$in: ["$_id.accountDoc.accountTypeId", ["30", "32", "34", "36", "40", "41", "50", "51"]]}, {$multiply: ["$value", -1]}, "$value"]}
                        }
                    }

                ], {allowDiskUse: true});
            }

            let dataHtml = "";

            let chartAccountParentList = Acc_ChartAccount.aggregate([

                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$_id",
                        connectFromField: "_id",
                        connectToField: "subAccountOf",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "childList"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true})


            let chartAccountList = Acc_ChartAccount.aggregate([
                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$subAccountOf",
                        connectFromField: "subAccountOf",
                        connectToField: "_id",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "parentDoc"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true});
            let j = 1;
            let totalDr = 0;
            let totalCr = 0;
            chartAccountList.forEach(function (chartAccDoc) {
                dataHtml += `<tr  class="rowClick" id="${chartAccDoc._id}"><th>${j}</th><th style="text-align: left !important;">${GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</th>
                         
            `;
                j++;
                let findJournalDocByChartAccountId = function (element) {
                    if (element._id.accountDoc._id === chartAccDoc._id) {
                        return element;
                    }
                }
                let journalDoc = journalList.find(findJournalDocByChartAccountId);
                let balance = 0;
                let totalParent = 0;
                let totalAccount = 0;

                let beginingBalClose = chartAccountBalance.find((o) => o.chartAccountId === chartAccDoc._id);
                if (beginingBalClose) {
                    totalAccount += beginingBalClose.value;
                    totalParent += beginingBalClose.value;


                }

                if (journalDoc) {
                    totalAccount += journalDoc.total;
                    totalParent += journalDoc.total;

                    if (["30", "32", "34", "36", "40", "41", "50", "51"].indexOf(chartAccDoc.accountTypeId) > -1) {
                        dataHtml += `
                        <th>${totalAccount > 0 ? "" : GlobalFn.formatCurrency(Math.abs(totalAccount), data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                        <th>${totalAccount > 0 ? GlobalFn.formatCurrency(totalAccount, data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15) : ""}</th>

                    </tr>
                `;
                        if (chartAccDoc.level === 0) {
                            totalDr += totalParent > 0 ? 0 : Math.abs(totalParent);
                            totalCr += totalParent > 0 ? totalParent : 0;
                        }
                    } else {
                        dataHtml += `
                        <th>${totalAccount > 0 ? GlobalFn.formatCurrency(totalAccount, data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15) : ""}</th>
                        <th>${totalAccount > 0 ? "" : GlobalFn.formatCurrency(Math.abs(totalAccount), data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `;
                        if (chartAccDoc.level === 0) {
                            totalCr += totalParent > 0 ? 0 : Math.abs(totalParent);
                            totalDr += totalParent > 0 ? totalParent : 0;
                        }
                    }


                } else {
                    let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                    if (childListDoc && childListDoc.childList.length > 0) {
                        let childListDocElement = childListDoc.childList.map(function (obj) {
                            return obj._id;
                        })

                        //Rank
                        let findJournalDocByParentId = function (element) {
                            if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                totalParent += element.total;
                            }
                        }

                        journalList.find(findJournalDocByParentId);

                        //Closing Entry

                        let findJournalDocClosingEntryByParentId = function (element) {
                            if (childListDocElement.indexOf(element.chartAccountId) > -1) {
                                totalParent += element.value;
                            }
                        }
                        chartAccountBalance.find(findJournalDocClosingEntryByParentId);

                    }

                    if (["30", "32", "34", "36", "40", "41", "50", "51"].indexOf(chartAccDoc.accountTypeId) > -1) {
                        dataHtml += `
                        <th>${totalParent > 0 ? "" : GlobalFn.formatCurrency(Math.abs(totalParent), data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                        <th>${totalParent > 0 ? GlobalFn.formatCurrency(totalParent, data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15) : ""}</th>

                    </tr>                    
                `;
                        if (chartAccDoc.level === 0) {
                            totalDr += totalParent > 0 ? 0 : Math.abs(totalParent);
                            totalCr += totalParent > 0 ? totalParent : 0;
                        }

                    } else {
                        dataHtml += `
                        <th>${totalParent > 0 ? GlobalFn.formatCurrency(totalParent, data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15) : ""}</th>
                        <th>${totalParent > 0 ? "" : GlobalFn.formatCurrency(Math.abs(totalParent), data.currencyHeader) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `;
                        if (chartAccDoc.level === 0) {
                            totalCr += totalParent > 0 ? 0 : Math.abs(totalParent);
                            totalDr += totalParent > 0 ? totalParent : 0;
                        }

                    }
                }

            })

            dataHtml += `
                <tr>
                    <th colspan="2">Total : </th>
                    <th>${GlobalFn.formatCurrency(totalDr, data.currencyHeader)}</th>
                    <th>${GlobalFn.formatCurrency(totalCr, data.currencyHeader)}</th>
                </tr>
                
        `;


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
