import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {
    Acc_ChartAccount,
    Acc_ChartAccountReact,
    Acc_ChartAccountAudit
} from "../../imports/collections/acc_chartAccount";
import {Branch} from "../../imports/collections/branch";
import {Acc_AccountType} from "../../imports/collections/acc_accountType";
import {Sb_Client} from "../../imports/collections/sb_client";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchChartAccount({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    let accountTypeList = Acc_AccountType.find({
                            name: {
                                $regex: reg,
                                $options: 'mi'
                            }
                        }, {_id: true},
                        {
                            $limit: options.limit
                        },
                        {
                            $skip: options.skip
                        }).fetch().map((obj) => {
                        return obj._id;
                    });
                    let subAccountOfList = Acc_ChartAccount.find({
                            $or: [
                                {
                                    name: {
                                        $regex: reg,
                                        $options: 'mi'
                                    }
                                },
                                {
                                    code: {
                                        $regex: reg,
                                        $options: 'mi'
                                    }
                                },
                            ]
                        }, {_id: true},
                        {
                            $limit: options.limit
                        },
                        {
                            $skip: options.skip
                        }).fetch().map((obj) => {
                        return obj._id;
                    });

                    selector.$or = [
                        {name: {$regex: reg, $options: 'mi'}},
                        {code: {$regex: reg, $options: 'mi'}},
                        {level: {$regex: reg, $options: 'mi'}},
                        {isPaidTax: {$regex: reg, $options: 'mi'}},
                        {isPayment: {$regex: reg, $options: 'mi'}},
                        {accountTypeId: {$in: accountTypeList}},
                        {subAccountOf: {$in: subAccountOfList}},
                    ];
                }
            }

            data.content = Acc_ChartAccount.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "acc_accountType",
                            localField: "accountTypeId",
                            foreignField: "_id",
                            as: "accountTypeDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$accountTypeDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "acc_chartAccount",
                            localField: "subAccountOf",
                            foreignField: "_id",
                            as: "subAccountOfDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$subAccountOfDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Acc_ChartAccount.find(selector).count();
            return data;
        }
    },
    acc_insertChartAccount(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                doc.level = 0;
                let parentDoc = Acc_ChartAccount.findOne({_id: doc.subAccountOf});
                if (parentDoc) {
                    doc.level = parentDoc.level + 1;
                }
                let id = Acc_ChartAccount.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Acc_ChartAccountReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    acc_updateChartAccount(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_ChartAccount.findOne({_id: id});

                let parentDoc = Acc_ChartAccount.findOne({_id: doc.subAccountOf});
                doc.level = 0;
                if (parentDoc) {
                    doc.level = parentDoc.level + 1;
                }
                let isUpdated = Acc_ChartAccount.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_ChartAccountReact, id, Acc_ChartAccountAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_updateMapChartAccount(doc, mapToAccount, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_ChartAccount.findOne({_id: doc._id});

                let isUpdated = Acc_ChartAccount.update({_id: doc._id}, {$set: {mapToAccount: mapToAccount}});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_ChartAccountReact, doc._id, Acc_ChartAccountAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_updateMapFixedAsset(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_ChartAccount.findOne({_id: doc.fixedAssetId});
                let mapFixedAssetObj = {};
                mapFixedAssetObj.mapFixedAsset = doc;

                let isUpdated = Acc_ChartAccount.update({_id: doc.fixedAssetId}, {$set: mapFixedAssetObj});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_ChartAccountReact, doc.fixedAssetId, Acc_ChartAccountAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_removeChartAccount(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Acc_ChartAccount.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Acc_ChartAccountReact, doc._id, Acc_ChartAccountAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findChartAccount(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Acc_ChartAccount.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findChartAccountById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                selector._id = id;
                return Acc_ChartAccount.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_fetchChartAccountOption(q, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                let list = [];
                Acc_ChartAccount.find(selector, {sort: {code: 1}}).fetch().forEach(function (obj) {
                    let subAccountOfDoc = Acc_ChartAccount.findOne({subAccountOf: obj._id});
                    let valDisable = false;
                    if (subAccountOfDoc) {
                        valDisable = true;
                    }
                    list.push({
                        label: GlobalFn.space(obj.level * 6) + obj.code + " | " + obj.name,
                        value: obj._id,
                        disabled: valDisable
                    })
                })
                return list;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    queryAccountTypeOption(accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {

                let list = [];
                Acc_AccountType.find().fetch().forEach(function (obj) {
                    list.push({label: obj._id + " : " + obj.name, value: obj._id});
                })
                return list;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    queryParentChartAccountOption(selector, chartAccountId, accessToken) {

        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {

                return Acc_ChartAccount.find(selector, {sort: {code: 1}}).fetch().map(function (obj) {
                    let valDisable = false;
                    if (obj._id === chartAccountId) {
                        valDisable = true;
                    }
                    return {
                        label: GlobalFn.space(obj.level * 6) + obj.code + " | " + obj.name,
                        value: obj._id,
                        disabled: valDisable

                    }
                })
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    queryChartAccountFixAssetOption(selector, accessToken) {

        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {

                let listFixedAsset = [];
                let listAccumulated = [];
                let listExpense = [];
                let obj = {};


                Acc_ChartAccount.find(selector, {sort: {code: 1}}).fetch().map(function (obj) {
                    let subAccountOfDoc = Acc_ChartAccount.findOne({subAccountOf: obj._id});
                    let valDisable = false;
                    if (subAccountOfDoc) {
                        valDisable = true;
                    }

                    if (obj.accountTypeId === "20") {
                        listFixedAsset.push({
                            label: GlobalFn.space(obj.level * 6) + obj.code + " | " + obj.name,
                            value: obj._id,
                            disabled: valDisable
                        })

                        listAccumulated.push({
                            label: GlobalFn.space(obj.level * 6) + obj.code + " | " + obj.name,
                            value: obj._id,
                            disabled: valDisable
                        })
                    } else if (obj.accountTypeId === "60" || obj.accountTypeId === "61") {
                        listExpense.push({
                            label: GlobalFn.space(obj.level * 6) + obj.code + " | " + obj.name,
                            value: obj._id,
                            disabled: valDisable
                        })
                    }
                })
                obj.listFixedAsset = listFixedAsset;
                obj.listAccumulated = listAccumulated;
                obj.listExpense = listExpense;
                return obj;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    queryChartAccountMethodOption(accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let list = [];
                let selector = {};
                selector.isPayment = true;
                Acc_ChartAccount.find(selector, {sort: {code: 1}}).fetch().forEach(function (obj) {
                    let subAccountOfDoc = Acc_ChartAccount.findOne({subAccountOf: obj._id});
                    let valDisable = false;
                    if (subAccountOfDoc) {
                        valDisable = true;
                    }
                    list.push({
                        label: obj.name,
                        value: obj._id,
                        disabled: valDisable
                    })
                })
                return list;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    queryAccountTypeOptionReport() {
        let data = {};
        let list = [];
        let listWithId = [];
        Acc_AccountType.find().fetch().forEach(function (obj) {
            list.push({label: obj.name, value: obj._id});
            listWithId.push(obj._id);
        });

        data.list = list;
        data.listWithId = listWithId;
        return data;
    },
})

//Unique

Acc_ChartAccount._ensureIndex({
    name: 1,
    code: 1,
}, {unique: 1, name: "Acc_ChartAccountUnique"});