import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Acc_FixedAsset, Acc_FixedAssetReact, Acc_FixedAssetAudit} from "../../imports/collections/acc_fixedAsset";
import {Branch} from "../../imports/collections/branch";
import {Company} from "../../imports/collections/company";
import numeral from "numeral"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchFixedAsset({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{"accountDoc.name": {$regex: reg, $options: 'mi'}}, {
                        code: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        description: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {life: {$regex: reg, $options: 'mi'}},
                        {value: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Acc_FixedAsset.aggregate([
                    {
                        $lookup: {
                            from: "acc_chartAccount",
                            localField: "account",
                            foreignField: "_id",
                            as: "accountDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$accountDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Acc_FixedAsset.find(selector).count();
            return data;
        }
    },
    acc_insertFixedAsset(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let transaction = generateScheduleFixedAsset(doc);
                doc.transaction = transaction;

                let id = Acc_FixedAsset.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Acc_FixedAssetReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    acc_updateFixedAsset(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_FixedAsset.findOne({_id: id});
                let transaction = generateScheduleFixedAsset(doc);
                doc.transaction = transaction;

                let isUpdated = Acc_FixedAsset.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_FixedAssetReact, id, Acc_FixedAssetAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_removeFixedAsset(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Acc_FixedAsset.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Acc_FixedAssetReact, doc._id, Acc_FixedAssetAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findFixedAsset(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Acc_FixedAsset.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findFixedAssetById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Acc_FixedAsset.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

})

//Unique

Acc_FixedAsset._ensureIndex({
    account: 1,
    value: 1,
    life: 1,
    estSalvage: 1,
    code: 1,
    description: 1,
    buyDate: 1,
    voucherId: 1,
    currencyId: 1,
    branchId: 1
}, {unique: 1, name: "Acc_FixedAssetUnique"});


function generateScheduleFixedAsset(doc) {
    let transaction = [];
    let curMonth = doc.buyDate.split("-")[1];
    let companyDoc = Company.findOne();
    if (companyDoc.depreciationType === "001") {
        let depPerYear = (doc.value - doc.estSalvage) / doc.life;
        if (curMonth !== "12") {
            for (let i = 1; i <= doc.life + 1; i++) {
                if (i === 1 || i === doc.life + 1) {
                    let maxMonth = i === 1 ? 12 - parseInt(curMonth) : parseInt(curMonth);
                    transaction.push({
                        year: i,
                        perMonth: numeral(GlobalFn.formatCurrency((depPerYear / 12), doc.currencyId)).value(),
                        perYear: numeral(GlobalFn.formatCurrency((depPerYear / 12) * maxMonth, doc.currencyId)).value(),
                        month: 0,
                        maxMonth: maxMonth,
                        status: false
                    })
                } else {
                    transaction.push({
                        year: i,
                        perMonth: numeral(GlobalFn.formatCurrency((depPerYear / 12), doc.currencyId)).value(),
                        perYear: numeral(GlobalFn.formatCurrency(depPerYear, doc.currencyId)).value(),
                        month: 0,
                        maxMonth: 12,
                        status: false
                    })
                }
            }
        } else {
            for (let i = 1; i <= doc.life; i++) {
                transaction.push({
                    year: i,
                    perMonth: numeral(GlobalFn.formatCurrency((depPerYear / 12), doc.currencyId)).value(),
                    perYear: numeral(GlobalFn.formatCurrency(depPerYear, doc.currencyId)).value(),
                    month: 0,
                    maxMonth: 12,
                    status: false
                })
            }
        }
    } else if (companyDoc.depreciationType === "002") {
        let percentage = math.round(((100 / doc.life) * 2), 2);
        let amount = doc.value;
        for (let i = 1; i <= doc.life; i++) {
            let depPerYear = 0;
            let maxMonth = 12;
            if (i === 1 || i === doc.life && curMonth !== 12) {
                maxMonth = i === 1 ? 12 - parseInt(curMonth) : parseInt(curMonth);
            }

            if (i === doc.life) {
                depPerYear = numeral(GlobalFn.formatCurrency(amount - doc.estSalvage, doc.currencyId)).value();
            } else {
                depPerYear = numeral(GlobalFn.formatCurrency(((amount - doc.estSalvage) * (percentage / 100)), doc.currencyId)).value();

            }

            transaction.push({
                year: i,
                perMonth: numeral(GlobalFn.formatCurrency(depPerYear / maxMonth, doc.currencyId)).value(),
                perYear: numeral(GlobalFn.formatCurrency(depPerYear, doc.currencyId)).value(),
                month: 0,
                maxMonth: maxMonth,
                status: false
            })
            amount -= depPerYear;

        }
    } else if (companyDoc.depreciationType === "003") {
        let numYear = 0;
        for (let i = 1; i <= doc.life; i++) {
            numYear += i;
        }

        let depreAmount = numeral(GlobalFn.formatCurrency((doc.value - doc.estSalvage), companyDoc.baseCurrency)).value();
        let y = 1;
        for (let i = doc.life; i > 0; i--) {
            let maxMonth = 12;
            if (i === 1 || i === doc.life && curMonth !== 12) {
                maxMonth = i === doc.life ? 12 - parseInt(curMonth) : parseInt(curMonth);
            }
            let depPerYear = numeral(GlobalFn.formatCurrency((i / numYear) * depreAmount, doc.currencyId)).value();
            transaction.push({
                year: y,
                perMonth: numeral(GlobalFn.formatCurrency((depPerYear / maxMonth), doc.currencyId)).value(),
                perYear: numeral(GlobalFn.formatCurrency(depPerYear, doc.currencyId)).value(),
                month: 0,
                maxMonth: maxMonth,
                status: false
            })
            y++;
        }
    }

    return transaction;
}
