import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {
    Sb_BusinessPartner,
    Sb_BusinessPartnerReact,
    Sb_BusinessPartnerAudit
} from "../../imports/collections/sb_businessPartner";
import {Branch} from "../../imports/collections/branch";
import {Sb_OpenAccount} from "../../imports/collections/sb_openAccount";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchBusinessPartner({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_BusinessPartner.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_BusinessPartner.find(selector).count();
            return data;
        }
    },
    sb_insertBusinessPartner(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_BusinessPartner.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_BusinessPartnerReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateBusinessPartner(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_BusinessPartner.findOne({_id: id});
                let isUpdated = Sb_BusinessPartner.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_BusinessPartnerReact, id, Sb_BusinessPartnerAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeBusinessPartner(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_BusinessPartner.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_BusinessPartnerReact, doc._id, Sb_BusinessPartnerAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findBusinessPartner(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_BusinessPartner.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findBusinessPartnerById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_BusinessPartner.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_fetchBusinessPartnerOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }

                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {company: {$regex: reg}},
                        {_id: q}
                    ];
                }
                return Sb_BusinessPartner.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + (obj.phoneNumber || "") + " : " + (obj.company || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})

//Unique

Sb_BusinessPartner._ensureIndex({
    name: 1,
    phoneNumber: 1,
    branchId: 1
}, {unique: 1, name: "Sb_BusinessPartnerUnique"});