import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Sb_RepaymentSchedule} from "../../../imports/collections/sb_repaymentSchedule";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import numeral from "numeral"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchProductActivityReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selectorConfig = {};
            let selectorDisbursement = {};

            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selectorDisbursement.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selectorDisbursement.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selectorDisbursement.coId = params.staffId;
            }


            let companyDoc = Company.findOne({});


            selectorDisbursement.$and = [
                {
                    $or: [
                        {status: "Active"},
                        {
                            payOffDate: {
                                $gt: params.dateRange[1],
                            }
                        },
                        {
                            writeOffDate: {
                                $gt: params.dateRange[1],
                            }
                        }
                    ]
                },
                {
                    $or: [
                        {status: {$ne: "Write Off"}},
                        {
                            writeOffDate: {
                                $gt: params.dateRange[1],
                            }
                        }
                    ]
                },
                {
                    status: {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]}
                }
            ];

            selectorDisbursement.disbursementDate = {$lte: params.dateRange[1]};

            //Range Date
            let loanProductActivityList = Sb_Disbursement.aggregate([

                    {
                        $match: selectorDisbursement
                    },
                    {
                        $lookup:
                            {
                                from: "sb_repayment",
                                localField: "_id",
                                foreignField: "disbursementId",
                                as: "repaymentDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$repaymentDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },

                    {
                        $group: {
                            _id: {
                                _id: "$_id"
                            },
                            clientId: {$last: "$clientId"},
                            currencyId: {$last: "$currencyId"},
                            productId: {$last: "$productId"},
                            disbursementDate: {$last: "$disbursementDate"},
                            coId: {$last: "$coId"},
                            loanAmount: {$last: "$loanAmount"},
                            repaymentIdList: {$push: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, "$repaymentDoc._id", null]}},
                            totalPrinciple: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, "$repaymentDoc.principlePaid", 0]}},
                            totalInterest: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, "$repaymentDoc.interestPaid", 0]}},
                            totalOperationFee: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, "$repaymentDoc.operationFeePaid", 0]}},
                            totalPenalty: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, "$repaymentDoc.penaltyPaid", 0]}},
                            totalFee: {$sum: {$cond: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$cond: [{$eq: ["$repaymentDoc.type", "Fee"]}, "$repaymentDoc.paid", 0]}, 0]}},

                            totalPrincipleInRange: {$sum: {$cond: [{$and: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$gte: ["$repaymentDoc.repaymentDate", params.dateRange[0]]}]}, "$repaymentDoc.principlePaid", 0]}},
                            totalInterestInRange: {$sum: {$cond: [{$and: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$gte: ["$repaymentDoc.repaymentDate", params.dateRange[0]]}]}, "$repaymentDoc.interestPaid", 0]}},
                            totalOperationFeeInRange: {$sum: {$cond: [{$and: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$gte: ["$repaymentDoc.repaymentDate", params.dateRange[0]]}]}, "$repaymentDoc.operationFeePaid", 0]}},
                            totalPenaltyInRange: {$sum: {$cond: [{$and: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$gte: ["$repaymentDoc.repaymentDate", params.dateRange[0]]}]}, "$repaymentDoc.penaltyPaid", 0]}},
                            totalFeeInRange: {$sum: {$cond: [{$and: [{$lte: ["$repaymentDoc.repaymentDate", params.dateRange[1]]}, {$gte: ["$repaymentDoc.repaymentDate", params.dateRange[0]]}]}, {$cond: [{$eq: ["$repaymentDoc.type", "Fee"]}, "$repaymentDoc.paid", 0]}, 0]}},
                        }
                    },

                    {
                        $lookup:
                            {
                                from: "sb_repaymentSchedule",
                                localField: "_id._id",
                                foreignField: "loanId",
                                as: "repaymentScheduleDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$repaymentScheduleDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: {
                            "repaymentScheduleDoc.isPaid": false
                        }
                    },
                    {
                        $group: {
                            _id: {
                                _id: "$_id._id"
                            },
                            clientId: {$last: "$clientId"},
                            currencyId: {$last: "$currencyId"},
                            productId: {$last: "$productId"},
                            disbursementDate: {$last: "$disbursementDate"},
                            repaidFrequency: {$last: "$repaidFrequency"},
                            coId: {$last: "$coId"},
                            repaymentIdList: {$last: "$repaymentIdList"},
                            loanAmount: {$last: "$loanAmount"},
                            totalPrinciple: {$last: "$totalPrinciple"},
                            totalInterest: {$last: "$totalInterest"},
                            totalPenalty: {$last: "$totalPenalty"},
                            totalOperationFee: {$last: "$totalOperationFee"},
                            totalFee: {$last: "$totalFee"},

                            totalPrincipleInRange: {$last: "$totalPrincipleInRange"},
                            totalInterestInRange: {$last: "$totalInterestInRange"},
                            totalPenaltyInRange: {$last: "$totalPenaltyInRange"},
                            totalOperationFeeInRange: {$last: "$totalOperationFeeInRange"},
                            totalFeeInRange: {$last: "$totalFeeInRange"},

                            firstScheduleDate: {$first: "$repaymentScheduleDoc.date"},
                            principleUnpaid: {$sum: "$repaymentScheduleDoc.principleUnpaid"},

                            repaymentScheduleDoc: {$last: "$repaymentScheduleDoc"},
                            arreasPrinciple: {$sum: {$cond: [{$and: [{$lt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$eq: ["$repaymentScheduleDoc.isPaid", false]}]}, "$repaymentScheduleDoc.principleUnpaid", 0]}},

                            arreasInterest: {$sum: {$cond: [{$and: [{$lt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$eq: ["$repaymentScheduleDoc.isPaid", false]}]}, "$repaymentScheduleDoc.interestUnpaid", 0]}},
                            arreasOperationFee: {$sum: {$cond: [{$and: [{$lt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$eq: ["$repaymentScheduleDoc.isPaid", false]}]}, "$repaymentScheduleDoc.operationFeeUnpaid", 0]}},
                        }
                    },


                    {
                        $unwind: {
                            path: "$repaymentScheduleDoc.paid",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $group: {
                            _id: {
                                _id: "$_id._id"
                            },
                            clientId: {$last: "$clientId"},
                            currencyId: {$last: "$currencyId"},
                            productId: {$last: "$productId"},
                            disbursementDate: {$last: "$disbursementDate"},
                            repaidFrequency: {$last: "$repaidFrequency"},
                            coId: {$last: "$coId"},
                            loanAmount: {$last: "$loanAmount"},
                            totalPrinciple: {$last: "$totalPrinciple"},
                            totalInterest: {$last: "$totalInterest"},
                            totalPenalty: {$last: "$totalPenalty"},
                            totalOperationFee: {$last: "$totalOperationFee"},
                            totalFee: {$last: "$totalFee"},

                            totalPrincipleInRange: {$last: "$totalPrincipleInRange"},
                            totalInterestInRange: {$last: "$totalInterestInRange"},
                            totalPenaltyInRange: {$last: "$totalPenaltyInRange"},
                            totalOperationFeeInRange: {$last: "$totalOperationFeeInRange"},
                            totalFeeInRange: {$last: "$totalFeeInRange"},

                            firstScheduleDate: {$first: "$firstScheduleDate"},
                            principleUnpaid: {$first: "$principleUnpaid"},

                            prepayPrinciple: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.principlePaid", 0]}},
                            prepayInterest: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.interestPaid", 0]}},
                            prepayOperationFee: {$sum: {$cond: [{$and: [{$gt: ["$repaymentScheduleDoc.date", params.dateRange[1]]}, {$in: ["$repaymentScheduleDoc.paid.repaymentId", "$repaymentIdList"]}, {$ne: ["$repaymentIdList", null]}]}, "$repaymentScheduleDoc.paid.operationFeePaid", 0]}},
                            arreasPrinciple: {$last: "$arreasPrinciple"},
                            arreasInterest: {$last: "$arreasInterest"},
                            arreasOperationFee: {$last: "$arreasOperationFee"},

                        }
                    },

                    {
                        $lookup:
                            {
                                from: "sb_client",
                                localField: "clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $group: {
                            _id: {
                                coId: "$coId",
                                currencyId: "$currencyId"
                            },
                            currencyId: {$last: "$currencyId"},
                            loanAmount: {$sum: "$loanAmount"},
                            newLoanAmount: {$sum: {$cond: [{$lte: ["$clientDoc.loanCycle", 1]}, "$loanAmount", 0]}},
                            newClient: {$sum: {$cond: [{$lte: ["$clientDoc.loanCycle", 1]}, 1, 0]}},
                            oldClient: {$sum: {$cond: [{$lte: ["$clientDoc.loanCycle", 1]}, 0, 1]}},
                            totalClient: {$sum: 1},

                            totalPrinciple: {$sum: "$totalPrinciple"},
                            totalInterest: {$sum: "$totalInterest"},
                            totalPenalty: {$sum: "$totalPenalty"},
                            totalOperationFee: {$sum: "$totalOperationFee"},
                            totalFee: {$sum: "$totalFee"},

                            totalPrincipleInRange: {$sum: "$totalPrincipleInRange"},
                            totalInterestInRange: {$sum: "$totalInterestInRange"},
                            totalPenaltyInRange: {$sum: "$totalPenaltyInRange"},
                            totalOperationFeeInRange: {$sum: "$totalOperationFeeInRange"},
                            totalFeeInRange: {$sum: "$totalFeeInRange"},

                            prepayPrinciple: {$sum: "$prepayPrinciple"},
                            prepayInterest: {$sum: "$prepayInterest"},
                            prepayOperationFee: {$sum: "$prepayOperationFee"},


                            principleUnpaid: {$sum: {$cond: [{$and: [{$lt: ["$firstScheduleDate", params.dateRange[1]]}]}, "$principleUnpaid", 0]}},
                            principleUnpaidNBC: {$sum: {$cond: [{$and: [{$lt: ["$firstScheduleDate", moment(moment(params.dateRange[1], "YYYY-MM-DD").add(-30, "days").toDate()).format("YYYY-MM-DD")]}]}, "$principleUnpaid", 0]}},

                            arreasPrinciple: {$sum: "$arreasPrinciple"},
                            arreasInterest: {$sum: "$arreasInterest"},
                            arreasOperationFee: {$sum: "$arreasOperationFee"},
                        }
                    },
                    {
                        $sort: {
                            arreasPrinciple: -1
                        }
                    },


                    {
                        $lookup:
                            {
                                from: "sb_staff",
                                localField: "_id.coId",
                                foreignField: "_id",
                                as: "creditOfficerDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$creditOfficerDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });

            data.currencyHeader = companyDoc.baseCurrency;
            let dataHtml = "";

            let totalNewClient = 0;
            let totalOldClient = 0;

            let totalAmountUSD = 0;
            let totalNewAmountUSD = 0;
            let totalFeeUSD = 0;
            let totalColPrinUSD = 0;
            let totalColIntUSD = 0;
            let totalColOptFeeUSD = 0;
            let totalColPenaltyUSD = 0;
            let totalLoanOutUSD = 0;
            let totalArrearsPrintUSD = 0;
            let totalArrearsPrintNBCUSD = 0;
            let totalArrearsIntUSD = 0;
            let totalArrearsOptFeeUSD = 0;
            let totalCollectUSD = 0;

            let totalAmountKHR = 0;
            let totalNewAmountKHR = 0;
            let totalFeeKHR = 0;
            let totalColPrinKHR = 0;
            let totalColIntKHR = 0;
            let totalColOptFeeKHR = 0;
            let totalColPenaltyKHR = 0;
            let totalLoanOutKHR = 0;
            let totalArrearsPrintKHR = 0;
            let totalArrearsPrintNBCKHR = 0;
            let totalArrearsIntKHR = 0;
            let totalArrearsOptFeeKHR = 0;
            let totalCollectKHR = 0;


            let totalAmountTHB = 0;
            let totalNewAmountTHB = 0;
            let totalFeeTHB = 0;
            let totalColPrinTHB = 0;
            let totalColIntTHB = 0;
            let totalColOptFeeTHB = 0;
            let totalColPenaltyTHB = 0;
            let totalLoanOutTHB = 0;
            let totalArrearsPrintTHB = 0;
            let totalArrearsPrintNBCTHB = 0;
            let totalArrearsIntTHB = 0;
            let totalArrearsOptFeeTHB = 0;
            let totalCollectTHB = 0;

            let totalAmount = 0;
            let totalNewAmount = 0;
            let totalFee = 0;
            let totalColPrin = 0;
            let totalColInt = 0;
            let totalColOptFee = 0;
            let totalColPenalty = 0;
            let totalLoanOut = 0;
            let totalArrearsPrint = 0;
            let totalArrearsPrintNBC = 0;
            let totalArrearsInt = 0;
            let totalArrearsOptFee = 0;
            let totalCollect = 0;
            let totalPar = 0;
            let totalParNBC = 0;

            let totalPrincipleUnpaid = 0;
            let totalPrincipleUnpaidNBC = 0;

            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";

            if (loanProductActivityList.length > 0) {
                let i = 1;
                loanProductActivityList.forEach((obj) => {

                    let par = (obj.principleUnpaid || 0) / ((obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple) || 1);
                    let parNBC = (obj.principleUnpaidNBC || 0) / ((obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple) || 1);

                    totalNewClient += obj.newClient;
                    totalOldClient += obj.oldClient;


                    totalPrincipleUnpaid += GlobalFn.exchange(obj._id.currencyId, companyDoc.baseCurrency, obj.principleUnpaid || 0, exchange);
                    totalPrincipleUnpaidNBC += GlobalFn.exchange(obj._id.currencyId, companyDoc.baseCurrency, obj.principleUnpaidNBC || 0, exchange);


                    dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.creditOfficerDoc.name}</td>
                            <td style="text-align: left !important;">${obj._id.currencyId}</td>

                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.newLoanAmount, obj.currencyId, companyDoc, branchDoc)}</td>

                            <td style="text-align: center !important;">${obj.newClient}</td>
                            <td style="text-align: center !important;">${obj.oldClient}</td>
                            <td style="text-align: center !important;">${obj.totalClient}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalFeeInRange, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalPrincipleInRange - obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalInterestInRange - obj.prepayInterest, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${displayFee}">${GlobalFn.formatCurrencyLast(obj.totalOperationFeeInRange - obj.prepayOperationFee, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.totalFeeInRange + obj.totalPrincipleInRange - obj.prepayPrinciple + obj.totalInterestInRange - obj.prepayInterest + obj.totalOperationFeeInRange - obj.prepayOperationFee + obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)}</td>
                            
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.arreasPrinciple, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${GlobalFn.formatCurrencyLast(obj.arreasInterest, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;display: ${displayFee}">${GlobalFn.formatCurrencyLast(obj.arreasOperationFee, obj.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: right !important;">${numeral(par * 100).format("0,00.00")}%</td>
                            <td style="text-align: right !important;">${numeral(parNBC * 100).format("0,00.00")}%</td>
                         
                                
                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {
                        totalAmountUSD += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalNewAmountUSD += numeral(GlobalFn.formatCurrencyLast(obj.newLoanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPrinUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalPrincipleInRange - obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColIntUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalInterestInRange - obj.prepayInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColOptFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFeeInRange - obj.prepayOperationFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPenaltyUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalCollectUSD += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange + obj.totalPrincipleInRange - obj.prepayPrinciple + obj.totalInterestInRange - obj.prepayInterest + obj.totalOperationFeeInRange - obj.prepayOperationFee + obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalLoanOutUSD += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintUSD += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintNBCUSD += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrincipleNBC, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsIntUSD += numeral(GlobalFn.formatCurrencyLast(obj.arreasInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsOptFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.arreasOperationFee, obj.currencyId, companyDoc, branchDoc)).value();


                    } else if (obj.currencyId === "KHR") {
                        totalAmountKHR += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalNewAmountKHR += numeral(GlobalFn.formatCurrencyLast(obj.newLoanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPrinKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalPrincipleInRange - obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColIntKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalInterestInRange - obj.prepayInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColOptFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFeeInRange - obj.prepayOperationFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPenaltyKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalCollectKHR += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange + obj.totalPrincipleInRange - obj.prepayPrinciple + obj.totalInterestInRange - obj.prepayInterest + obj.totalOperationFeeInRange - obj.prepayOperationFee + obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalLoanOutKHR += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintKHR += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintNBCKHR += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrincipleNBC, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsIntKHR += numeral(GlobalFn.formatCurrencyLast(obj.arreasInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsOptFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.arreasOperationFee, obj.currencyId, companyDoc, branchDoc)).value();


                    } else if (obj.currencyId === "THB") {
                        totalAmountTHB += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalNewAmountTHB += numeral(GlobalFn.formatCurrencyLast(obj.newLoanAmount, obj.currencyId, companyDoc, branchDoc)).value();
                        totalFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPrinTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalPrincipleInRange - obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColIntTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalInterestInRange - obj.prepayInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColOptFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalOperationFeeInRange - obj.prepayOperationFee, obj.currencyId, companyDoc, branchDoc)).value();
                        totalColPenaltyTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalCollectTHB += numeral(GlobalFn.formatCurrencyLast(obj.totalFeeInRange + obj.totalPrincipleInRange - obj.prepayPrinciple + obj.totalInterestInRange - obj.prepayInterest + obj.totalOperationFeeInRange - obj.prepayOperationFee + obj.totalPenaltyInRange, obj.currencyId, companyDoc, branchDoc)).value();
                        totalLoanOutTHB += numeral(GlobalFn.formatCurrencyLast(obj.loanAmount - obj.totalPrinciple + obj.prepayPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintTHB += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrinciple, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsPrintNBCTHB += numeral(GlobalFn.formatCurrencyLast(obj.arreasPrincipleNBC, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsIntTHB += numeral(GlobalFn.formatCurrencyLast(obj.arreasInterest, obj.currencyId, companyDoc, branchDoc)).value();
                        totalArrearsOptFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.arreasOperationFee, obj.currencyId, companyDoc, branchDoc)).value();


                    }
                })
            }


            totalAmount = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalAmountUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalAmountTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalAmountKHR, exchange);
            totalNewAmount = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalNewAmountUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalNewAmountTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalNewAmountKHR, exchange);
            totalFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFeeKHR, exchange);
            totalColPrin = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalColPrinUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalColPrinTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalColPrinKHR, exchange);
            totalColInt = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalColIntUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalColIntTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalColIntKHR, exchange);
            totalColOptFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalColOptFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalColOptFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalColOptFeeKHR, exchange);
            totalColPenalty = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalColPenaltyUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalColPenaltyTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalColPenaltyKHR, exchange);
            totalCollect = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalCollectUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalCollectTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalCollectKHR, exchange);
            totalLoanOut = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalLoanOutUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalLoanOutTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalLoanOutKHR, exchange);
            totalArrearsPrint = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalArrearsPrintUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalArrearsPrintTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalArrearsPrintKHR, exchange);
            totalArrearsPrintNBC = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalArrearsPrintNBCUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalArrearsPrintNBCTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalArrearsPrintNBCKHR, exchange);
            totalArrearsInt = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalArrearsIntUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalArrearsIntTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalArrearsIntKHR, exchange);
            totalArrearsOptFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalArrearsOptFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalArrearsOptFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalArrearsOptFeeKHR, exchange);


            totalPar = (totalPrincipleUnpaid || 0) / (totalLoanOut || 1);
            totalParNBC = (totalPrincipleUnpaidNBC || 0) / (totalLoanOut || 1);

            dataHtml += `
                <tr>
                    <td colspan="3">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalNewAmountUSD, "USD")}</td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td>${GlobalFn.formatCurrency(totalFeeUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPrinUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalColIntUSD, "USD")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalColOptFeeUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPenaltyUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalCollectUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalLoanOutUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsPrintUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsIntUSD, "USD")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalArrearsOptFeeUSD, "USD")}</td>
                    <td></td>
                    <td></td>
                  
                </tr> 
                <tr>
                    <td colspan="3">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalNewAmountKHR, "KHR")}</td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td>${GlobalFn.formatCurrency(totalFeeKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPrinKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalColIntKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalColOptFeeKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPenaltyKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalCollectKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalLoanOutKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsPrintKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsIntKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalArrearsOptFeeKHR, "KHR")}</td>
                    <td></td>
                    <td></td>
                  
                </tr> <tr>
                    <td colspan="3">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalNewAmountTHB, "THB")}</td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td style="text-align: center"></td>
                    <td>${GlobalFn.formatCurrency(totalFeeTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPrinTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalColIntTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalColOptFeeTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalColPenaltyTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalCollectTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalLoanOutTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsPrintTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalArrearsIntTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalArrearsOptFeeTHB, "THB")}</td>
                    <td></td>
                    <td></td>
                  
                </tr> 
                <tr>
                    <th colspan="3">${t[language]['grandTotal']}</th>
                    <th>${GlobalFn.formatCurrency(totalAmount, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalNewAmount, companyDoc.baseCurrency)}</th>
                    <td style="text-align: center !important;">${totalNewClient}</td>
                    <td style="text-align: center !important;">${totalOldClient}</td>
                    <td style="text-align: center !important;">${totalNewClient + totalOldClient}</td>
                    <th>${GlobalFn.formatCurrency(totalFee, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalColPrin, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalColInt, companyDoc.baseCurrency)}</th>
                    <th style="display: ${displayFee}">${GlobalFn.formatCurrency(totalColOptFee, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalColPenalty, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalCollect, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalLoanOut, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalArrearsPrint, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalArrearsInt, companyDoc.baseCurrency)}</th>
                    <th style="display: ${displayFee}">${GlobalFn.formatCurrency(totalArrearsOptFee, companyDoc.baseCurrency)}</th>
                    <td>${numeral(totalPar * 100).format("0,00.00")}%</td>
                    <td>${numeral(totalParNBC * 100).format("0,00.00")}%</td>
                  
                </tr>
        `;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
