import {Mongo} from "meteor/mongo";

export const Sb_Guarantor = new Mongo.Collection("sb_guarantor");
Sb_Guarantor.schema = new SimpleSchema({
    name: {
        type: String,
    },
    gender: {
        type: String,
    },
    homeNo: {
        type: String,
        optional: true
    },
    groupNo: {
        type: String,
        optional: true
    },
    facebookName: {
        type: String,
        optional: true
    },
    facebookId: {
        type: String,
        optional: true
    },
    provinceId: {
        type: String,
        optional: true
    },
    districtId: {
        type: String,
        optional: true
    },
    communeId: {
        type: String,
        optional: true
    },
    villageId: {
        type: String,
        optional: true
    },
    provinceName: {
        type: String,
        optional: true
    },
    districtName: {
        type: String,
        optional: true
    },
    communeName: {
        type: String,
        optional: true
    },
    villageName: {
        type: String,
        optional: true
    },
    address: {
        type: String,
        optional: true
    },
    dob: {
        type: String,
        optional: true
    },
    email: {
        type: String,
        optional: true
    },
    phoneNumber: {
        type: String,
        optional: true
    },
    job: {
        type: String,
        optional: true
    },
    nationId: {
        type: String,
        optional: true
    },
    type: {
        type: String,
        optional: true
    },
    openingBalance: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    loanCycle: {
        type: Number,
        defaultValue: 0,
        optional: true
    },
    order: {
        type: Number,
        optional: true,
        defaultValue: 0
    },
    note: {
        type: String,
        optional: true
    },
    checkIn: {
        type: String,
        optional: true
    },



    joinAccountName: {
        type: String,
        optional: true

    },
    joinAccountGender: {
        type: String,
        optional: true

    },
    joinAccountNationId: {
        type: String,
        optional: true
    },
    joinAccountType: {
        type: String,
        optional: true
    },
    income: {
        type: Number,
        optional: true
    },
    expense: {
        type: Number,
        optional: true
    },
    profit: {
        type: Number,
        optional: true
    },
    guarantorPropertyNote: {
        type: String,
        optional: true

    },


    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Guarantor.attachSchema(Sb_Guarantor.schema);


