import {Mongo} from "meteor/mongo";

export const Sb_Repayment = new Mongo.Collection("sb_repayment");
Sb_Repayment.schema = new SimpleSchema({
    clientId: {
        type: String,
        label: "Client",
    },
    disbursementId: {
        type: String,
        label: "Client",
    },
    authorizeUserId: {
        type: String,
        optional: true
    },
    currencyId: {
        type: String,
        label: "Currency",
    },
    type: {
        type: String,
    },
    paymentTypeId: {
        type: String,
        optional: true
    },
    payOnInstallment: {
        type: String,
        optional: true
    },
    repaymentDate: {
        type: String,
        label: "Repayment Date",
    },
    paidFromSaving: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    savingAmount: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    paid: {
        type: Number,
        decimal: true
    },
    penaltyPaid: {
        type: Number,
        decimal: true
    },
    principlePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    insurancePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    interestPaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    operationFeePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    lawFeePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    lawFee: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },

    roFeePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    bpFeePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    penalty: {
        type: Number,
        decimal: true
    },
    interestReminderCharge: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    interestReminderChargePaid: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    totalPaid: {
        type: Number,
        decimal: true
    },
    voucher: {
        type: String,
    },
    note: {
        type: String,
        optional: true
    },
    dayLate: {
        type: Number
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Repayment.attachSchema(Sb_Repayment.schema);


