import {Mongo} from "meteor/mongo";

export const Sb_CloseSaving = new Mongo.Collection("sb_closeSaving");
Sb_CloseSaving.schema = new SimpleSchema({
    closeDate: {
        type: String,
        label: "Close Date"
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_CloseSaving.attachSchema(Sb_CloseSaving.schema);


