import {Mongo} from "meteor/mongo";

export const CountsCollectionSheet = new Mongo.Collection('countsCollectionSheet');

export const Sb_RepaymentSchedule = new Mongo.Collection("sb_repaymentSchedule");
Sb_RepaymentSchedule.schema = new SimpleSchema({
    loanId: {
        type: String,
        label: "Client",
    },
    clientId: {
        type: String,
        label: "Client",
    },
    tmpRepaymentId: {
        type: String,
        optional: true
    },
    productId: {
        type: String,
        label: "Product",
    },
    currencyId: {
        type: String,
        label: "Currency",
    },
    installment: {
        type: Number
    },
    amount: {
        type: Number,
        decimal: true
    },
    principle: {
        type: Number,
        decimal: true
    },
    interest: {
        type: Number,
        decimal: true
    },
    operationFee: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    date: {
        type: String,
        label: "RepaymentSchedule Date",
    },

    dayRange: {
        type: Number
    },
    isPaid: {
        type: Boolean,
        defaultValue: false
    },
    isAllowClosing: {
        type: Boolean,
        defaultValue: false
    },
    balanceUnpaid: {
        type: Number,
        decimal: true
    },
    principleUnpaid: {
        type: Number,
        decimal: true
    },
    interestUnpaid: {
        type: Number,
        decimal: true
    },
    operationFeeUnpaid: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    paid: {
        type: [Object],
        optional: true,
        blackbox: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_RepaymentSchedule.attachSchema(Sb_RepaymentSchedule.schema);


