import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_Client, Sb_ClientReact, Sb_ClientAudit} from "../../imports/collections/sb_client"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchClient({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1, name: 1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        phoneNumber: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        order: parseFloat(q)
                    }, {
                        note: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        _id: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        address: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        nationId: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        checkIn: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            data.content = Sb_Client.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "createdUser",
                            foreignField: "_id",
                            as: "userCDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$userCDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "updatedUser",
                            foreignField: "_id",
                            as: "userUDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$userUDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_Client.find(selector).count();
            return data;
        }
    },
    sb_insertClient(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let clientDoc = Sb_Client.findOne({branchId: doc.branchId}, {sort: {order: -1}});
                doc.order = (clientDoc && clientDoc.order || 0) + 1;
                let id = Sb_Client.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_ClientReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateClient(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Client.findOne({_id: id});
                let isUpdated = Sb_Client.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_ClientReact, id, Sb_ClientAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_updateClientStatus(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Client.findOne({_id: doc._id});
                let isUpdated = Sb_Client.update({_id: doc._id}, {$set: {status: doc.status}});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_ClientReact, doc._id, Sb_ClientAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_updateClientConnect(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Client.findOne({_id: id});
                let isUpdated = Sb_Client.update({_id: id}, {
                    $set: {
                        sendToPhoneNumber: doc.sendToPhoneNumber,
                        isConnectPhoneNumber: doc.isConnectPhoneNumber,
                        sendToTelegram: doc.sendToTelegram,
                        isConnectTelegram: doc.isConnectTelegram,
                    }
                });
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_ClientReact, id, Sb_ClientAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeClient(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_Client.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_ClientReact, doc._id, Sb_ClientAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findClient(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_Client.find(selector, {sort: {name: 1}}).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findClientById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                selector._id = id;
                return Sb_Client.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findClientByIdList(idList, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = {$in: idList};
                return Sb_Client.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchClientOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }

                let qList = [];
                if (Array.isArray(q)) {
                    qList.push(...q);
                } else {
                    qList.push(q);
                }


                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {_id: {$in: qList}},
                        {_id: q}
                    ];
                }

                return Sb_Client.find(selector, {sort: {createdAt: -1},limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + (obj.dob ? " : " + (obj.dob || "") : "") + (obj.phoneNumber ? " : " + obj.phoneNumber : "") + (obj.status ? " : " + obj.status : ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_Client._ensureIndex({
    name: 1,
    phoneNumber: 1,
    nationId: 1,
    type: 1,
    checkIn: 1,
    branchId: 1
}, {unique: 1, name: "Sb_ClientUnique"});