import {Mongo} from "meteor/mongo";

export const Sb_LoanConfig = new Mongo.Collection("sb_loanConfig");
Sb_LoanConfig.schema = new SimpleSchema({
    slogan: {
        type: String,
        optional: true,
    },
    note: {
        type: String,
        optional: true,
    },
    operationFeeTitle: {
        type: String,
        optional: true
    },
    analystLevel: {
        type: Number,
        optional: true,
        defaultValue: 3
    },
    costOfFund: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    collectionSheetType: {
        type: String,
        optional: true,
    },
    repaymentScheduleType: {
        type: String,
        optional: true,
    },
    bankAccount: {
        type: String,
        optional: true,
    },
    workTime: {
        type: String,
        optional: true,
    },
    validateCustomer: {
        type: Boolean,
        defaultValue: false,
    },
    isOperationFee: {
        type: Boolean,
        defaultValue: false,

    },
    isHideNameInSchedule: {
        type: Boolean,
        defaultValue: false,
    },
    isHideRate: {
        type: Boolean,
        defaultValue: false,

    },
    isAccountantNameInSchedule: {
        type: Boolean,
        defaultValue: false,

    },
    isShowPrincipleDue: {
        type: Boolean,
        defaultValue: true,

    },
    isShowTotal: {
        type: Boolean,
        defaultValue: false,

    },
    isNotValidateAddress: {
        type: Boolean,
        defaultValue: false,
        optional: true

    },
    isNoteUp: {
        type: Boolean,
        defaultValue: false,

    },
    isPenaltyMinusGraceDay: {
        type: Boolean,
        defaultValue: false,

    },
    isSpecialLoan: {
        type: Boolean,
        defaultValue: false,

    },
    isNotPenalty: {
        type: Boolean,
        defaultValue: false,

    },
    isLoanAmountInCollect: {
        type: Boolean,
        defaultValue: false,

    },
    isPayOffInCollect: {
        type: Boolean,
        defaultValue: false,

    },
    isApproveProcess: {
        type: Boolean,
        defaultValue: false,

    },
    analystLoan: {
        type: Boolean,
        defaultValue: false,

    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_LoanConfig.attachSchema(Sb_LoanConfig.schema);


