import {Mongo} from "meteor/mongo";

export const Sb_LoanProduct = new Mongo.Collection("sb_loanProduct");
Sb_LoanProduct.schema = new SimpleSchema({
    rate: {
        type: Number,
        decimal: true,
        label: "Rate",
    },
    isSpecialLoan: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    isCustomPrinciple: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    isCustomInterestRate: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    rateType: {
        type: "String",
        label: "Rate Type"
    },
    productDate: {
        type: String,
        label: "Product Date",
    },
    status: {
        type: Boolean,
        defaultValue: false,
    },
    methodType: {
        type: String
    },
    name: {
        type: String,
        label: "Name",
    },
    productTypeId: {
        type: String,
        optional: true
    },
    penaltyId: {
        type: String,
        label: "Penalty",
    },
    penaltyClosingId: {
        type: String,
        label: "Penalty Closing",
    },
    description: {
        type: String,
        label: "Description",
        optional: true
    },
    interestList: {
        type: [Object],
        optional: true,
        blackbox: true
    },
    'interestList.$': {
        type: Object
    },
    'interestList.$.from': {
        type: Number,
        optional: true,
    },
    'interestList.$.to': {
        type: Number,
        optional: true,
    },
    'interestList.$.principle': {
        type: Number,
        decimal: true,
        optional: true,
    },
    'interestList.$.rate': {
        type: Number,
        decimal: true,
        optional: true,
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_LoanProduct.attachSchema(Sb_LoanProduct.schema);


