    sb_findDisbursementInGroupById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                let selectorGroup = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                    selectorGroup.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                    selectorGroup.branchId = branchId;
                }
                selector._id = id;
                let disDoc = Sb_Disbursement.findOne(selector);

                selectorGroup.groupId = disDoc.groupId || "donthave";
                selectorGroup._id = {$ne: id};
                return Sb_Disbursement.aggregate([
                        {
                            $match: selectorGroup
                        },
                        {
                            $lookup: {
                                from: "sb_client",
                                localField: "clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                        },
                        {
                            $unwind: {
                                path: "$clientDoc",
                                preserveNullAndEmptyArrays: true
                            }
                        },
                    ],
                    {
                        allowDiskUse: true
                    });

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findDisbursementByGuarantorId(guarantorId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};

                selector.guarantorId = guarantorId;
                return Sb_Disbursement.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchDisbursementOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                let selectorCli = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                    selectorCli.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                    selectorCli.branchId = branchId;
                }
                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');
                    selectorCli.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}}
                    ];
                    let clientList = Sb_Client.find(selectorCli, {limit: 300}).map((obj) => obj._id);
                    selector.$or = [
                        {_id: {$regex: reg}},
                        {loanAcc: {$regex: reg}},
                        {description: {$regex: reg}},
                        {clientId: {$in: clientList}}
                    ];
                }

                selector.status = "Active";
                return Sb_Disbursement.aggregate([
                    {$match: selector},
                    {$sort: {createdAt: -1}},
                    {$limit: 300},
                    {

                        $lookup: {
                            from: "sb_client",
                            localField: "clientId",
                            foreignField: "_id",
                            as: "clientDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },


                ]).map(obj => ({
                    label: obj.loanAcc + " | Client:  " + obj.clientDoc.name + " | Amount : " + GlobalFn.formatCurrency(obj.loanAmount, obj.currencyId) + " | Dis Date:  " + obj.disbursementDate + (obj.description ? " [" + obj.description + "]" : "") + (obj.clientDoc.phoneNumber ? "  |  " + obj.clientDoc.phoneNumber : ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
