let calculateProjectInterest = function (disbursementDoc, productDoc) {
    let projectInterest = 0;

    if (productDoc.isSpecialLoan === true) {
        return disbursementDoc.specialInterest * (disbursementDoc.term / disbursementDoc.repaidFrequency);
    }
    if (productDoc.methodType === "Straight Line") {
        projectInterest = disbursementDoc.loanAmount * (productDoc.rate / 100) * disbursementDoc.term;
    } else {
        return 0;
    }


    return GlobalFn.roundCurrency(projectInterest, disbursementDoc.currencyId, disbursementDoc.branchId);
}

let removeRepaymentSchedule = function (loanId) {
    return Sb_RepaymentSchedule.remove({loanId: loanId});
}

let escapeDayFn = function (setup, escapeList, repayDate) {
    let yearName = moment(repayDate, "YYYY-MM-DD").format("YYYY");

    let findEscape = escapeList.find(function (element) {
        return element.year === yearName;
    });


    if (setup.escapeType === "No Escape") {
        return repayDate;
    } else if (setup.escapeType === "Before Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeBeforeDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else if (setup.escapeType === "After Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeAfterDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else {
        return repayDate;
    }
}

let changeDayDown = function (day) {
    if (day === 0) {
        return 6;
    } else {
        day = day - 1;
        return day;
    }
}
let changeDayUp = function (day) {
    if (day === 0) {
        return 1;
    } else if (day === 6) {
        return 0;
    } else {
        day = day + 1;
        return day;
    }
}

let escapeAfterDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, 1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();

    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayUp(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, changeWeekendDay, "days");
    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
};

let escapeBeforeDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, -1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();
    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayDown(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, -changeWeekendDay, "days");

    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
}


let makeRepaymentWhenUpdate = function (disbursementId, doc, repaymentId) {
    let repaymentScheduleList = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
    let amountPaid = doc.paid;
    let operationFeePaid = 0;
    let interestPaid = 0;
    let principlePaid = 0;
    let savingAmount = doc.savingAmount || 0;
    let disBursementDoc = Sb_Disbursement.findOne({_id: disbursementId});
    if (repaymentScheduleList.length > 0) {
        Sb_Repayment.update({_id: repaymentId}, {
            $set: {
                principlePaid: 0,
                interestPaid: 0,
                operationFeePaid: 0
            }
        });
        repaymentScheduleList.forEach((obj) => {
            if (amountPaid > 0) {
                let repaymentScheduleDoc = obj;
                let paid = obj.paid || [];
                let paidDoc = {};
                paidDoc.repaymentId = repaymentId;

                paidDoc.operationFeePaid = amountPaid >= (obj.operationFeeUnpaid || 0) ? (obj.operationFeeUnpaid || 0) : amountPaid;
                amountPaid = amountPaid - paidDoc.operationFeePaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.operationFeePaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.operationFeeUnpaid = repaymentScheduleDoc.operationFeeUnpaid - paidDoc.operationFeePaid;

                paidDoc.interestPaid = amountPaid >= obj.interestUnpaid ? obj.interestUnpaid : amountPaid;
                amountPaid = amountPaid - paidDoc.interestPaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.interestPaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.interestUnpaid = repaymentScheduleDoc.interestUnpaid - paidDoc.interestPaid;

                paidDoc.principlePaid = amountPaid >= obj.principleUnpaid ? obj.principleUnpaid : amountPaid;
                amountPaid = amountPaid - paidDoc.principlePaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.principlePaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.principleUnpaid = repaymentScheduleDoc.principleUnpaid - paidDoc.principlePaid;

                paid.push(paidDoc);
                repaymentScheduleDoc.isPaid = !(math.round(repaymentScheduleDoc.balanceUnpaid, 2) > 0);
                repaymentScheduleDoc.paid = paid;


                //លុយរាប់ មិនកំណត់Term
                if (disBursementDoc.term === 0 && math.round(repaymentScheduleDoc.interestUnpaid) === 0 && repaymentScheduleDoc.principleUnpaid > 0) {
                    if (math.round(repaymentScheduleDoc.interestUnpaid) === 0) {
                        repaymentScheduleDoc.isPaid = true;
                    }
                    let i = obj.installment + 1;
                    let j = i - 1;
                    let setup = Branch.findOne({_id: disBursementDoc.branchId});
                    let escapeDayList = Sb_Holiday.find({}).fetch();
                    let productDoc = Sb_LoanProduct.findOne({_id: disBursementDoc.productId});
                    let paidDate = "";
                    let numDay = "";
                    let rawPaidDate = obj.repaymentDate;
                    if (productDoc.rateType === "Monthly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, j * disBursementDoc.repaidFrequency, "month");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Weekly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, 7 * j * disBursementDoc.repaidFrequency, "days");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Daily") {
                        paidDate = GlobalFn.addDay(rawPaidDate, disBursementDoc.repaidFrequency, "days");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Yearly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, j * disBursementDoc.repaidFrequency, "year");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    }


                    let repaymentScheduleList = [];
                    let repaymentScheduleDocTmp = {};
                    repaymentScheduleDocTmp._id = disBursementDoc._id + i;
                    repaymentScheduleDocTmp.installment = i;
                    repaymentScheduleDocTmp.tmpRepaymentId = repaymentId;
                    repaymentScheduleDocTmp.date = paidDate;
                    repaymentScheduleDocTmp.principle = repaymentScheduleDoc.principleUnpaid;
                    repaymentScheduleDocTmp.interest = GlobalFn.roundCurrency(repaymentScheduleDoc.principleUnpaid * disBursementDoc.repaidFrequency * productDoc.rate / 100, disBursementDoc.currencyId, disBursementDoc.branchId);
                    repaymentScheduleDocTmp.amount = repaymentScheduleDocTmp.principle + repaymentScheduleDocTmp.interest;
                    repaymentScheduleDocTmp.operationFee = 0;

                    repaymentScheduleDocTmp.amount = repaymentScheduleDocTmp.amount + repaymentScheduleDocTmp.operationFee;
                    repaymentScheduleDocTmp.isPaid = false;
                    repaymentScheduleDocTmp.isAllowClosing = true;

                    repaymentScheduleDocTmp.loanId = disBursementDoc._id;
                    repaymentScheduleDocTmp.clientId = disBursementDoc.clientId;
                    repaymentScheduleDocTmp.productId = disBursementDoc.productId;
                    repaymentScheduleDocTmp.currencyId = disBursementDoc.currencyId;
                    repaymentScheduleDocTmp.branchId = disBursementDoc.branchId;

                    repaymentScheduleDocTmp.balanceUnpaid = repaymentScheduleDocTmp.amount;
                    repaymentScheduleDocTmp.principleUnpaid = repaymentScheduleDocTmp.principle;
                    repaymentScheduleDocTmp.interestUnpaid = repaymentScheduleDocTmp.interest;
                    repaymentScheduleDocTmp.operationFeeUnpaid = repaymentScheduleDocTmp.operationFee;

                    repaymentScheduleDocTmp.dayRange = numDay;


                    repaymentScheduleList.push(repaymentScheduleDocTmp);
                    Sb_RepaymentSchedule.rawCollection().insert(repaymentScheduleList);
                }
                //=============End លុយរាប់មិនកំណត់Term


                Sb_RepaymentSchedule.update({_id: obj._id}, {$set: repaymentScheduleDoc});
                Sb_Repayment.update({_id: repaymentId}, {
                    $inc: {
                        principlePaid: paidDoc.principlePaid,
                        interestPaid: paidDoc.interestPaid,
                        operationFeePaid: paidDoc.operationFeePaid
                    }
                });

                principlePaid += paidDoc.principlePaid;
                interestPaid += paidDoc.interestPaid;
                operationFeePaid += paidDoc.operationFeePaid;

            } else {
                let checkPayOff = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
                if (checkPayOff.length === 0) {
                    Sb_Disbursement.update({_id: disbursementId}, {
                        $set: {
                            status: "Pay Off",
                            payOffDate: doc.repaymentDate
                        }
                    });
                }
                return false;
            }
        })
        Sb_Disbursement.update({_id: disbursementId}, {$inc: {savingAmount: savingAmount}});


        let checkPayOff = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
        if (checkPayOff.length === 0) {
            Sb_Disbursement.update({_id: disbursementId}, {
                $set: {
                    status: "Pay Off",
                    payOffDate: doc.repaymentDate
                }
            });
        }
    }
}


