import {Mongo} from "meteor/mongo";

export const Sb_Penalty = new Mongo.Collection("sb_penalty");
Sb_Penalty.schema = new SimpleSchema({
    name: {
        type: String,
        label: "Name",
    },
    amountUSD: {
        type: Number,
        label: "Amount",
        decimal: true
    },
    amountKHR: {
        type: Number,
        label: "Amount",
        decimal: true
    },
    amountTHB: {
        type: Number,
        label: "Amount",
        decimal: true
    },
    graceDay: {
        type: Number
    },

    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Penalty.attachSchema(Sb_Penalty.schema);


