import {Meteor} from 'meteor/meteor';
import {Acc_JournalReact} from "../../imports/collections/acc_journal";

if (Meteor.isServer) {
    Meteor.publish('acc_journalReact', function () {
        if (this.userId) {
            return Acc_JournalReact.find({});
        }
        return this.ready();

    });
}
