import {Meteor} from 'meteor/meteor';
import {Sb_DisbursementReact} from "../../imports/collections/sb_disbursement";

if (Meteor.isServer) {
    Meteor.publish('sb_disbursementReact', function () {
        if (this.userId) {
            return Sb_DisbursementReact.find({});
        }
        return this.ready();

    });
}
