import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_Journal} from "../../../imports/collections/acc_journal";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchJournalReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
            }


            if (params.currencyId && params.currencyId !== "") {
                selector.currencyId = params.currencyId;
            }


            selector.journalDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let chartAccountList = Acc_ChartAccount.find().fetch();
            let dataList = Acc_Journal.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        journalDate: 1
                    }
                },


            ], {allowDiskUse: true});


            let dataHtml = "";


            let i = 1;
            dataList.map(function (obj) {
                let accountName = "";
                let dr = "";
                let cr = "";
                let transactionLength = 1;
                if (obj.transaction.length > 1) {
                    transactionLength = obj.transaction.length + 1;
                } else {
                    transactionLength++;
                }

                dataHtml += `<tr class="rowClick" id="${obj._id}" >
                                <td rowspan="${transactionLength}"​ style="vertical-align: middle; text-align: center !important;">${i}</td>
                                <td rowspan="${transactionLength}"​ style="vertical-align: middle; text-align: center !important;">${GlobalFn.formatDate(obj.journalDate) }</td>
                                <td rowspan="${transactionLength}"​ style="vertical-align: middle; text-align: left !important;">${obj.voucherId.substr(obj.voucherId.length - 6, 6)}</td>
                                <td rowspan="${transactionLength}"​ style="vertical-align: middle; text-align: left !important;">${obj.currencyId}</td>
                                <td rowspan="${transactionLength}"​ style="vertical-align: middle; text-align: left !important;">${obj.memo}</td>
                         
                            `;
                let j = 1;
                obj.transaction.forEach(function (ob) {
                    let findChartAccountById = function (element) {
                        if (element._id == ob.account) {
                            return element;
                        }
                    }

                    let chartAccountDoc = chartAccountList.find(findChartAccountById);
                    accountName = (chartAccountDoc && chartAccountDoc.code || "") + " : " + (chartAccountDoc && chartAccountDoc.name || "");
                    dr = GlobalFn.formatCurrency(ob.dr, obj.currencyId);
                    cr = GlobalFn.formatCurrency(ob.cr, obj.currencyId);
                    if (j == 1) {
                        dataHtml += `<td style="text-align: left !important;">${accountName}</td>
                                  <td>${dr}</td>
                                  <td>${cr}</td>
                               </tr>
                                   
                    `;

                    } else {
                        dataHtml += `<tr>
                                        <td style="text-align: left !important;">${accountName}</td>
                                        <td>${dr}</td>
                                        <td>${cr}</td>
                                  </tr>
                       
                    `;
                    }
                    j++;
                })
                dataHtml += `<tr>
                        <td><b>Total:</b></td>
                        <td>${GlobalFn.formatCurrency(obj.total, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId)}</td>
                        <td>${GlobalFn.formatCurrency(obj.total, obj.currencyId) + GlobalFn.getCurrencySymbol(obj.currencyId)}</td>
            </tr>`;
                i++;
            });


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
