import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Survey} from "../../../imports/collections/sb_survey";
import {Exchange} from "../../../imports/collections/exchange";
import numeral from "numeral"
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchSurveyReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorDisburs = {};
            let selectorConfig = {};
            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }

            if (params.staffId && params.staffId !== "") {
                selectorDisburs["disbursementDoc.coId"] = params.staffId;
            }


            selector.date = {$gte: params.dateRange[0], $lte: params.dateRange[1]};

            if (params.type === "true") {
                selector.isAgreeWithConsultant = true;
            } else if (params.type === "false") {
                selector.isAgreeWithConsultant = false;
            }

            let dataList = Sb_Survey.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        surveyDate: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "coId",
                        foreignField: "_id",
                        as: "staffDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$staffDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_ourService",
                        localField: "chooseOurService",
                        foreignField: "_id",
                        as: "serviceDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$serviceDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },

            ], {allowDiskUse: true});


            let dataHtml = "";

            let i = 1;
            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                    <tr>
                             <td style="text-align: center !important;">${i}</td>
                             <td style="text-align: center !important;">${obj.date}</td>
                            <td style="text-align: left !important;">${obj.staffDoc && obj.staffDoc.name || ""}</td>
                            <td style="text-align: left !important;">${obj.name}</td>
                            <td style="text-align: left !important;">${obj.gender === "1" ? "ប្រុស" : "ស្រី"}</td>
                            
                            
                            <td style="text-align: left !important;">${obj.age || ""}</td>
                            <td style="text-align: left !important;">${obj.job || ""}</td>
                            <td style="text-align: left !important;">${obj.phoneNumber || ""}</td>
                            
                            <td style="text-align: left !important;">${obj.address || ""}</td>
                            <td style="text-align: left !important;">${obj.note || ""}</td>
                            <td style="text-align: left !important;">${obj.facebookName || ""}</td>
                            <td style="text-align: left !important;">${obj.familyMemberEarnMoney || ""}</td>
                            <td style="text-align: left !important;">${obj.owingCompany || ""}</td>
                            <td style="text-align: left !important;">${obj.loanAmount || ""}</td>
                            <td style="text-align: left !important;">${obj.term || ""}</td>
                            <td style="text-align: left !important;">${obj.loanOutstanding || ""}</td>
                            <td style="text-align: left !important;">${obj.termOutstanding || ""}</td>
                            <td style="text-align: left !important;">${obj.collateral || ""}</td>
                            <td style="text-align: left !important;">${obj.usedToLate === true ? "បាទ" : "ទេ"}</td>
                            <td style="text-align: left !important;">${obj.reasonLate || ""}</td>
                            <td style="text-align: left !important;">${obj.applyLoanToCompanyCurrentMonth || ""}</td>
                            <td style="text-align: left !important;">${obj.usedToWriteOff === true ? "បាទ" : "ទេ"}</td>
                            <td style="text-align: left !important;">${obj.likeCurrentCompany === true ? "បាទ" : "ទេ"}</td>
                            <td style="text-align: left !important;">${obj.wantOtherLoan === true ? "បាទ" : "ទេ"}</td>
                            <td style="text-align: left !important;">${obj.serviceDoc && obj.serviceDoc.name || ""}</td>
                            <td style="text-align: left !important;">${obj.reasonChooseOurService || ""}</td>
                            <td style="text-align: left !important;">${obj.isAgreeWithConsultant === true ? "បាទ" : "ទេ"}</td>
                    </tr>
            
            `;
                    i++;

                }
            })

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
