import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_Holiday, Sb_HolidayReact, Sb_HolidayAudit} from "../../imports/collections/sb_holiday";
import {Branch} from "../../imports/collections/branch";
import {Sb_OpenAccount} from "../../imports/collections/sb_openAccount";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchHoliday({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{description: {$regex: reg, $options: 'mi'}},
                        {year: {$regex: reg, $options: 'mi'}},
                        {dateList: {$regex: reg, $options: 'mi'}},
                    ];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_Holiday.aggregate([
                {
                    $match: selector
                }
                ,
                {
                    $sort: sortObj
                },

                {
                    $limit: options.limit
                },
                {
                    $skip: options.skip
                }
            ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_Holiday.find(selector).count();
            return data;
        }
    },
    sb_insertHoliday(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_Holiday.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_HolidayReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateHoliday(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Holiday.findOne({_id: id});
                let isUpdated = Sb_Holiday.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_HolidayReact, id, Sb_HolidayAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeHoliday(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_Holiday.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_HolidayReact, doc._id, Sb_HolidayAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_findHolidayById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_Holiday.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

})

//Unique

Sb_Holiday._ensureIndex({
    year: 1,
    branchId: 1
}, {unique: 1, name: "Sb_HolidayUnique"});