import {Mongo} from "meteor/mongo";

export const Sb_Staff = new Mongo.Collection("sb_staff");
Sb_Staff.schema = new SimpleSchema({
    code: {
        type: String,
        optional: true,
    },
    name: {
        type: String
    },
    startDate: {
        type: String,
        optional: true

    },

    address: {
        type: String,
        optional: true
    },
    facebookId: {
        type: String,
        optional: true
    },
    facebookName: {
        type: String,
        optional: true
    },
    dob: {
        type: String,
        optional: true
    },
    email: {
        type: String,
        optional: true
    },
    phoneNumber: {
        type: String,
        optional: true
    },
    position: {
        type: String,
        optional: true
    },
    description: {
        type: String,
        optional: true
    },
    gender: {
        type: String,
    },
    planAmountInYear: {
        type: Number,
        defaultValue: 0,
        optional: true
    },
    status: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Staff.attachSchema(Sb_Staff.schema);


