import {Mongo} from "meteor/mongo";

export const Acc_ClosingEntry = new Mongo.Collection("acc_closingEntry");
Acc_ClosingEntry.schema = new SimpleSchema({
    closeDate: {
        type: String,
    },
    month: {
        type: String,
        optional: true
    },
    year: {
        type: String,
        optional: true
    },
    exchangeId: {
        type: String,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    isDaily: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Acc_ClosingEntry.attachSchema(Acc_ClosingEntry.schema);


