import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {
    Sb_ReverseAccount,
    Sb_ReverseAccountReact,
    Sb_ReverseAccountAudit
} from "../../imports/collections/sb_reverseAccount";
import {Branch} from "../../imports/collections/branch";
import {Sb_OpenAccount, Sb_OpenAccountAudit, Sb_OpenAccountReact} from "../../imports/collections/sb_openAccount";
import {Company} from "../../imports/collections/company";
import {Acc_ChartAccount} from "../../imports/collections/acc_chartAccount";
import {Sb_ClientSaving} from "../../imports/collections/sb_clientSaving";
import numeral from "numeral";
import {Acc_Journal} from "../../imports/collections/acc_journal";
import {Sb_DepositAudit, Sb_DepositReact} from "../../imports/collections/sb_deposit";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchReverseAccount({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{reverseDate: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_ReverseAccount.aggregate([
                    {
                        $match: selector
                    }
                    ,

                    {
                        $lookup: {
                            from: "sb_openAccount",
                            localField: "openAccountId",
                            foreignField: "_id",
                            as: "openAccountDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$openAccountDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "sb_clientSaving",
                            localField: "openAccountDoc.clientId",
                            foreignField: "_id",
                            as: "clientDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_ReverseAccount.find(selector).count();
            return data;
        }
    },
    sb_insertReverseAccount(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let data = doc;
                let id = Sb_ReverseAccount.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_ReverseAccountReact, id);
                    let openAccountDoc = Sb_OpenAccount.findOne({_id: data.openAccountId});
                    Sb_OpenAccount.update({_id: doc.openAccountId}, {$set: {status: "Approved"}});
                    GlobalFn.collectionReact(Sb_OpenAccountReact, data.openAccountId, Sb_OpenAccountAudit, openAccountDoc, "Update");

                    let companyDoc = Company.findOne({});
                    if (companyDoc.isIntegrateLoanAndAccount === true) {
                        if (true) {
                            let cashAcc = Acc_ChartAccount.findOne({mapToAccount: "005"});
                            let savingAcc = Acc_ChartAccount.findOne({mapToAccount: "027"});
                            let cutSavingAcc = Acc_ChartAccount.findOne({mapToAccount: "030"});
                            let penaltyAcc = Acc_ChartAccount.findOne({mapToAccount: "015"});

                            let cusDoc = Sb_ClientSaving.findOne({_id: openAccountDoc.clientId});

                            let journalDoc = {};
                            journalDoc.journalDate = data.reverseDate;
                            journalDoc.currencyId = data.currencyId;
                            journalDoc.memo = cusDoc.name + "ទាញយកគណនីមកវិញ";
                            journalDoc.branchId = data.branchId;
                            journalDoc.closingEntryId = id;
                            journalDoc.status = "ReverseAccount";
                            journalDoc.refId = id;
                            journalDoc.total = numeral(GlobalFn.formatCurrency(data.penalty + data.balance, data.currencyId)).value();

                            let transaction = [];
                            if (data.penalty > 0) {
                                transaction.push({
                                    account: cashAcc._id,
                                    dr: data.penalty,
                                    cr: 0,
                                    drcr: data.penalty
                                });

                                transaction.push({
                                    account: penaltyAcc._id,
                                    dr: 0,
                                    cr: data.penalty,
                                    drcr: -data.penalty
                                });

                            }

                            if (data.balance > 0) {
                                transaction.push({
                                    account: cutSavingAcc._id,
                                    dr: data.balance,
                                    cr: 0,
                                    drcr: data.balance
                                });

                                transaction.push({
                                    account: savingAcc._id,
                                    dr: 0,
                                    cr: data.balance,
                                    drcr: -data.balance
                                });
                            }

                            journalDoc.transaction = transaction;
                            Meteor.call("acc_insertJournal", journalDoc, accessToken);

                        }
                    }

                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_removeReverseAccount(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_ReverseAccount.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_ReverseAccountReact, doc._id, Sb_ReverseAccountAudit, doc, "Remove");
                    Sb_OpenAccount.update({_id: doc.openAccountId}, {$set: {status: "Cut"}});

                    //Integrate to Account
                    let companyDoc = Company.findOne({});
                    if (companyDoc.isIntegrateSavingAndAccount === true) {
                        Acc_Journal.remove({refId: doc._id, status: "ReverseAccount"});
                    }

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
})

//Unique

Sb_ReverseAccount._ensureIndex({
    openAccountId: 1,
    reverseDate: 1,
    penalty: 1,
    balance: 1,
    currencyId: 1,
    branchId: 1
}, {unique: 1, name: "Sb_ReverseAccountUnique"});