import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {
    Acc_ClosingEntry,
    Acc_ClosingEntryReact,
    Acc_ClosingEntryAudit
} from "../../imports/collections/acc_closingEntry";
import {Acc_ChartAccountBalance} from "../../imports/collections/acc_chartAccountBalance";

import {Acc_Journal} from "../../imports/collections/acc_journal";
import {Branch} from "../../imports/collections/branch";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchClosingEntry({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [
                        {closeDate: {$regex: reg, $options: 'mi'}},
                        {month: {$regex: reg, $options: 'mi'}},
                        {year: {$regex: reg, $options: 'mi'}}
                    ];
                }
            }
            selector.branchId = branchId;


            data.content = Acc_ClosingEntry.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Acc_ClosingEntry.find(selector).count();
            return data;
        }
    },
    acc_insertClosingEntry(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                doc.month = doc.closeDate.split("-")[1];
                doc.year = doc.closeDate.split("-")[0];
                doc.isDaily = !(moment(doc.closeDate, "YYYY-MM-DD").add(1, "day").format("DD") === "01");
                let id = Acc_ClosingEntry.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Acc_ClosingEntryReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    acc_updateClosingEntry(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_ClosingEntry.findOne({_id: id});
                doc.month = doc.closeDate.split("-")[1];
                doc.year = doc.closeDate.split("-")[0];

                let isUpdated = Acc_ClosingEntry.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_ClosingEntryReact, id, Acc_ClosingEntryAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_removeClosingEntry(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Acc_ClosingEntry.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Acc_ClosingEntryReact, doc._id, Acc_ClosingEntryAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    insertChartAccountBalance: function (selector, branchId, selectorLastBalance, lastDate, dateSelect, closingEntryId, selectorChartAccount) {
        let data = [];
        let dataRaw = [];
        let dataLast;

        let result = Acc_Journal.aggregate([
            {$match: selector},
            {$unwind: "$transaction"},

            {
                $lookup:
                    {
                        from: "acc_chartAccount",
                        localField: "transaction.account",
                        foreignField: "_id",
                        as: "accountDoc"
                    }
            },
            {
                $unwind: {
                    path: "$accountDoc",
                    preserveNullAndEmptyArrays: true
                }
            },
            {$match: selectorChartAccount},
            {
                $group: {
                    _id: {
                        account: "$accountDoc._id",
                        code: "$accountDoc.code",
                        currencyId: "$currencyId"
                    },
                    result: {$sum: "$transaction.drcr"}
                }
            },
            {$sort: {"_id.code": 1}}
        ]);
        let curMonth = dateSelect.split("-")[1];
        result.forEach(function (obj) {
            data.push({
                chartAccountId: obj._id.account,
                value: obj.result,
                closeDate: dateSelect,
                currencyId: obj._id.currencyId,
                branchId: branchId
            })
        });

        if (lastDate !== null) {
            if (curMonth === "12") {
                dataLast = Acc_ChartAccountBalance.aggregate([
                    {
                        $match: selectorLastBalance

                    },
                    {
                        $lookup:
                            {
                                from: "acc_chartAccount",
                                localField: "chartAccountId",
                                foreignField: "_id",
                                as: "accountDoc"
                            }
                    }, {
                        $match: {
                            "accountDoc.accountTypeId": {
                                $gte: "01",
                                $lte: "49"
                            }
                        }
                    }

                ]);
            } else {
                dataLast = Acc_ChartAccountBalance.find(selectorLastBalance).fetch();
            }
            if (dataLast !== null) {
                dataLast.forEach(function (lastBal) {
                    data.push({
                        chartAccountId: lastBal.chartAccountId,
                        value: lastBal.value,
                        closeDate: dateSelect,
                        currencyId: lastBal.currencyId,
                        branchId: lastBal.branchId
                    })
                })
            }
        }
        data.reduce(function (key, val) {
            if (!key[val.chartAccountId + val.currencyId]) {
                key[val.chartAccountId + val.currencyId] = {
                    chartAccountId: val.chartAccountId,
                    value: val.value,
                    closeDate: val.closeDate,
                    currencyId: val.currencyId,
                    branchId: val.branchId
                };
                dataRaw.push(key[val.chartAccountId + val.currencyId]);
            } else {
                key[val.chartAccountId + val.currencyId].value += val.value;
            }
            return key;
        }, {});
        //console.log(dataRaw);
        dataRaw.forEach(function (ob) {
            Acc_ChartAccountBalance.insert({
                chartAccountId: ob.chartAccountId,
                value: ob.value,
                closeDate: ob.closeDate,
                currencyId: ob.currencyId,
                branchId: ob.branchId,
                closingEntryId: closingEntryId,
                year: ob.closeDate.split("-")[0],
                month: ob.closeDate.split("-")[1]
            })
        });
        return "Success";
    },
})

//Unique

Acc_ClosingEntry._ensureIndex({
    month: 1,
    year: 1,
    closeDate: 1,
    exchangeId: 1,
    branchId: 1
}, {unique: 1, name: "Acc_ClosingEntryUnique"});