import {Mongo} from "meteor/mongo";

export const Sb_Group = new Mongo.Collection("sb_group");
Sb_Group.schema = new SimpleSchema({
    name: {
        type: String,
    },
    address: {
        type: String,
        optional: true
    },
    order: {
        type: Number,
        optional: true,
        defaultValue: 0
    },
    date: {
        type: String,
        optional: true
    },
    phoneNumber: {
        type: String,
        optional: true
    },

    note: {
        type: String,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Group.attachSchema(Sb_Group.schema);


