import {Mongo} from "meteor/mongo";

export const Sb_CoPlan = new Mongo.Collection("sb_coPlan");
Sb_CoPlan.schema = new SimpleSchema({
    year: {
        type: String,
        optional: true
    },
    coId: {
        type: String,
    },
    monthly: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    yearly: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    semester: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    quarter: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    note: {
        type: String,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_CoPlan.attachSchema(Sb_CoPlan.schema);


