import {Meteor} from 'meteor/meteor';
import {Sb_ClientSavingReact} from "../../imports/collections/sb_clientSaving";

if (Meteor.isServer) {
    Meteor.publish('sb_clientSavingReact', function () {
        if (this.userId) {
            return Sb_ClientSavingReact.find({});
        }
        return this.ready();

    });
}
