import {Mongo} from "meteor/mongo";

export const Sb_BusinessPartner = new Mongo.Collection("sb_businessPartner");
Sb_BusinessPartner.schema = new SimpleSchema({
    name: {
        type: String,
        label: "Name"
    },
    phoneNumber: {
        type: String,
        label: "Name"
    },
    company: {
        type: String,
        label: "Name"
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_BusinessPartner.attachSchema(Sb_BusinessPartner.schema);


