import GlobalFn from "../../imports/libs/globalFn"
import {Meteor} from "meteor/meteor";
import {Sb_Disbursement} from "../../imports/collections/sb_disbursement";
import {Sb_LoanConfig} from "../../imports/collections/sb_loanConfig";
import numeral from "numeral"
import {Sb_RepaymentWithBank} from "../../imports/collections/sb_repaymentWithBank";
import math from "mathjs";
import {Constants} from "../../imports/libs/constant";

const API = JsonRoutes;
const sendResult = API.sendResult;
var jwt = require('jsonwebtoken');

const secret = Meteor.settings.private.secret;

API.add('post', GlobalFn.Namespace1('/getPaymentBalance/fetch'), (req, res, next) => {
    const {api_key, merchant_id} = req.headers;
    const {loan_account} = req.body;

    let isVerify = Meteor.users.findOne({username: merchant_id, "profile.approved": true, apiKey: api_key}); // if token failed we decline all process
    if (!isVerify || !merchant_id || !api_key) {
        sendResult(res, {
            data: {
                code: 3,
                message: "Token or Merchant Id or Api Key not Correct",
            }
        })
        return false;
    }
    return new Promise((resolve, reject) => {
        let loanDoc = Sb_Disbursement.findOne({loanAcc: loan_account, status: "Active"});
        if (loanDoc) {
            Meteor.call("getAmountNeedToPaid", loanDoc._id, moment().format("YYYY-MM-DD"), (err, result) => {
                if (!err) {
                    let returnDoc = {};
                    returnDoc.client_name = result.clientDoc.name;
                    returnDoc.loan_account = result.disbursementDoc.loanAcc;
                    returnDoc.currency = result.currencyId;

                    let penalty = 0;
                    let repaymentDate = moment().format("YYYY-MM-DD");
                    let time = moment().format("hh:mm:ss");
                    let dayLate = moment(repaymentDate, "YYYY-MM-DD").diff(result.date, "days");
                    dayLate = dayLate > 0 ? dayLate : 0;
                    if (result.productDoc && result.productDoc.isSpecialLoan === true) {
                        if (result && result.penaltyDoc && dayLate > result.penaltyDoc.graceDay) {
                            penalty = (result && result.disbursementDoc && result.disbursementDoc.specialPenalty || 0) * (Math.ceil(dayLate / (result.dayRange || 1)));
                        } else {
                            penalty = 0;
                        }
                    } else {
                        if (result && result.penaltyDoc && dayLate > result.penaltyDoc.graceDay) {
                            let penaltyPerday;
                            if (result.currencyId === "USD") {
                                penaltyPerday = result.penaltyDoc.amountUSD;
                            } else if (result.currencyId === "KHR") {
                                penaltyPerday = result.penaltyDoc.amountKHR;
                            } else if (result.currencyId === "THB") {
                                penaltyPerday = result.penaltyDoc.amountTHB;
                            }
                            let configDoc = Sb_LoanConfig.findOne({branchId: result.disbursementDoc.branchId});
                            let isPenaltyMinusGraceDay = configDoc && configDoc.isPenaltyMinusGraceDay || false;
                            if (isPenaltyMinusGraceDay === true) {
                                if (result.penaltyDoc.type === "P") {
                                    penalty = GlobalFn.formatCurrencyLast((dayLate - result.penaltyDoc.graceDay) * (penaltyPerday * result.principleUnpaid / 100), result.currencyId);

                                } else {
                                    penalty = GlobalFn.formatCurrencyLast((dayLate - result.penaltyDoc.graceDay) * penaltyPerday, result.currencyId);

                                }
                            } else {
                                if (result.penaltyDoc.type === "P") {
                                    penalty = GlobalFn.formatCurrencyLast((dayLate) * (penaltyPerday * result.principleUnpaid / 100), result.currencyId);
                                } else {
                                    penalty = GlobalFn.formatCurrencyLast((dayLate) * penaltyPerday, result.currencyId);

                                }
                            }
                        } else {
                            penalty = 0;
                        }
                    }

                    penalty = numeral(penalty).value();
                    returnDoc.total_amount = numeral(GlobalFn.formatCurrencyLast(result.balanceUnpaid + penalty, result.currencyId)).value();
                    returnDoc.repayment_date = repaymentDate;
                    returnDoc.time = time;

                    let paymentWithBankDoc = {};
                    paymentWithBankDoc.clientId = result.clientDoc._id;
                    paymentWithBankDoc.disbursementId = result.disbursementDoc._id;
                    paymentWithBankDoc.currencyId = result.currencyId;
                    paymentWithBankDoc.repaymentDate = repaymentDate;
                    paymentWithBankDoc.paid = result.balanceUnpaid;
                    paymentWithBankDoc.paymentTypeId = "";
                    paymentWithBankDoc.principlePaid = result.principleUnpaid || 0;
                    paymentWithBankDoc.interestPaid = result.interestUnpaid || 0;
                    paymentWithBankDoc.operationFeePaid = result.operationFeeUnpaid || 0;
                    paymentWithBankDoc.penaltyPaid = penalty || 0;
                    paymentWithBankDoc.totalPaid = returnDoc.total_amount || 0;
                    paymentWithBankDoc.branchId = result.disbursementDoc.branchId;
                    paymentWithBankDoc.type = "Repayment";
                    paymentWithBankDoc.dayLate = dayLate;
                    paymentWithBankDoc.time = returnDoc.time;
                    paymentWithBankDoc.createdUser = isVerify._id;
                    paymentWithBankDoc.createdAt = moment().toDate();

                    let repaymentId = Sb_RepaymentWithBank.insert(paymentWithBankDoc);
                    let token = GlobalFn.generateToken(returnDoc.loan_account, returnDoc.total_amount, returnDoc.currency, returnDoc.repayment_date, returnDoc.time, repaymentId, api_key, merchant_id);
                    Sb_RepaymentWithBank.update({_id: repaymentId}, {$set: {token: token}});
                    returnDoc.tran_id = repaymentId;
                    returnDoc.token = token;
                    resolve(returnDoc);
                } else {
                    reject({code: 2, message: err.message});
                }
            });
        } else {
            reject({code: 1, message: "Loan Account Number not Correct"});
        }
    }).then((r) => {
        sendResult(res, {
            data: {
                code: 0,
                data: r
            }
        })
    }).catch((er) => {
        sendResult(res, {
            data: {
                code: er.code,
                message: er.message
            }
        })
    })
})


API.add('post', GlobalFn.Namespace1('/repayment/add'), (req, res, next) => {
    const {token} = req.headers;
    const {loan_account, currency, bank_name, bank_ref, paid_by, paid_date, description, total_amount} = req.body;

    let repaymentWithBankDoc = Sb_RepaymentWithBank.findOne({token: token, status: false});
    if (!repaymentWithBankDoc) {
        sendResult(res, {
            data: {
                code: 3,
                message: "Token or Merchant Id or Api Key not Correct",
            }
        })
        return false;
    }
    console.log(repaymentWithBankDoc);
    return new Promise((resolve, reject) => {
        let loanRepaymentDoc = {
            disbursementId: repaymentWithBankDoc.disbursementId,
            currencyId: repaymentWithBankDoc.currencyId,
            repaymentDate: repaymentWithBankDoc.repaymentDate,
            remain: 0,
            penalty: repaymentWithBankDoc.penaltyPaid,
            penaltyPaid: repaymentWithBankDoc.penaltyPaid,
            lawFee: 0,
            lawFeePaid: 0,
            roFeePaid: 0,
            bpFeePaid: 0,
            voucher: "",
            note: "",
            type: "Repayment",
            dayLate: repaymentWithBankDoc.dayLate,
            savingAmount: 0,
            paidFromSaving: 0,
            interestReminderCharge: 0,
            interestReminderChargePaid: 0,
            interestPaid: repaymentWithBankDoc.interestPaid || 0,
            operationFeePaid: repaymentWithBankDoc.operationFeePaid || 0,
            clientId: repaymentWithBankDoc.clientId,
            paymentTypeId: repaymentWithBankDoc.paymentTypeId || "",
            paid: total_amount - repaymentWithBankDoc.penaltyPaid,
            totalPaid: total_amount,
            branchId: repaymentWithBankDoc.branchId,
            createdUser: repaymentWithBankDoc.createdUser,
            createdAt: moment().toDate()
        };

        Meteor.call("sb_insertRepayment", loanRepaymentDoc, secret, (err, result) => {
            if (!err) {
                resolve("Success");
                Sb_RepaymentWithBank.update({_id: repaymentWithBankDoc._id}, {$set: {status: true}});

            } else {
                reject({code: 2, message: err.message});
            }

        })
    }).then((r) => {
        sendResult(res, {
            data: {
                code: 0,
                data: r
            }
        })
    }).catch((er) => {
        sendResult(res, {
            data: {
                code: er.code,
                message: er
            }
        })
    })


})