import {Meteor} from 'meteor/meteor';
import {Sb_SurveyReact} from "../../imports/collections/sb_survey";

if (Meteor.isServer) {
    Meteor.publish('sb_surveyReact', function () {
        if (this.userId) {
            return Sb_SurveyReact.find({});
        }
        return this.ready();

    });
}
