import {Meteor} from 'meteor/meteor';
import {Sb_WithdrawReact} from "../../imports/collections/sb_withdraw";

if (Meteor.isServer) {
    Meteor.publish('sb_withdrawReact', function () {
        if (this.userId) {
            return Sb_WithdrawReact.find({});
        }
        return this.ready();

    });
}
