import {Meteor} from 'meteor/meteor';
import {Sb_SavingProductReact} from "../../imports/collections/sb_savingProduct";

if (Meteor.isServer) {
    Meteor.publish('sb_savingProductReact', function () {
        if (this.userId) {
            return Sb_SavingProductReact.find({});
        }
        return this.ready();

    });
}
