import {Meteor} from 'meteor/meteor';
import {Sb_HolidayReact} from "../../imports/collections/sb_holiday";

if (Meteor.isServer) {
    Meteor.publish('sb_holidayReact', function () {
        if (this.userId) {
            return Sb_HolidayReact.find({});
        }
        return this.ready();

    });
}
