import {Meteor} from 'meteor/meteor';
import {Sb_RepaymentReact} from "../../imports/collections/sb_repayment";

if (Meteor.isServer) {
    Meteor.publish('sb_repaymentReact', function () {
        if (this.userId) {
            return Sb_RepaymentReact.find({});
        }
        return this.ready();

    });
}
