import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Acc_Journal, Acc_JournalReact, Acc_JournalAudit} from "../../imports/collections/acc_journal";
import {
    Acc_ClosingEntry,
    Acc_ClosingEntryAudit,
    Acc_ClosingEntryReact
} from "../../imports/collections/acc_closingEntry";
import {Branch} from "../../imports/collections/branch";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchJournal({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [
                        {_id: {$regex: reg, $options: 'mi'}},
                        {name: {$regex: reg, $options: 'mi'}},
                        {currencyId: {$regex: reg, $options: 'mi'}},
                        {memo: {$regex: reg, $options: 'mi'}},
                        {journalDateName: {$regex: reg, $options: 'mi'}},
                        {status: {$regex: reg, $options: 'mi'}},
                        {total: {$regex: reg, $options: 'mi'}},
                        {voucherId: {$regex: reg, $options: 'mi'}}
                    ];
                }
            }
            selector.branchId = branchId;


            data.content = Acc_Journal.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "createdUser",
                            foreignField: "_id",
                            as: "createdUserDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$createdUserDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "updatedUser",
                            foreignField: "_id",
                            as: "updatedUserDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$updatedUserDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Acc_Journal.find(selector).count();
            return data;
        }
    },
    acc_insertJournal(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let branchDoc = Branch.findOne({_id: doc.branchId});
                if (doc.voucherId && doc.voucherId !== "") {
                    doc.voucherId = branchDoc.code + doc.journalDate.split("-")[0] + pad(doc.voucherId, 6);
                } else {
                    doc.voucherId = Meteor.call("autoIncreseVoucher", doc.branchId, doc.journalDate, doc.currencyId, accessToken);
                    doc.voucherId = branchDoc.code + doc.journalDate.split("-")[0] + pad(parseInt(doc.voucherId) + 1, 6);
                }
                doc.transaction.map(function (obj) {
                    return obj.drcr = parseFloat(obj.dr) - parseFloat(obj.cr);
                });

                let id = Acc_Journal.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Acc_JournalReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    acc_insertJournalPR(doc, type, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let branchDoc = Branch.findOne({_id: doc.branchId});
                if (doc.voucherId && doc.voucherId !== "") {
                    doc.voucherId = branchDoc.code + doc.journalDate.split("-")[0] + pad(doc.voucherId, 6);
                } else {
                    doc.voucherId = Meteor.call("autoIncreseVoucher", doc.branchId, doc.journalDate, doc.currencyId, accessToken);
                    doc.voucherId = branchDoc.code + doc.journalDate.split("-")[0] + pad(parseInt(doc.voucherId) + 1, 6);
                }


                let totalMethod = 0;
                let newTransaction = [];
                if (type === "Receive") {
                    doc.transaction.map(function (obj) {
                        totalMethod += parseFloat(obj.amount);
                        newTransaction.push({
                            account: obj.account,
                            dr: 0,
                            cr: parseFloat(obj.amount),
                            drcr: -parseFloat(obj.amount)
                        })
                    });

                    newTransaction.unshift({
                        account: doc.method,
                        dr: totalMethod,
                        cr: 0,
                        drcr: totalMethod

                    })
                } else {
                    doc.transaction.map(function (obj) {
                        totalMethod += parseFloat(obj.amount);
                        newTransaction.push({
                            account: obj.account,
                            dr: parseFloat(obj.amount),
                            cr: 0,
                            drcr: parseFloat(obj.amount)
                        })
                    });


                    newTransaction.push({
                        account: doc.method,
                        dr: 0,
                        cr: totalMethod,
                        drcr: -totalMethod
                    })
                }
                doc.transaction = newTransaction;
                let id = Acc_Journal.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Acc_JournalReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    acc_updateJournal(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Acc_Journal.findOne({_id: id});
                let branchDoc = Branch.findOne({_id: doc.branchId});
                doc.voucherId = branchDoc.code + doc.journalDate.split("-")[0] + pad(doc.voucherId, 6);
                doc.transaction.map(function (obj) {
                    return obj.drcr = parseFloat(obj.dr) - parseFloat(obj.cr);
                });


                let isUpdated = Acc_Journal.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Acc_JournalReact, id, Acc_JournalAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_removeJournal(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Acc_Journal.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Acc_JournalReact, doc._id, Acc_JournalAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findJournal(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Acc_Journal.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    acc_findJournalById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Acc_Journal.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    acc_fetchJournalOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Acc_Journal.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    autoIncreseVoucher(branchId, journalDate, currencyId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let spJournalDate = journalDate.split("-");
            let startYear = spJournalDate[0] + "-01-01";
            let endYear = spJournalDate[0] + "-12-31";
            let journalDoc = Acc_Journal.findOne({
                branchId: branchId,
                journalDate: {$gte: startYear, $lte: endYear},
                currencyId: currencyId
            }, {sort: {journalDate: -1, voucherId: -1}});

            if (journalDoc == undefined) {
                return 0;
            }
            return journalDoc.voucherId.length > 9 ? journalDoc.voucherId.substr(8, 20) : journalDoc.voucherId;
        }
    },
    queryLastClosingEntry(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            return Acc_ClosingEntry.findOne({branchId: branchId}, {sort: {closeDate: -1}});
        }
    },
})

//Unique

Acc_Journal._ensureIndex({
    journalDate: 1,
    voucherId: 1,
    currencyId: 1,
    memo: 1,
    total: 1,
    transaction: 1,
    closingEntryId: 1,
    refId: 1,
    branchId: 1
}, {unique: 1, name: "Acc_JournalUnique"});


function pad(number, length) {
    let str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
