import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_ClientType, Sb_ClientTypeReact, Sb_ClientTypeAudit} from "../../imports/collections/sb_clientType";
import {Branch} from "../../imports/collections/branch";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchClientType({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}},
                        {commission: {$regex: reg, $options: 'mi'}},
                    ];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_ClientType.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_ClientType.find(selector).count();
            return data;
        }
    },
    sb_insertClientType(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_ClientType.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_ClientTypeReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateClientType(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_ClientType.findOne({_id: id});
                let isUpdated = Sb_ClientType.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_ClientTypeReact, id, Sb_ClientTypeAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeClientType(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_ClientType.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_ClientTypeReact, doc._id, Sb_ClientTypeAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findClientType(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_ClientType.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findClientTypeById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_ClientType.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_fetchClientTypeOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_ClientType.find(selector).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + obj.commission + "%",
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})

//Unique

Sb_ClientType._ensureIndex({
    name: 1,
    branchId: 1
}, {unique: 1, name: "Sb_ClientTypeUnique"});