import {Meteor} from 'meteor/meteor';
import {Sb_PenaltyClosingReact} from "../../imports/collections/sb_penaltyClosing";

if (Meteor.isServer) {
    Meteor.publish('sb_penaltyClosingReact', function () {
        if (this.userId) {
            return Sb_PenaltyClosingReact.find({});
        }
        return this.ready();

    });
}
