import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_CloseSaving, Sb_CloseSavingReact, Sb_CloseSavingAudit} from "../../imports/collections/sb_closeSaving";
import {Acc_ClosingEntry} from "../../imports/collections/acc_closingEntry";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchCloseSaving({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_CloseSaving.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_CloseSaving.find(selector).count();
            return data;
        }
    },
    sb_insertCloseSaving(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_CloseSaving.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_CloseSavingReact, id);
                    Meteor.call("sb_calculateInterestAndWithdrawRunDaily", doc.closeDate, accessToken, doc.branchId, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateCloseSaving(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_CloseSaving.findOne({_id: id});
                let isUpdated = Sb_CloseSaving.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_CloseSavingReact, id, Sb_CloseSavingAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeCloseSaving(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_CloseSaving.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_CloseSavingReact, doc._id, Sb_CloseSavingAudit, doc, "Remove");
                    Meteor.call("sb_removeCalculateInterestAndWithdrawRunDaily", doc.closeDate, accessToken, doc.branchId, doc._id);

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findCloseSaving(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_CloseSaving.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findCloseSavingById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_CloseSaving.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_fetchCloseSavingOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_CloseSaving.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    queryLastSavingClosingEntry(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            return Sb_CloseSaving.findOne({branchId: branchId}, {sort: {closeDate: -1}});
        }
    },
})

//Unique

Sb_CloseSaving._ensureIndex({
    closeDate: 1,
    branchId: 1
}, {unique: 1, name: "Sb_CloseSavingUnique"});