import {Meteor} from 'meteor/meteor';
import {Sb_GuarantorReact} from "../../imports/collections/sb_guarantor";

if (Meteor.isServer) {
    Meteor.publish('sb_guarantorReact', function () {
        if (this.userId) {
            return Sb_GuarantorReact.find({});
        }
        return this.ready();

    });
}
