import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Sb_RepaymentSchedule} from "../../../imports/collections/sb_repaymentSchedule";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import numeral from "numeral"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchRepaymentForecastReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let selectLoanActive = {};
            let selectorDisbursement = {};

            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
                selectLoanActive.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
                selectLoanActive.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selectorDisbursement["disbursementDoc.coId"] = params.staffId;
            }

            selector.date = {$gte: params.dateRange[0], $lte: params.dateRange[1]};

            selectLoanActive.$or = [
                {status: "Active"},
                {
                    payOffDate: {
                        $gt: params.dateRange[1],
                    }
                },
                {
                    writeOffDate: {
                        $gt: params.dateRange[1],
                    }
                }
            ];
            let loanActiveListId = Sb_Disbursement.find(selectLoanActive).fetch().map((obj) => obj._id);

            selector.loanId = {$in: loanActiveListId || []};


            let companyDoc = Company.findOne({});

            let dataList = Sb_RepaymentSchedule.aggregate([

                    {
                        $match: selector
                    },
                    {
                        $sort: {
                            date: 1
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "sb_client",
                                localField: "clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "sb_disbursement",
                                localField: "loanId",
                                foreignField: "_id",
                                as: "disbursementDoc"
                            }
                    },

                    {
                        $unwind: {
                            path: "$disbursementDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: selectorDisbursement
                    },
                    {
                        $lookup:
                            {
                                from: "sb_loanProduct",
                                localField: "disbursementDoc.productId",
                                foreignField: "_id",
                                as: "productDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$productDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }, {
                        $lookup:
                            {
                                from: "sb_staff",
                                localField: "disbursementDoc.coId",
                                foreignField: "_id",
                                as: "creditOfficerDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$creditOfficerDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.currencyHeader = companyDoc.baseCurrency;
            let dataHtml = "";

            let totalPrincipleUSD = 0;
            let totalInterestUSD = 0;
            let totalOperationFeeUSD = 0;
            let totalUSD = 0;


            let totalPrincipleTHB = 0;
            let totalInterestTHB = 0;
            let totalOperationFeeTHB = 0;
            let totalTHB = 0;


            let totalPrincipleKHR = 0;
            let totalInterestKHR = 0;
            let totalOperationFeeKHR = 0;
            let totalKHR = 0;

            let totalPrinciple = 0;
            let totalInterest = 0;
            let totalOperationFee = 0;
            let total = 0;
            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";

            if (dataList.length > 0) {
                let i = 1;
                dataList.forEach((obj) => {
                    dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.disbursementDoc.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                            <td style="text-align: left !important;">${obj.currencyId}</td>
                            <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.rateType || ""}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.disbursementDoc.loanAmount, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.disbursementDoc.projectInterest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatDate(obj.date)}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.address}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.principle, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.interest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;display: ${displayFee}">${GlobalFn.formatCurrencyLast(obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                            <td style="text-align: left !important;">${GlobalFn.formatCurrencyLast(obj.principle + obj.interest + obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)}</td>
                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {
                        totalPrincipleUSD += numeral(GlobalFn.formatCurrencyLast(obj.principle, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestUSD += numeral(GlobalFn.formatCurrencyLast(obj.interest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeUSD += numeral(GlobalFn.formatCurrencyLast(obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalUSD += numeral(GlobalFn.formatCurrencyLast(obj.principle + obj.interest + obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    } else if (obj.currencyId === "KHR") {
                        totalPrincipleKHR += numeral(GlobalFn.formatCurrencyLast(obj.principle, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestKHR += numeral(GlobalFn.formatCurrencyLast(obj.interest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeKHR += numeral(GlobalFn.formatCurrencyLast(obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalKHR += numeral(GlobalFn.formatCurrencyLast(obj.principle + obj.interest + obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    } else if (obj.currencyId === "THB") {
                        totalPrincipleTHB += numeral(GlobalFn.formatCurrencyLast(obj.principle, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalInterestTHB += numeral(GlobalFn.formatCurrencyLast(obj.interest, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalOperationFeeTHB += numeral(GlobalFn.formatCurrencyLast(obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                        totalTHB += numeral(GlobalFn.formatCurrencyLast(obj.principle + obj.interest + obj.operationFee, obj.disbursementDoc.currencyId, companyDoc, branchDoc)).value();
                    }
                })
            }


            totalPrinciple = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPrincipleUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPrincipleTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPrincipleKHR, exchange);
            totalInterest = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInterestUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInterestTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInterestKHR, exchange);
            totalOperationFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalOperationFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalOperationFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalOperationFeeKHR, exchange);
            total = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalKHR, exchange);


            dataHtml += `
                <tr>
                    <td colspan="9" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestUSD, "USD")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeeUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalUSD, "USD")}</td>
                  
                </tr> 
                <tr>
                    <td colspan="9" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeeKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalKHR, "KHR")}</td>
                  
                </tr> <tr>
                    <td colspan="9" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.formatCurrency(totalPrincipleTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFeeTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalTHB, "THB")}</td>
                  
                </tr> 
                <tr>
                    <th colspan="9" style="text-align: right">${t[language]['grandTotal']}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciple, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalInterest, companyDoc.baseCurrency)}</th>
                    <th style="display: ${displayFee}">${GlobalFn.formatCurrency(totalOperationFee, companyDoc.baseCurrency)}</th>
                    <th>${GlobalFn.formatCurrency(totalPrinciple + totalInterest + totalOperationFee, companyDoc.baseCurrency)}</th>
                  
                </tr>
        `;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
