import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_TargetPlan, Sb_TargetPlanReact, Sb_TargetPlanAudit} from "../../imports/collections/sb_targetPlan"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchTargetPlan({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken, coId}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        year: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        note: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            selector.coId = coId;
            data.content = Sb_TargetPlan.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_TargetPlan.find(selector).count();
            return data;
        }
    },
    sb_insertTargetPlan(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_TargetPlan.insert(doc);
                if (id) {
                    GlobalFn.collectionReact(Sb_TargetPlanReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error("Duplicate year! Error");
            }
        }

    },
    sb_updateTargetPlan(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_TargetPlan.findOne({_id: id});
                let isUpdated = Sb_TargetPlan.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_TargetPlanReact, id, Sb_TargetPlanAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeTargetPlan(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_TargetPlan.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_TargetPlanReact, doc._id, Sb_TargetPlanAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findTargetPlanById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};

                selector._id = id;
                return Sb_TargetPlan.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findTargetPlanByIdList(idList, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = {$in: idList};
                return Sb_TargetPlan.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchTargetPlanOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }


                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {_id: q}
                    ];
                }

                return Sb_TargetPlan.find(selector, {sort: {createdAt: -1}, limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + (obj.date || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_TargetPlan._ensureIndex({
    year: 1,
    branchId: 1
}, {unique: 1, name: "Sb_TargetPlanUnique"});