import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";

import {Sb_OpenAccount} from "../../../imports/collections/sb_openAccount";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_Deposit} from "../../../imports/collections/sb_deposit";
import {Sb_Withdraw} from "../../../imports/collections/sb_withdraw";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchResultAchievementReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selectorDeposit = {};

            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selectorDeposit.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selectorDeposit.branchId = {$in: userDoc.branch || []};
            }

            if (params.staffId && params.staffId !== "") {
                selectorDeposit.staffId = params.staffId;
            }


            let companyDoc = Company.findOne();
            let exchange = Exchange.findOne({_id: params.exchangeId});


            let coefficientAmountDeposit = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$amount',
                baseCurrency: companyDoc.baseCurrency
            });
            selectorDeposit.depositDate = {
                $lte: params.dateAs,
                $gte: moment(params.dateAs, "YYYY-MM-DD").startOf("year").format("YYYY-MM-DD")
            };
            let dataList = Sb_Deposit.aggregate([
                {$match: selectorDeposit},
                {
                    $project: {
                        _id: 1,
                        staffId: 1,
                        amount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientAmountDeposit.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientAmountDeposit.KHR, coefficientAmountDeposit.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: "$staffId",
                        totalAmount: {$sum: "$amount"},
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "_id",
                        foreignField: "_id",
                        as: "staffDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$staffDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
            ], {allowDiskUse: true});

            let dataHtml = "";
            let ind = 1;
            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                            <tr>
                                <td style="text-align: left !important;">${ind}</td>
                              
                                                                <td style="text-align: left !important;">${obj.staffDoc.name || ""}</td>
                                                                <td style="text-align: left !important;">${companyDoc.baseCurrency || ""}</td>

                                                                <td style="text-align: left !important;">${obj.staffDoc.phoneNumber || ""}</td>
                                                                <td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.totalAmount, companyDoc.baseCurrency)}</td>
                                                                <td style="text-align: right !important;">${GlobalFn.formatNumber(obj.staffDoc.planAmountInYear === 0 ? 0 : ((obj.totalAmount) / (obj.staffDoc.planAmountInYear * parseInt(moment(params.dateAs).format("MM")) / 12)) * 100)}%</td>
                                                                <td style="text-align: right !important;">${GlobalFn.formatNumber(obj.staffDoc.planAmountInYear === 0 ? 0 : ((obj.totalAmount) / obj.staffDoc.planAmountInYear) * 100)}%</td>
                                                                <td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.staffDoc.planAmountInYear, companyDoc.baseCurrency)}</td>

                            </tr>
                        `;
                    ind++;

                }
            })


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
