import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_Survey, Sb_SurveyReact, Sb_SurveyAudit} from "../../imports/collections/sb_survey"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchSurvey({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1, name: 1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        phoneNumber: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        note: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        _id: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        address: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            data.content = Sb_Survey.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "sb_staff",
                            localField: "coId",
                            foreignField: "_id",
                            as: "staffDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$staffDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "createdUser",
                            foreignField: "_id",
                            as: "userCDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$userCDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup: {
                            from: "users",
                            localField: "updatedUser",
                            foreignField: "_id",
                            as: "userUDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$userUDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_Survey.find(selector).count();
            return data;
        }
    },
    sb_insertSurvey(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let surveyDoc = Sb_Survey.findOne({branchId: doc.branchId}, {sort: {order: -1}});
                doc.order = (surveyDoc && surveyDoc.order || 0) + 1;
                let id = Sb_Survey.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_SurveyReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateSurvey(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_Survey.findOne({_id: id});
                let isUpdated = Sb_Survey.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_SurveyReact, id, Sb_SurveyAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },


    sb_removeSurvey(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_Survey.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_SurveyReact, doc._id, Sb_SurveyAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findSurvey(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_Survey.find(selector, {sort: {name: 1}}).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findSurveyById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                selector._id = id;
                return Sb_Survey.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findSurveyByIdList(idList, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = {$in: idList};
                return Sb_Survey.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
})


//Unique

Sb_Survey._ensureIndex({
    name: 1,
    phoneNumber: 1,
    branchId: 1
}, {unique: 1, name: "Sb_SurveyUnique"});