import {Meteor} from 'meteor/meteor';
import {Sb_ReverseAccountReact} from "../../imports/collections/sb_reverseAccount";

if (Meteor.isServer) {
    Meteor.publish('sb_reverseAccountReact', function () {
        if (this.userId) {
            return Sb_ReverseAccountReact.find({});
        }
        return this.ready();

    });
}
