import {Mongo} from "meteor/mongo";

export const Sb_SavingSchedule = new Mongo.Collection("sb_savingSchedule");
Sb_SavingSchedule.schema = new SimpleSchema({
    savingId: {
        type: String,
        label: "Client",
    },
    clientId: {
        type: String,
        label: "Client",
    },
    withdrawId: {
        type: String,
        optional: true,
    },
    productId: {
        type: String,
        label: "Product",
    },
    installment: {
        type: Number
    },
    interest: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    date: {
        type: String,
        label: "SavingSchedule Date",
    },
    withdrawHistory: {
        type: [Object],
        blackbox: true,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_SavingSchedule.attachSchema(Sb_SavingSchedule.schema);


