import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_Journal} from "../../../imports/collections/acc_journal";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Acc_ChartAccountBalance} from "../../../imports/collections/acc_chartAccountBalance";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchTransactionDetailReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
            }


            if (params.currencyId && params.currencyId !== "") {
                selector.currencyId = params.currencyId;
            }

            selector.journalDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let exchange = Exchange.findOne({_id: params.exchangeId});
            let companyDoc = Company.findOne({});

            let coefficientDrCr = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$transaction.drcr',
                baseCurrency: companyDoc.baseCurrency
            });

            let selectorChartAccount = {};

            if (params.accountType && params.accountType.length > 0) {
                selectorChartAccount["accountDoc.accountTypeId"] = {$in: params.accountType};
            }
            if (params.account && params.account !== "" && params.account !== null && params.account !== undefined) {
                selectorChartAccount["accountDoc._id"] = params.account;
            }

            let journalList = Acc_Journal.aggregate([

                {
                    $match: selector
                },
                {
                    $project: {
                        _id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: "$transaction"
                    }

                },
                {
                    $unwind: {
                        path: "$transaction",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup:
                        {
                            from: "acc_chartAccount",
                            localField: "transaction.account",
                            foreignField: "_id",
                            as: "accountDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$accountDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $match: selectorChartAccount
                }
                ,
                {
                    $project: {
                        id: "$_id",
                        currencyId: "$currencyId",
                        journalDate: "$journalDate",
                        total: "$total",
                        voucherId: "$voucherId",
                        memo: "$memo",
                        transaction: "$transaction",
                        copyTransaction: "$copyTransaction",
                        accountDoc: "$accountDoc",
                        drcr: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientDrCr.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientDrCr.KHR, coefficientDrCr.THB]}
                            }
                        }
                    }
                }, {
                    $project: {
                        id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: 1,
                        accountDoc: 1,
                        drcr: {$cond: [{$in: ["$accountDoc.accountTypeId", ["30", "32", "34", "36", "40", "41", "50", "51"]]}, {$multiply: ["$drcr", -1]}, "$drcr"]}
                    }
                },
                {$sort: {"accountDoc.code": -1}},
                {
                    $group: {
                        _id: {
                            accountDoc: "$accountDoc"
                        },
                        data: {$addToSet: "$$ROOT"},
                        total: {$sum: "$drcr"},
                    }
                }
            ], {allowDiskUse: true})

            let dataHtml = "";

            let chartAccountParentList = Acc_ChartAccount.aggregate([

                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$_id",
                        connectFromField: "_id",
                        connectToField: "subAccountOf",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "childList"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true})

            let newParams = {};
            if (params.accountType.length > 0) {
                if (params.account && params.account !== "" && params.account!==undefined && params.account!==null) {
                    newParams._id = params.account;
                } else {
                    let chartAccountList = Acc_ChartAccount.find({accountTypeId: {$in: params.accountType}}).map((obj) => obj._id);
                    newParams._id = {$in: chartAccountList};
                }
            }
            let chartAccountList = Acc_ChartAccount.aggregate([
                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$subAccountOf",
                        connectFromField: "subAccountOf",
                        connectToField: "_id",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "parentDoc"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true});

            let rawChartAccountList = Acc_ChartAccount.aggregate([
                {
                    $match: newParams
                },
                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$subAccountOf",
                        connectFromField: "subAccountOf",
                        connectToField: "_id",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "parentDoc"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true});

            rawChartAccountList.forEach(function (chartAccDoc) {

                dataHtml += `<th colspan="6"​  style="text-align: left !important;">${GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</th>
                         
            `
                let findJournalDocByChartAccountId = function (element) {
                    if (element._id.accountDoc._id == chartAccDoc._id) {
                        return element;
                    }
                }
                let journalDoc = journalList.find(findJournalDocByChartAccountId);
                let j = 1;
                let balance = 0;
                if (journalDoc) {
                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(journalDoc.total, companyDoc.baseCurrency)}</th>
                    </tr>
                `;

                    let newJournalDocData = journalDoc.data.sort(compareASD);
                    newJournalDocData.forEach(function (subObj) {
                        balance += subObj.drcr;

                        let splitData = "split";
                        if (subObj.copyTransaction && subObj.copyTransaction.length < 3) {

                            subObj.copyTransaction.forEach(function (objTransaction) {
                                if (objTransaction.account !== chartAccDoc._id) {

                                    let findChartAccountById = function (element) {
                                        if (element._id === objTransaction.account) {
                                            return element;
                                        }
                                    }
                                    let chartAccountDoc = chartAccountList.find(findChartAccountById);
                                    splitData = chartAccountDoc.code + " : " + chartAccountDoc.name;
                                }
                            })
                        }


                        dataHtml += `<tr class="rowClick" id="${subObj._id}">
                            <td style="text-align: left !important;">${j}</td>
                            <td style="text-align: center !important;"">${GlobalFn.formatDate(subObj.journalDate)}</td>
                            <td  style="text-align: center !important;">${subObj.voucherId.substr(subObj.voucherId.length - 6, 6)}</td>
                            <td  style="text-align: left !important;">${subObj.memo}</td>
                            <td style="text-align: left !important;">${splitData}</td>
                            <td>${GlobalFn.formatCurrency(subObj.drcr, companyDoc.baseCurrency)}</td>
                            <td>${GlobalFn.formatCurrency(balance, companyDoc.baseCurrency)}</td>
                            </tr>
                    `;
                        j++;

                    })
                } else {

                    let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                    let totalParent = 0;
                    if (childListDoc && childListDoc.childList.length > 0) {
                        let childListDocElement = childListDoc.childList.map(function (obj) {
                            return obj._id;
                        })
                        let findJournalDocByParentId = function (element) {
                            if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                totalParent += element.total;
                            }
                        }
                        journalList.find(findJournalDocByParentId);
                    }
                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency)}</th>
                    </tr>
                `
                }

            })

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
let compareASD = function (a, b) {
    if (a.journalDate < b.journalDate) {
        return -1;
    } else if (a.journalDate > b.journalDate) {
        return 1;
    } else {
        return 0;
    }
}


