import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_LoanConfig, Sb_LoanConfigReact, Sb_LoanConfigAudit} from "../../imports/collections/sb_loanConfig";
import {Branch} from "../../imports/collections/branch";
import {Sb_OpenAccount} from "../../imports/collections/sb_openAccount";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchLoanConfig({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_LoanConfig.aggregate([
                {
                    $match: selector
                }
                ,
                {
                    $sort: sortObj
                },

                {
                    $limit: options.limit
                },
                {
                    $skip: options.skip
                },

                {
                    $lookup: {
                        from: "branch",
                        localField: "branchId",
                        foreignField: "_id",
                        as: "branchDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$branchDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
            ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_LoanConfig.find(selector).count();
            return data;
        }
    },
    sb_insertLoanConfig(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_LoanConfig.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_LoanConfigReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateLoanConfig(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_LoanConfig.findOne({_id: id});
                let isUpdated = Sb_LoanConfig.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_LoanConfigReact, id, Sb_LoanConfigAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeLoanConfig(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_LoanConfig.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_LoanConfigReact, doc._id, Sb_LoanConfigAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findLoanConfigByBranchId(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_LoanConfig.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findLoanConfigById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_LoanConfig.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_fetchLoanConfigOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_LoanConfig.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})

//Unique

Sb_LoanConfig._ensureIndex({
    branchId: 1
}, {unique: 1, name: "Sb_LoanConfigUnique"});