import {Meteor} from 'meteor/meteor';
import {Acc_FixedAssetReact} from "../../imports/collections/acc_fixedAsset";

if (Meteor.isServer) {
    Meteor.publish('acc_fixedAssetReact', function () {
        if (this.userId) {
            return Acc_FixedAssetReact.find({});
        }
        return this.ready();

    });
}
