import {Meteor} from 'meteor/meteor';
import {Sb_SavingConfigReact, Sb_SavingConfig} from "../../imports/collections/sb_savingConfig";

if (Meteor.isServer) {
    Meteor.publish('sb_savingConfigReact', function () {
        if (this.userId) {
            return Sb_SavingConfigReact.find({});
        }
        return this.ready();

    });
    Meteor.publish('sb_savingConfig', function () {
        if (this.userId) {
            return Sb_SavingConfig.find({});
        }
        return this.ready();

    });
}
