let makeRepayment = function (disbursementId, doc, repaymentId, cashAccChoose) {
    let repaymentScheduleList = Sb_RepaymentSchedule.find({
        loanId: disbursementId,
        isPaid: false
    }, {sort: {installment: 1}}).fetch();
    let amountPaid = doc.paid;
    let operationFeePaid = 0;
    let interestPaid = 0;
    let principlePaid = 0;
    let savingAmount = doc.savingAmount || 0;
    let disBursementDoc = Sb_Disbursement.findOne({_id: disbursementId});
    let companyDoc = Company.findOne({});


    if (repaymentScheduleList.length > 0) {
        repaymentScheduleList.forEach((obj) => {
            if (amountPaid > 0) {
                let repaymentScheduleDoc = obj;
                let paid = obj.paid || [];
                let paidDoc = {};
                paidDoc.repaymentId = repaymentId;

                paidDoc.operationFeePaid = amountPaid >= (obj.operationFeeUnpaid || 0) ? (obj.operationFeeUnpaid || 0) : amountPaid;
                amountPaid = amountPaid - paidDoc.operationFeePaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.operationFeePaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.operationFeeUnpaid = repaymentScheduleDoc.operationFeeUnpaid - paidDoc.operationFeePaid;

                paidDoc.interestPaid = amountPaid >= obj.interestUnpaid ? obj.interestUnpaid : amountPaid;
                amountPaid = amountPaid - paidDoc.interestPaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.interestPaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.interestUnpaid = repaymentScheduleDoc.interestUnpaid - paidDoc.interestPaid;

                paidDoc.principlePaid = amountPaid >= obj.principleUnpaid ? obj.principleUnpaid : amountPaid;
                amountPaid = amountPaid - paidDoc.principlePaid;

                repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.principlePaid, obj.currencyId, obj.branchId);
                repaymentScheduleDoc.principleUnpaid = repaymentScheduleDoc.principleUnpaid - paidDoc.principlePaid;

                paid.push(paidDoc);
                repaymentScheduleDoc.isPaid = !(math.round(repaymentScheduleDoc.balanceUnpaid, 2) > 0);
                repaymentScheduleDoc.paid = paid;


                //លុយរាប់ មិនកំណត់Term
                if (disBursementDoc.term === 0 && math.round(repaymentScheduleDoc.interestUnpaid) === 0 && repaymentScheduleDoc.principleUnpaid > 0) {
                    if (math.round(repaymentScheduleDoc.interestUnpaid) === 0) {
                        repaymentScheduleDoc.isPaid = true;
                    }
                    let i = obj.installment + 1;
                    let j = i - 1;
                    let setup = Branch.findOne({_id: disBursementDoc.branchId});
                    let escapeDayList = Sb_Holiday.find({}).fetch();
                    let productDoc = Sb_LoanProduct.findOne({_id: disBursementDoc.productId});
                    let paidDate = "";
                    let numDay = "";
                    let rawPaidDate = obj.repaymentDate;
                    if (productDoc.rateType === "Monthly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, j * disBursementDoc.repaidFrequency, "month");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Weekly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, 7 * j * disBursementDoc.repaidFrequency, "days");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Daily") {
                        paidDate = GlobalFn.addDay(rawPaidDate, disBursementDoc.repaidFrequency, "days");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    } else if (productDoc.rateType === "Yearly") {
                        paidDate = GlobalFn.addDay(disBursementDoc.startPaidDate, j * disBursementDoc.repaidFrequency, "year");
                        paidDate = escapeDayFn(setup, escapeDayList, paidDate);
                        numDay = moment(paidDate, "YYYY-MM-DD").diff(rawPaidDate, 'days');
                    }


                    let repaymentScheduleList = [];
                    let repaymentScheduleDocTmp = {};
                    repaymentScheduleDocTmp._id = disBursementDoc._id + i;
                    repaymentScheduleDocTmp.installment = i;
                    repaymentScheduleDocTmp.tmpRepaymentId = repaymentId;
                    repaymentScheduleDocTmp.date = paidDate;
                    repaymentScheduleDocTmp.principle = repaymentScheduleDoc.principleUnpaid;
                    repaymentScheduleDocTmp.interest = GlobalFn.roundCurrency(repaymentScheduleDoc.principleUnpaid * disBursementDoc.repaidFrequency * productDoc.rate / 100, disBursementDoc.currencyId, disBursementDoc.branchId);
                    repaymentScheduleDocTmp.amount = repaymentScheduleDocTmp.principle + repaymentScheduleDocTmp.interest;
                    repaymentScheduleDocTmp.operationFee = 0;

                    repaymentScheduleDocTmp.amount = repaymentScheduleDocTmp.amount + repaymentScheduleDocTmp.operationFee;
                    repaymentScheduleDocTmp.isPaid = false;
                    repaymentScheduleDocTmp.isAllowClosing = true;

                    repaymentScheduleDocTmp.loanId = disBursementDoc._id;
                    repaymentScheduleDocTmp.clientId = disBursementDoc.clientId;
                    repaymentScheduleDocTmp.productId = disBursementDoc.productId;
                    repaymentScheduleDocTmp.currencyId = disBursementDoc.currencyId;
                    repaymentScheduleDocTmp.branchId = disBursementDoc.branchId;

                    repaymentScheduleDocTmp.balanceUnpaid = repaymentScheduleDocTmp.amount;
                    repaymentScheduleDocTmp.principleUnpaid = repaymentScheduleDocTmp.principle;
                    repaymentScheduleDocTmp.interestUnpaid = repaymentScheduleDocTmp.interest;
                    repaymentScheduleDocTmp.operationFeeUnpaid = repaymentScheduleDocTmp.operationFee;

                    repaymentScheduleDocTmp.dayRange = numDay;


                    repaymentScheduleList.push(repaymentScheduleDocTmp);
                    Sb_RepaymentSchedule.rawCollection().insert(repaymentScheduleList);
                }
                //=============End លុយរាប់មិនកំណត់Term


                Sb_RepaymentSchedule.update({_id: obj._id}, {$set: repaymentScheduleDoc});
                Sb_Repayment.update({_id: repaymentId}, {
                    $inc: {
                        principlePaid: paidDoc.principlePaid,
                        interestPaid: paidDoc.interestPaid,
                        operationFeePaid: paidDoc.operationFeePaid
                    }
                });

                principlePaid += paidDoc.principlePaid;
                interestPaid += paidDoc.interestPaid;
                operationFeePaid += paidDoc.operationFeePaid;

            } else {
                let checkPayOff = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
                if (checkPayOff.length === 0) {
                    Sb_Disbursement.update({_id: disbursementId}, {
                        $set: {
                            status: "Pay Off",
                            payOffDate: doc.repaymentDate
                        }
                    });
                }
                return false;
            }
        })
        Sb_Disbursement.update({_id: disbursementId}, {$inc: {savingAmount: savingAmount}});


        let checkPayOff = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
        if (checkPayOff.length === 0) {
            Sb_Disbursement.update({_id: disbursementId}, {
                $set: {
                    status: "Pay Off",
                    payOffDate: doc.repaymentDate
                }
            });
        }

        //Integrate to Account
        if (companyDoc.isIntegrateLoanAndAccount === true) {

            if (doc.paid + doc.penaltyPaid > 0) {
                let cashAcc = cashAccChoose || Acc_ChartAccount.findOne({mapToAccount: "005"});
                let penaltyAcc = Acc_ChartAccount.findOne({mapToAccount: "015"});
                let principalAcc = Acc_ChartAccount.findOne({mapToAccount: "014"});
                let interestAcc = Acc_ChartAccount.findOne({mapToAccount: "017"});
                let operationFeeAcc = Acc_ChartAccount.findOne({mapToAccount: "019"});
                let savingAmountAcc = Acc_ChartAccount.findOne({mapToAccount: "008"});


                let cusDoc = Sb_Client.findOne({_id: doc.clientId});

                let journalDoc = {};
                journalDoc.journalDate = doc.repaymentDate;
                journalDoc.currencyId = doc.currencyId;
                journalDoc.memo = cusDoc.name + "បង់ប្រាក់";
                journalDoc.branchId = doc.branchId;
                journalDoc.closingEntryId = repaymentId;
                journalDoc.status = "Payment";
                journalDoc.refId = repaymentId;
                journalDoc.total = numeral(GlobalFn.formatCurrency(principlePaid + interestPaid + operationFeePaid + doc.penaltyPaid + savingAmount, doc.currencyId)).value();

                let transaction = [];


                if (principlePaid + interestPaid + operationFeePaid + doc.penaltyPaid + savingAmount > 0) {
                    transaction.push({
                        account: cashAcc._id,
                        dr: principlePaid + interestPaid + operationFeePaid + doc.penaltyPaid + savingAmount,
                        cr: 0,
                        drcr: principlePaid + interestPaid + operationFeePaid + doc.penaltyPaid + savingAmount
                    });
                    if (principlePaid > 0) {
                        transaction.push({
                            account: principalAcc._id,
                            dr: 0,
                            cr: principlePaid,
                            drcr: -principlePaid
                        });
                    }

                    if (interestPaid > 0) {
                        transaction.push({
                            account: interestAcc._id,
                            dr: 0,
                            cr: interestPaid,
                            drcr: -interestPaid
                        });
                    }
                    if (operationFeePaid > 0) {
                        transaction.push({
                            account: operationFeeAcc._id,
                            dr: 0,
                            cr: operationFeePaid,
                            drcr: -operationFeePaid
                        });
                    }
                    if (doc.penaltyPaid > 0) {
                        transaction.push({
                            account: penaltyAcc._id,
                            dr: 0,
                            cr: doc.penaltyPaid,
                            drcr: -doc.penaltyPaid
                        });
                    }
                    if (savingAmount > 0) {
                        transaction.push({
                            account: savingAmountAcc._id,
                            dr: 0,
                            cr: savingAmount,
                            drcr: -savingAmount
                        });
                    }
                }

                journalDoc.transaction = transaction;
                Meteor.call("acc_insertJournal", journalDoc, Constants.secret);

            }
        }


    } else {

    }
}

let makeRepaymentPayOff = function (disbursementId, doc, repaymentId, cashAccChoose) {
    let repaymentScheduleList = Sb_RepaymentSchedule.find({
        loanId: disbursementId,
        isPaid: false
    }, {sort: {installment: 1}}).fetch();
    let amountPaid = doc.paid;
    let interestPaid = doc.interestPaid;

    let operationFeePaid = doc.operationFeePaid;
    if (repaymentScheduleList.length > 0) {
        repaymentScheduleList.forEach((obj) => {

            let repaymentScheduleDoc = obj;
            let paid = obj.paid || [];
            let paidDoc = {};
            paidDoc.repaymentId = repaymentId;

            paidDoc.operationFeePaid = operationFeePaid >= (obj.operationFeeUnpaid || 0) ? (obj.operationFeeUnpaid || 0) : operationFeePaid;
            operationFeePaid = operationFeePaid - paidDoc.operationFeePaid;
            operationFeePaid = operationFeePaid <= 0 ? 0 : operationFeePaid;

            repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.operationFeePaid, obj.currencyId, obj.branchId);
            repaymentScheduleDoc.operationFeeUnpaid = repaymentScheduleDoc.operationFeeUnpaid - paidDoc.operationFeePaid;

            paidDoc.interestPaid = interestPaid >= obj.interestUnpaid ? obj.interestUnpaid : interestPaid;
            interestPaid = interestPaid - paidDoc.interestPaid;
            interestPaid = interestPaid <= 0 ? 0 : interestPaid;

            repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.interestPaid, obj.currencyId, obj.branchId);
            repaymentScheduleDoc.interestUnpaid = repaymentScheduleDoc.interestUnpaid - paidDoc.interestPaid;

            paidDoc.principlePaid = amountPaid >= obj.principleUnpaid ? obj.principleUnpaid : amountPaid;
            amountPaid = amountPaid - paidDoc.principlePaid;
            amountPaid = amountPaid <= 0 ? 0 : amountPaid;

            repaymentScheduleDoc.balanceUnpaid = GlobalFn.roundCurrency(repaymentScheduleDoc.balanceUnpaid - paidDoc.principlePaid, obj.currencyId, obj.branchId);
            repaymentScheduleDoc.principleUnpaid = repaymentScheduleDoc.principleUnpaid - paidDoc.principlePaid;

            paid.push(paidDoc);
            repaymentScheduleDoc.isPaid = true;
            repaymentScheduleDoc.paid = paid;

            Sb_RepaymentSchedule.update({_id: obj._id}, {$set: repaymentScheduleDoc});

            Sb_Repayment.update({_id: repaymentId}, {
                $inc: {
                    principlePaid: math.round(paidDoc.principlePaid, 3),
                    paid: math.round(paidDoc.interestPaid + paidDoc.operationFeePaid, 3)
                }
            });

        })

        let checkPayOff = Sb_RepaymentSchedule.find({loanId: disbursementId, isPaid: false}).fetch();
        if (checkPayOff.length === 0) {
            Sb_Disbursement.update({_id: disbursementId}, {$set: {status: "Pay Off", payOffDate: doc.repaymentDate}});
        }
        //Integrate to Account
        let companyDoc = Company.findOne({});
        if (companyDoc.isIntegrateLoanAndAccount === true) {

            if (doc.paid + doc.penaltyPaid > 0) {
                let cashAcc = cashAccChoose || Acc_ChartAccount.findOne({mapToAccount: "005"});
                let penaltyAcc = Acc_ChartAccount.findOne({mapToAccount: "015"});
                let principalAcc = Acc_ChartAccount.findOne({mapToAccount: "014"});
                let interestAcc = Acc_ChartAccount.findOne({mapToAccount: "017"});
                let operationFeeAcc = Acc_ChartAccount.findOne({mapToAccount: "019"});
                let savingAcc = Acc_ChartAccount.findOne({mapToAccount: "008"});
                let feeLawAcc = Acc_ChartAccount.findOne({mapToAccount: "024"});
                let bpFeeAcc = Acc_ChartAccount.findOne({mapToAccount: "025"});
                let roFeeAcc = Acc_ChartAccount.findOne({mapToAccount: "026"});

                let feeLaw = doc.lawFeePaid || 0;
                let roFee = doc.roFeePaid || 0;
                let bpFee = doc.bpFeePaid || 0;


                let cusDoc = Sb_Client.findOne({_id: doc.clientId});

                let journalDoc = {};
                journalDoc.journalDate = doc.repaymentDate;
                journalDoc.currencyId = doc.currencyId;
                journalDoc.memo = cusDoc.name + "បង់ផ្តាច់";
                journalDoc.branchId = doc.branchId;
                journalDoc.closingEntryId = repaymentId;
                journalDoc.status = "Pay Off";
                journalDoc.refId = repaymentId;
                journalDoc.total = numeral(GlobalFn.formatCurrency(doc.paid + (doc.interestPaid > 0 ? doc.interestPaid : 0) + doc.interestReminderChargePaid + doc.operationFeePaid + doc.penaltyPaid + feeLaw, doc.currencyId)).value();

                let transaction = [];

                let rem = doc.paid + doc.interestPaid + doc.interestReminderChargePaid + doc.operationFeePaid + doc.penaltyPaid - (doc.paidFromSaving || 0) - roFee - bpFee;
                if (doc.paidFromSaving > 0) {
                    transaction.push({
                        account: savingAcc._id,
                        dr: doc.paidFromSaving,
                        cr: 0,
                        drcr: doc.paidFromSaving
                    });
                }

                if (doc.interestPaid < 0) {
                    transaction.push({
                        account: interestAcc._id,
                        dr: -doc.interestPaid,
                        cr: 0,
                        drcr: -doc.interestPaid
                    });
                }
                if (rem >= 0) {
                    if (rem > 0) {
                        transaction.push({
                            account: cashAcc._id,
                            dr: rem,
                            cr: 0,
                            drcr: rem
                        });
                    }

                } else if (rem < 0) {
                    transaction.push({
                        account: cashAcc._id,
                        dr: 0,
                        cr: -rem,
                        drcr: -rem
                    });
                }


                if (doc.paid + doc.interestPaid + doc.interestReminderChargePaid + doc.operationFeePaid + doc.penaltyPaid > 0) {
                    if (doc.paid > 0) {
                        transaction.push({
                            account: principalAcc._id,
                            dr: 0,
                            cr: doc.paid,
                            drcr: -doc.paid
                        });
                    }

                    if (doc.interestPaid + doc.interestReminderChargePaid > 0) {
                        transaction.push({
                            account: interestAcc._id,
                            dr: 0,
                            cr: doc.interestPaid + doc.interestReminderChargePaid,
                            drcr: -doc.interestPaid + doc.interestReminderChargePaid
                        });
                    }
                    if (doc.operationFeePaid + feeLaw > 0) {
                        transaction.push({
                            account: operationFeeAcc._id,
                            dr: 0,
                            cr: doc.operationFeePaid + feeLaw,
                            drcr: -(doc.operationFeePaid + feeLaw)
                        });
                    }
                    if (feeLaw > 0) {
                        transaction.push({
                            account: feeLawAcc._id,
                            dr: feeLaw,
                            cr: 0,
                            drcr: feeLaw
                        });
                    }
                    if (bpFee > 0) {
                        transaction.push({
                            account: bpFeeAcc._id,
                            dr: bpFee,
                            cr: 0,
                            drcr: bpFee
                        });
                    }
                    if (roFee > 0) {
                        transaction.push({
                            account: roFeeAcc._id,
                            dr: roFee,
                            cr: 0,
                            drcr: roFee
                        });
                    }
                    if (doc.penaltyPaid > 0) {
                        transaction.push({
                            account: penaltyAcc._id,
                            dr: 0,
                            cr: doc.penaltyPaid,
                            drcr: -doc.penaltyPaid
                        });
                    }
                }

                journalDoc.transaction = transaction;
                Meteor.call("acc_insertJournal", journalDoc, Constants.secret);

            }
        }


    }
}

let removeRepayment = function (repaymentDoc) {
    let repaymentScheduleList = Sb_RepaymentSchedule.find({
        "paid.repaymentId": repaymentDoc._id,
        loanId: repaymentDoc.disbursementId
    }).fetch();
    let companyDoc = Company.findOne({});
    if (repaymentScheduleList.length > 0) {
        let savingAmount = companyDoc.isSavingInLoan ? (repaymentDoc.savingAmount || 0) : 0;
        repaymentScheduleList.forEach((obj) => {
            let newRepaymentScheduleDoc = obj;
            let newPaid = [];
            if (obj.paid.length > 0) {
                obj.paid.forEach((ob) => {
                    if (ob.repaymentId === repaymentDoc._id) {
                        newRepaymentScheduleDoc.balanceUnpaid = newRepaymentScheduleDoc.balanceUnpaid + ob.principlePaid + ob.interestPaid + (ob.operationFeePaid || 0);
                        newRepaymentScheduleDoc.principleUnpaid = newRepaymentScheduleDoc.principleUnpaid + ob.principlePaid;
                        newRepaymentScheduleDoc.interestUnpaid = newRepaymentScheduleDoc.interestUnpaid + ob.interestPaid;
                        newRepaymentScheduleDoc.operationFeeUnpaid = newRepaymentScheduleDoc.operationFeeUnpaid + (ob.operationFeePaid || 0);
                    } else {
                        newPaid.push(ob);
                    }
                })

                newRepaymentScheduleDoc.isPaid = !(math.round(newRepaymentScheduleDoc.balanceUnpaid, 2) > 0);

                newRepaymentScheduleDoc.paid = newPaid;
                Sb_RepaymentSchedule.update({_id: obj._id}, {$set: newRepaymentScheduleDoc});
            }
        })
        Sb_Disbursement.update({_id: repaymentDoc.disbursementId}, {
            $set: {
                status: "Active",
                payOffDate: ""
            },
            $inc: {
                savingAmount: -savingAmount
            }
        });
        Sb_RepaymentSchedule.remove({tmpRepaymentId: repaymentDoc._id});

    } else {
        let savingAmount = companyDoc.isSavingInLoan ? (repaymentDoc.savingAmount || 0) : 0;
        Sb_Disbursement.update({_id: repaymentDoc.disbursementId}, {
            $set: {
                status: "Active",
                payOffDate: ""
            },
            $inc: {
                savingAmount: -savingAmount
            }
        });
    }
}

