function pad(number, length) {
    let str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}


let reactiveCollectionSheet = function () {
    let selector = {};
    selector.isPaid = false;
    selector.date = {$lte: moment().format("YYYY-MM-DD")};
    let disbursementList = Sb_Disbursement.find({status: "Active"}).map(o => o._id);
    selector.loanId = {$in: disbursementList};
    let collect = Sb_RepaymentSchedule.aggregate([
            {
                $match: selector
            },
            {
                $lookup:
                    {
                        from: "sb_disbursement",
                        localField: "loanId",
                        foreignField: "_id",
                        as: "disbursementDoc"
                    }
            },
            {
                $unwind: {
                    path: "$disbursementDoc",
                    preserveNullAndEmptyArrays: true
                }
            },
            {
                $match: {"disbursementDoc.branchId": {$ne: undefined}, "disbursementDoc.status": "Active"}
            },
            {
                $group: {
                    _id: {
                        branchId: "$branchId",
                        loanId: "$loanId",
                    },
                    count: {$sum: 1}
                }
            }, {
                $group: {
                    _id: "$_id.branchId",
                    count: {$sum: 1}
                }
            },

        ],
        {
            allowDiskUse: true
        });

    collect.forEach((ob) => {
        CountsCollectionSheet.update({_id: ob._id}, {$set: {count: ob.count}});
    })
}


let escapeDayFn = function (setup, escapeList, repayDate) {
    let yearName = moment(repayDate, "YYYY-MM-DD").format("YYYY");

    let findEscape = escapeList.find(function (element) {
        return element.year === yearName;
    });


    if (setup.escapeType === "No Escape") {
        return repayDate;
    } else if (setup.escapeType === "Before Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeBeforeDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else if (setup.escapeType === "After Day") {
        let newRepayDate = repayDate;
        let inc = 0;
        let isLoop = true;
        do {
            let escAft = escapeAfterDay(newRepayDate, findEscape, setup, inc);
            newRepayDate = escAft.repayDate;
            isLoop = !(inc === escAft.inc);
            inc = escAft.inc;
        } while (isLoop === true);
        return newRepayDate;
    } else {
        return repayDate;
    }
}

let changeDayDown = function (day) {
    if (day === 0) {
        return 6;
    } else {
        day = day - 1;
        return day;
    }
}
let changeDayUp = function (day) {
    if (day === 0) {
        return 1;
    } else if (day === 6) {
        return 0;
    } else {
        day = day + 1;
        return day;
    }
}

let escapeAfterDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, 1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();

    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayUp(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, changeWeekendDay, "days");
    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
};

let escapeBeforeDay = function (repayDate, findEscape, setup, inc) {
    let data = {};
    let oldRepayDate = repayDate;
    let changeWeekendDay = 0;
    //Move Holiday
    let isMoveHoliday = true;
    while (isMoveHoliday) {
        let repaymentDateName = repayDate;
        if (findEscape && findEscape.dateList && findEscape.dateList.indexOf(repaymentDateName) > -1) {
            repayDate = GlobalFn.addDay(repayDate, -1, "days");
        } else {
            isMoveHoliday = false;
        }
    }

    //Move Weekend
    let isMoveWeekend = true;
    let numDay = moment(repayDate, "YYYY-MM-DD").toDate().getDay();
    while (isMoveWeekend) {
        if (setup.escapeDay.indexOf(numDay) > -1) {
            numDay = changeDayDown(numDay);
            changeWeekendDay++;
        } else {
            isMoveWeekend = false;
        }
    }

    repayDate = GlobalFn.addDay(repayDate, -changeWeekendDay, "days");

    if (oldRepayDate !== repayDate) {
        data.inc = inc + 1;
    }
    data.repayDate = repayDate;
    return data;
}

