import {Mongo} from "meteor/mongo";

export const Sb_SavingConfig = new Mongo.Collection("sb_savingConfig");
Sb_SavingConfig.schema = new SimpleSchema({
    isApproveProcess: {
        type: Boolean,
        defaultValue: false,

    },
    isChangeStartDate: {
        type: Boolean,
        defaultValue: false,

    },
    analystLevel: {
        type: Number,
        optional: true,
        defaultValue: 3
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_SavingConfig.attachSchema(Sb_SavingConfig.schema);


