import {Mongo} from "meteor/mongo";

export const Sb_Disbursement = new Mongo.Collection("sb_disbursement");
Sb_Disbursement.schema = new SimpleSchema({
    clientId: {
        type: String,
        label: "Client",
    },
    clientType: {
        type: String,
        optional: true
    },
    mortgage: {
        type: String,
        optional: true
    },
    paymentTypeId: {
        type: String,
        optional: true
    },
    groupId: {
        type: String,
        optional: true
    },
    isGroupLoan: {
        type: Boolean,
        optional: true,
        defaultValue: false
    },
    model: {
        type: String,
        optional: true
    },
    price: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    customPrinciple: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    specialInterest: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },

    specialPenalty: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },

    specialLawFee: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    specialROFee: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },

    specialBPFee: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    paid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    costOfFundP: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    costOfFundA: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    commission: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    guarantorId: {
        type: String,
        label: "Guarantor",
        optional: true
    },
    purposeId: {
        type: String,
        label: "Purpose",
        optional: true
    },
    productId: {
        type: String,
        label: "Product",
    },
    currencyId: {
        type: String,
        label: "Currency",
    },
    loanAmount: {
        type: Number,
        decimal: true,
        label: "Amount",
    },
    projectInterest: {
        type: Number,
        decimal: true,
        label: "Interest",
    },
    projectOperation: {
        type: Number,
        decimal: true,
        label: "Project Operation",
        optional: true
    },
    savingAmount: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    term: {
        type: Number
    },
    repaidFrequency: {
        type: Number
    },
    repaidPrinciple: {
        type: Number,
        defaultValue: 1
    },
    startInstallment: {
        type: Number,
        optional: true,
        defaultValue: 1
    },
    disbursementDate: {
        type: String,
        label: "Disbursement Date",
    },

    maturityDate: {
        type: String,
        label: "Maturity Date",
    },
    payOffDate: {
        type: String,
        label: "Pay Off Date",
        optional: true
    },
    writeOffDate: {
        type: String,
        label: "Write Off Date",
        optional: true
    },
    startPaidDate: {
        type: String,
        label: "Start Paid Date",
    },
    isReschedule: {
        type: Boolean,
        optional: true,
        defaultValue: false
    },
    repaymentId: {
        type: String,
        optional: true,
        defaultValue: ""
    },
    coId: {
        type: String,
        label: "Credit Officer",
    },
    tmpDis: {
        type: String,
        optional: true
    },
    status: {
        type: String,
        defaultValue: "Active",

        //Active
        //Processing
        //Rejected
        //Analyst Approved
        //CFO Approved
        //Write Off
        //Pay Off
    },
    description: {
        type: String,
        label: "Description",
        optional: true
    },
    paymentNumber: {
        type: Number,
        label: "Payment Number",
        optional: true,
        defaultValue: 0
    },
    feeType: {
        type: String,
        defaultValue: "Amount"
    },
    feeValue: {
        type: Number,
        decimal: true
    },
    feeAmount: {
        type: Number,
        decimal: true,
        label: "Fee Amount",
        optional: true,
        defaultValue: 0
    },
    insuranceType: {
        type: String,
        defaultValue: "Amount"
    },
    insuranceValue: {
        type: Number,
        decimal: true
    },
    insuranceAmount: {
        type: Number,
        decimal: true,
        label: "Fee Amount",
        optional: true,
        defaultValue: 0
    },
    numInstallmentPaid: {
        type: Number,
        optional: true,
        defaultValue: 0
    },
    operationFeeType: {
        type: String,
        defaultValue: "Amount"
    },
    operationFeeOf: {
        type: String,
        optional: true
    },
    operationFeeValue: {
        type: Number,
        decimal: true
    },
    lastInstallmentAmount: {
        type: Number,
        decimal: true,
        optional: true
    },
    operationFeeAmount: {
        type: Number,
        decimal: true,
        label: "Operation Fee Amount",
        optional: true,
        defaultValue: 0
    },
    paidFeeAmount: {
        type: Number,
        decimal: true,
        label: "Fee Amount",
        optional: true,
        defaultValue: 0
    },
    loanAcc: {
        type: String,
        optional: true,
    },

    stepper: {
        type: Number,
        optional: true,
    },
    analyst: {
        type: String,
        optional: true,
    },
    analystAt: {
        type: String,
        optional: true,
    },
    analystComment: {
        type: String,
        optional: true,
    },
    cfo: {
        type: String,
        optional: true,
    },
    cfoAt: {
        type: String,
        optional: true,
    },
    cfoComment: {
        type: String,
        optional: true,
    },

    bpDoc: {
        type: Object,
        optional: true,
        blackbox: true
    },
    'bpDoc.$.loanAmount': {
        type: Number,
        decimal: true,
        optional: true
    },
    'bpDoc.$.interest': {
        type: String,
        optional: true
    },
    'bpDoc.$.term': {
        type: String,
        optional: true
    },
    'bpDoc.$.bpId': {
        type: String,
        optional: true
    },

    //========================Custom Interest Rate
    isCustomInterestRate: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    interestList: {
        type: [Object],
        optional: true,
        blackbox: true
    },
    'interestList.$': {
        type: Object
    },
    'interestList.$.from': {
        type: Number,
        optional: true,
    },
    'interestList.$.to': {
        type: Number,
        optional: true,
    },
    'interestList.$.principle': {
        type: Number,
        decimal: true,
        optional: true,
    },
    'interestList.$.rate': {
        type: Number,
        decimal: true,
        optional: true,
    },
    //===============End Custom Interest Rate
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Disbursement.attachSchema(Sb_Disbursement.schema);


