import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Constants, t} from "../../imports/libs/constant";

import {Company} from "../../imports/collections/company";

import {Sb_Disbursement} from "../../imports/collections/sb_disbursement";
import {Branch} from "../../imports/collections/branch";
import {Exchange} from "../../imports/collections/exchange";
import numeral from "numeral"
import {Sb_LoanConfig} from "../../imports/collections/sb_loanConfig";
import {Sb_RepaymentSchedule} from "../../imports/collections/sb_repaymentSchedule";
import {Sb_Repayment} from "../../imports/collections/sb_repayment";
import {Sb_Staff} from "../../imports/collections/sb_staff";
import math from "mathjs"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchAuthorizePaymentList(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorDisbursement = {};


            let data = {};

            let branchDoc = {};
            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                branchDoc = Branch.findOne({_id: {$in: params.branch}});
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                branchDoc = Branch.findOne({_id: {$in: userDoc.branch}});
            }


            if (params.staffId && params.staffId !== "") {
                selectorDisbursement["disbursementDoc.coId"] = params.staffId;
            }

            selector.repaymentDate = params.dateAs;

            if (params.type === "Authorize") {
                selector.authorizeUserId = {$exists: true};
            } else if (params.type === "Non Authorize") {
                selector.authorizeUserId = {$exists: false};
            }

            let exchange = Exchange.findOne({_id: params.exchangeId});
            let companyDoc = Company.findOne({});


            let coefficientTotalPaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$totalPaid',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientPrinciplePaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$principlePaid',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientInterestPaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$interestPaid',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientOperationFeePaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$operationFeePaid',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientPenaltyPaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$penaltyPaid',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientInterestReminderChargePaid = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$interestReminderChargePaid',
                baseCurrency: companyDoc.baseCurrency
            });

            //Range Date
            let repaymentList = Sb_Repayment.aggregate([

                    {
                        $match: selector
                    },
                    {
                        $project: {
                            _id: 1,
                            voucher: 1,
                            disbursementId: 1,
                            clientId: 1,
                            currencyId: 1,
                            type: 1,
                            authorizeUserId: 1,
                            totalPaid: 1,
                            principlePaid: 1,
                            interestPaid: 1,
                            createdUser: 1,
                            operationFeePaid: 1,
                            penaltyPaid: 1,
                            interestReminderChargePaid: 1,
                            totalPaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientTotalPaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientTotalPaid.KHR, coefficientTotalPaid.THB]}
                                }
                            },
                            principlePaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientPrinciplePaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientPrinciplePaid.KHR, coefficientPrinciplePaid.THB]}
                                }
                            },
                            interestPaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientInterestPaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientInterestPaid.KHR, coefficientInterestPaid.THB]}
                                }
                            },
                            operationFeePaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientOperationFeePaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientOperationFeePaid.KHR, coefficientOperationFeePaid.THB]}
                                }
                            },
                            penaltyPaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientPenaltyPaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientPenaltyPaid.KHR, coefficientPenaltyPaid.THB]}
                                }
                            },
                            interestReminderChargePaidBase: {
                                $cond: {
                                    if: {$eq: ['$currencyId', 'USD']},
                                    then: coefficientInterestReminderChargePaid.USD,
                                    else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientInterestReminderChargePaid.KHR, coefficientInterestReminderChargePaid.THB]}
                                }
                            }
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "sb_disbursement",
                                localField: "disbursementId",
                                foreignField: "_id",
                                as: "disbursementDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$disbursementDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $match: selectorDisbursement
                    },

                    {
                        $lookup:
                            {
                                from: "sb_client",
                                localField: "clientId",
                                foreignField: "_id",
                                as: "clientDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$clientDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "users",
                                localField: "authorizeUserId",
                                foreignField: "_id",
                                as: "authorizeDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$authorizeDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $lookup:
                            {
                                from: "users",
                                localField: "createdUser",
                                foreignField: "_id",
                                as: "createdUserDoc"
                            }
                    },
                    {
                        $unwind: {
                            path: "$createdUserDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    },
                    {
                        $sort: {
                            "clientDoc.address": 1,
                            "clientDoc.name": 1
                        }
                    },
                    {
                        $group: {
                            _id: null,
                            data: {$push: "$$ROOT"},
                            totalPaidBase: {$sum: "$totalPaidBase"},
                            principlePaidBase: {$sum: "$principlePaidBase"},
                            interestPaidBase: {$sum: "$interestPaidBase"},
                            operationFeePaidBase: {$sum: "$operationFeePaidBase"},
                            penaltyPaidBase: {$sum: "$penaltyPaidBase"},
                            interestReminderChargePaidBase: {$sum: "$interestReminderChargePaidBase"},
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });

            return repaymentList;
        }
    },
    sb_insertAuthorizePayment(list, userId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let i = 1;
            try {
                list.forEach((d) => {
                    Sb_Repayment.direct.update({_id: d._id}, {$set: {authorizeUserId: userId}});
                })
                return true;
            } catch (err) {
                throw new Meteor.Error(err.message);
            }
        }
    }
})
