import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";

import {Sb_RepaymentSchedule} from "../../../imports/collections/sb_repaymentSchedule";
import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Sb_Staff} from "../../../imports/collections/sb_staff";
import {Sb_Client} from "../../../imports/collections/sb_client";
import {Sb_LoanProduct} from "../../../imports/collections/sb_loanProduct";
import {Branch} from "../../../imports/collections/branch";
import math, {isString} from "mathjs";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";

import {Sb_Repayment} from "../../../imports/collections/sb_repayment";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchRepaymentDetailReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selector1 = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selector1.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selector1.branchId = {$in: userDoc.branch || []};
            }
            let loanConfigDoc = Sb_LoanConfig.findOne(selector);

            let disbursementList = Sb_Disbursement.find({_id: params.disbursementId}).fetch().map(o => o._id);
            selector.loanId = {$in: disbursementList};

            let companyDoc = Company.findOne({});
            let isShowPenalty = loanConfigDoc.isNotPenalty ? "none" : "";

            let repaymentScheduleList = Sb_RepaymentSchedule.aggregate([
                {$match: selector},
                {$sort: {date: 1, installment: 1}},
                {
                    $unwind: {
                        path: "$paid",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup:
                        {
                            from: "sb_repayment",
                            localField: "paid.repaymentId",
                            foreignField: "_id",
                            as: "repaymentDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$repaymentDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup:
                        {
                            from: "users",
                            localField: "repaymentDoc.createdUser",
                            foreignField: "_id",
                            as: "userDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$userDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }, {
                    $project: {
                        repaymentDateShow: "$repaymentDoc.repaymentDate",
                        penaltyPaid: "$repaymentDoc.penaltyPaid",
                        interestPaid: "$paid.interestPaid",
                        principlePaid: "$paid.principlePaid",
                        repaymentId: "$repaymentDoc._id",
                        repaymentDate: {$ifNull: ['$repaymentDoc.repaymentDate', '$date']},
                        loanId: "$loanId",
                        isPaid: "$isPaid",
                        date: "$date",
                        balanceUnpaid: "$balanceUnpaid",
                        principle: "$principle",
                        interest: "$interest",
                        amount: "$amount",
                        currencyId: "$currencyId",
                        installment: "$installment",
                        isAllowClosing: "$isAllowClosing",
                        dayRange: "$dayRange",
                        productId: "$productId",
                        clientId: "$clientId",
                        branchId: "$branchId",
                        isFromSchedule: "true",
                        createdAt: {$ifNull: ['$repaymentDoc.createdAt', '$date']},
                        userDoc: "$userDoc",
                    }
                }

            ], {allowDiskUse: true});


            let repaymentList = Sb_Repayment.aggregate([

                {
                    $match: {
                        disbursementId: params.disbursementId,
                        type: {$in: ["Reschedule", "Booking"]}
                    }
                },
                {$sort: {repaymentDate: 1, createdAt: 1}},
                {
                    $lookup:
                        {
                            from: "users",
                            localField: "createdUser",
                            foreignField: "_id",
                            as: "userDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$userDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $project: {
                        _id: 1,
                        repaymentDate: 1,
                        currencyId: 1,
                        voucher: 1,
                        type: 1,
                        clientId: 1,
                        paid: 1,
                        totalPaid: 1,
                        branchId: 1,
                        penaltyPaid: 1,
                        principlePaid: 1,
                        interestPaid: 1,
                        operationFeePaid: 1,
                        penalty: 1,
                        interestReminderCharge: 1,
                        interestReminderChargePaid: 1,
                        dayLate: 1,
                        userDoc: 1,
                        createdAt: {$ifNull: ['$createdAt', '$repaymentDate']},
                    }
                }
            ], {allowDiskUse: true});
            let repaymentTotalList = Sb_Repayment.aggregate([

                {
                    $match: {
                        disbursementId: params.disbursementId,
                    }
                },
                {$sort: {repaymentDate: 1, createdAt: 1}},
                {
                    $lookup:
                        {
                            from: "users",
                            localField: "createdUser",
                            foreignField: "_id",
                            as: "userDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$userDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $project: {
                        _id: 1,
                        repaymentDate: 1,
                        currencyId: 1,
                        voucher: 1,
                        type: 1,
                        clientId: 1,
                        paid: 1,
                        totalPaid: 1,
                        branchId: 1,
                        penaltyPaid: 1,
                        principlePaid: 1,
                        interestPaid: 1,
                        operationFeePaid: 1,
                        penalty: 1,
                        interestReminderCharge: 1,
                        interestReminderChargePaid: 1,
                        dayLate: 1,
                        userDoc: 1,
                        createdAt: {$ifNull: ['$createdAt', '$repaymentDate']},
                    }
                },
                {
                    $group: {
                        _id: null,
                        data: {$push: "$$ROOT"},
                        totalPaid: {$sum: "$totalPaid"},
                        penaltyPaid: {$sum: "$penaltyPaid"},
                        principlePaid: {$sum: "$principlePaid"},
                        interestPaid: {$sum: "$interestPaid"},
                        operationFeePaid: {$sum: "$operationFeePaid"},
                        interestReminderChargePaid: {$sum: "$interestReminderChargePaid"},
                    }
                },
            ], {allowDiskUse: true});

            if (repaymentScheduleList.length > 0) {
                repaymentScheduleList.push(...repaymentList);
            }
            let newRepaymentScheduleList = repaymentScheduleList.sort(compareRepayASD);
            let disbursementDoc = Sb_Disbursement.findOne({_id: params.disbursementId});
            let balanceDue = disbursementDoc.loanAmount;

            if (repaymentScheduleList.length > 0) {
                data.currencyHeader = repaymentScheduleList[0].currencyId === "KHR" ? "ប្រាក់រៀល" : companyDoc.baseCurrency === "THB" ? "ប្រាក់បាត" : "ប្រាក់ដុល្លារ";
                data.currencySymbol = GlobalFn.getCurrencySymbol(repaymentScheduleList[0].currencyId);
            } else {
                data.currencyHeader = "";
                data.currencySymbol = "";
            }
            let repaymentHTML = "";

            let totalPrinciple = 0;
            let totalInterest = 0;

            let totalPenalty = 0;
            let i = 1;
            let inst = 1;
            let repaymentId = "";
            if (newRepaymentScheduleList.length > 0) {
                newRepaymentScheduleList.forEach((obj) => {
                    if (repaymentId === obj.repaymentId) {
                        obj.penaltyPaid = 0;
                    }

                    if (obj.isFromSchedule === "true") {
                        if (obj.isPaid === true || (obj.interestPaid > 0 || obj.principlePaid > 0 || obj.penaltyPaid > 0)) {
                            let backgroundColor = math.round(obj.balanceUnpaid, 0) > 0 ? "transparent" : "yellow";
                            let tdth = math.round(obj.balanceUnpaid, 0) > 0 ? "td" : "th";
                            let colorRow = (math.round(obj.balanceUnpaid, 0) > 0 && (math.round(obj.balanceUnpaid, 0) < math.round(obj.amount, 0)) ? "red" : "#333");


                            if (obj.isAllowClosing === false) {
                                repaymentHTML += `
<tr style="background-color:${backgroundColor};color: ${colorRow}">
<td style="text-align: center !important;"><u>${inst}</u></td>
<${tdth} style="text-align: left !important;">${GlobalFn.formatDate(obj.date)}</${tdth}>
<${tdth} style="text-align: left !important;">${GlobalFn.formatDate(obj.repaymentDateShow || "")}</${tdth}>

<td style="text-align: center !important;">${obj.dayRange}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(balanceDue || 0, obj.currencyId)}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.principlePaid || 0, obj.currencyId)}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.interestPaid || 0, obj.currencyId)}</td>
<th style="text-align: right !important;">${GlobalFn.formatCurrency(obj.penaltyPaid || 0, obj.currencyId)}</th>
<th style="text-align: right !important;">${GlobalFn.formatCurrency(((obj.penaltyPaid || 0) + (obj.principlePaid || 0) + (obj.interestPaid || 0)), obj.currencyId)}</th>
<th style="text-align: center !important;">${obj.isPaid && math.round(obj.balanceUnpaid, 0) > 0 ? "Paid Off" : obj.isPaid ? "X" : ""}</th>
<th style="text-align: left !important;">${obj.userDoc && obj.userDoc.username || ""}</th>
</tr>

`;


                            } else {
                                repaymentHTML += `
<tr style="background-color: ${backgroundColor};color: ${colorRow}">
<td style="text-align: center !important;">${inst}</td>
<${tdth} style="text-align: left !important;">${GlobalFn.formatDate(obj.date)}</${tdth}>
<${tdth} style="text-align: left !important;">${GlobalFn.formatDate(obj.repaymentDateShow || "")}</${tdth}>
<td style="text-align: center !important;">${obj.dayRange}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(balanceDue || 0, obj.currencyId)}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.principlePaid || 0, obj.currencyId)}</td>
<td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.interestPaid || 0, obj.currencyId)}</td>
<th style="text-align: right !important;display: ${isShowPenalty}">${GlobalFn.formatCurrency(obj.penaltyPaid || 0, obj.currencyId)}</th>
<th style="text-align: right !important;">${GlobalFn.formatCurrency(((obj.penaltyPaid || 0) + (obj.principlePaid || 0) + (obj.interestPaid || 0)), obj.currencyId)}</th>
<th style="text-align: center !important;">${obj.isPaid && math.round(obj.balanceUnpaid, 0) > 0 ? "Paid Off" : obj.isPaid ? "X" : ""}</th>
<th style="text-align: left !important;">${obj.userDoc && obj.userDoc.username || ""}</th>
</tr>

`;

                            }

                            totalPrinciple += obj.principlePaid;
                            totalInterest += obj.interestPaid;
                            totalPenalty += parseFloat(obj.penaltyPaid || 0);
                            balanceDue = GlobalFn.roundCurrency(balanceDue - obj.principlePaid, obj.currencyId, obj.branchId);
                            i++;
                            inst++;
                        }
                    } else {
                        balanceDue = GlobalFn.roundCurrency(balanceDue - obj.paid, obj.currencyId, obj.branchId);
                        i++;

                    }
                    repaymentId = obj.repaymentId;
                })
            }


            let clientDoc = Sb_Client.findOne({_id: disbursementDoc.clientId});
            let creditOfficerDoc = Sb_Staff.findOne({_id: disbursementDoc.coId});


            repaymentHTML += `
<tr>
<th colspan="4">ឈ្មោះអតិថិជន</th>
<th style="text-align: right !important;">${clientDoc.name}</th>
<th style="text-align: right">${GlobalFn.formatCurrency(totalPrinciple, disbursementDoc.currencyId)}</th>
<th style="text-align: right">${GlobalFn.formatCurrency(totalInterest, disbursementDoc.currencyId)}</th>
<th style="text-align: right">${GlobalFn.formatCurrency(totalPenalty, disbursementDoc.currencyId)}</th>
<th style="text-align: right">${GlobalFn.formatCurrency(totalPrinciple + totalInterest + totalPenalty, disbursementDoc.currencyId)}</th>
</tr>
<tr>
<tr>
<th colspan="4">លេខគណនី</th>
<th style="text-align: right !important;">${disbursementDoc.loanAcc}</th>
</tr>
<tr>



`

            repaymentHTML += `
<tr>
<th colspan="4">សរុបសងប្រាក់</th>
<th style="text-align: right !important;color: red">${GlobalFn.formatCurrency(totalPrinciple + totalInterest + totalPenalty, disbursementDoc.currencyId)}</th>
</tr>
`;


            let paymentDetail = "";
            if (repaymentTotalList.length > 0) {

                paymentDetail += `<table><thead>
                        <tr><th style="color: blue" colspan="10">ព័តមានបង់ប្រាក់</th></tr>
                        <tr>
                            <th>ល.រ</th>
                            <th>ថ្ងៃបង់</th>
                            <th>ថ្ងៃយឺត</th>
                            <th>បង់ដើម</th>
                            <th>បង់ការ</th>
                            <th>បង់ពិន័យ</th>
                            <th>បង់ពិន័យលើការប្រាក់</th>
                            <th>បង់សេរវាប្រតិបត្តការ</th>
                            <th>សរុបបង់</th>
                            <th>អ្នកទទួល</th>
                        </tr>
                </thead><tbody>`;
                let j = 1;
                repaymentTotalList[0].data.forEach((d) => {
                    if (d) {
                        paymentDetail += `
                        <tr>
                            <td>${j}</td>
                            <td>${GlobalFn.formatDate(d.repaymentDate)}</td>
                            <td>${d.dayLate}</td>
                            <td>${GlobalFn.formatCurrency(d.principlePaid || 0, d.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(d.interestPaid || 0, d.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(d.penaltyPaid || 0, d.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(d.interestReminderChargePaid || 0, d.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(d.operationFeePaid || 0, d.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(d.totalPaid || 0, d.currencyId)}</td>
                            <th style="text-align: left !important;">${d.userDoc && d.userDoc.username || ""}</th>

                        </tr>
                    
                    `;
                        j++;
                    }
                })
                paymentDetail += `
                        <tr>
                            <th colspan="3">សរុប</th>
                       
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].principlePaid || 0, disbursementDoc.currencyId)}</th>
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].interestPaid || 0, disbursementDoc.currencyId)}</th>
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].penaltyPaid || 0, disbursementDoc.currencyId)}</th>
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].interestReminderChargePaid || 0, disbursementDoc.currencyId)}</th>
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].operationFeePaid || 0, disbursementDoc.currencyId)}</th>
                            <th>${GlobalFn.formatCurrency(repaymentTotalList[0].totalPaid || 0, disbursementDoc.currencyId)}</th>
                            <th></th>
                        </tr>`;

                paymentDetail += `</tbody></table >`;
            }
            repaymentHTML += paymentDetail;
            data.dataHtml = repaymentHTML;
            data.clientDoc = clientDoc;
            data.disbursementDoc = disbursementDoc;
            data.creditOfficerDoc = creditOfficerDoc;
            return data;
        }
    }
})

let compareRepayASD = function (a, b) {
    if (a.repaymentDate === b.repaymentDate) {
        let aCreatedAt = isString(a.createdAt) ? moment(a.createdAt, "YYYY-MM-DD").toDate() : a.createdAt;
        let bCreatedAt = isString(b.createdAt) ? moment(b.createdAt, "YYYY-MM-DD").toDate() : b.createdAt;
        if (aCreatedAt.getTime() === bCreatedAt.getTime()) {
            return 0;
        }
        return aCreatedAt.getTime() < bCreatedAt.getTime() ? -1 : 1;
    }
    return a.repaymentDate < b.repaymentDate ? -1 : 1;
}