import {Mongo} from "meteor/mongo";

export const Sb_OpenAccount = new Mongo.Collection("sb_openAccount");
Sb_OpenAccount.schema = new SimpleSchema({
    clientId: {
        type: String,
    },
    staffId: {
        type: String,
    },
    productId: {
        type: String,
    },
    savingAcc: {
        type: String,
        optional: true
    },
    joinClientId: {
        type: [String],
        blackbox: true,
        optional: true
    },
    currencyId: {
        type: String,
        optional: true
    },
    depositNumber: {
        type: Number,
        defaultValue: 0,
        optional: true
    },
    balance: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    interest: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    installmentAlreadyWithdraw: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    withdrawFrequency: {
        type: Number,
        defaultValue: 1,
        optional: true
    },
    type: {
        type: String,
        optional: true
    },
    rate: {
        type: Number,
        decimal: true
    },
    term: {
        type: Number,
    },
    note: {
        type: String,
        optional: true
    },
    openAccountDate: {
        type: String,
        optional: true
    },
    startDate: {
        type: String,
        optional: true
    },

    endDate: {
        type: String,
        optional: true
    },
    closeDate: {
        type: String,
        optional: true
    },
    cutDate: {
        type: String,
        optional: true
    },
    status: {
        type: String,
        optional: true,
        defaultValue: "Processing"
        //Processing
        //Analyst Approved
        //CFO Approved
        //Approved
        //Rejected
    },
    stepper: {
        type: Number,
        optional: true,
    },
    analyst: {
        type: String,
        optional: true,
    },
    analystAt: {
        type: String,
        optional: true,
    },
    analystComment: {
        type: String,
        optional: true,
    },
    cfo: {
        type: String,
        optional: true,
    },
    cfoAt: {
        type: String,
        optional: true,
    },
    cfoComment: {
        type: String,
        optional: true,
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_OpenAccount.attachSchema(Sb_OpenAccount.schema);


