import {Mongo} from "meteor/mongo";

export const Sb_RepaymentWithBank = new Mongo.Collection("sb_repaymentWithBank");
Sb_RepaymentWithBank.schema = new SimpleSchema({
    clientId: {
        type: String,
        label: "Client",
    },
    disbursementId: {
        type: String,
        label: "Client",
    },
    currencyId: {
        type: String,
        label: "Currency",
    },
    type: {
        type: String,
    },
    paymentTypeId: {
        type: String,
        optional: true
    },
    token: {
        type: String,
        optional: true
    },
    repaymentDate: {
        type: String,
        label: "RepaymentWithBank Date",
    },
    time: {
        type: String,
        optional: true
    },
    dayLate: {
        type: Number,
        optional: true
    },
    paid: {
        type: Number,
        decimal: true
    },
    penaltyPaid: {
        type: Number,
        decimal: true
    },
    principlePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    interestPaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    operationFeePaid: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    totalPaid: {
        type: Number,
        decimal: true
    },
    status: {
        type: Boolean,
        defaultValue: false,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,
    },
    updatedAt: {
        type: Date,
        optional: true,
    },
    createdUser: {
        type: String,
        optional: true,
    }
});
Sb_RepaymentWithBank.attachSchema(Sb_RepaymentWithBank.schema);