import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import {Sb_TargetPlan} from "../../../imports/collections/sb_targetPlan";
import {Sb_Repayment} from "../../../imports/collections/sb_repayment";
import {Sb_LoanProduct} from "../../../imports/collections/sb_loanProduct";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchDisbursementReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;
            }

            if (params.productId && params.productId !== "") {
                selector.productId = params.productId;
            }

            if (params.productTypeId && params.productTypeId !== "") {
                let productList = Sb_LoanProduct.find({productTypeId: params.productTypeId}).map((d) => d._id);
                selector.productId = {$in: productList};
            }

            selector.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};
            selector.$or = [
                {repaymentId: ""},
                {repaymentId: null},
                {repaymentId: undefined},
            ];
            selector.disbursementDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let companyDoc = Company.findOne({});
            let showCostOfFund = companyDoc.isCostOfFund ? "" : " none";
            let showBP = companyDoc.isSpecialLoan ? "" : "none";
            let bpSpan = companyDoc.isSpecialLoan ? 13 : 7;
            if (params.bpId && params.bpId !== "") {
                selector["bpDoc.bpId"] = params.bpId;
            }

            if (params.paymentTypeId && params.paymentTypeId !== "") {
                selector.paymentTypeId = params.paymentTypeId;
            }
            let dataList = Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        disbursementDate: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_client",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "coId",
                        foreignField: "_id",
                        as: "creditOfficerDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$creditOfficerDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_loanProduct",
                        localField: "productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }, {
                    $lookup:
                        {
                            from: "sb_purpose",
                            localField: "purposeId",
                            foreignField: "_id",
                            as: "purposeDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$purposeDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_businessPartner",
                        localField: "bpDoc.bpId",
                        foreignField: "_id",
                        as: "bpDetail"
                    }
                },
                {
                    $unwind: {
                        path: "$bpDetail",
                        preserveNullAndEmptyArrays: true
                    }
                },

            ], {allowDiskUse: true});


            let dataHtml = "";


            let totalAmountUSD = 0;
            let totalFeeUSD = 0;
            let totalInterestUSD = 0;
            let totalProjectOperationUSD = 0;
            let totalCostOfFundUSD = 0;


            let totalAmountTHB = 0;
            let totalFeeTHB = 0;
            let totalInterestTHB = 0;
            let totalProjectOperationTHB = 0;
            let totalCostOfFundTHB = 0;


            let totalAmountKHR = 0;
            let totalFeeKHR = 0;
            let totalInterestKHR = 0;
            let totalProjectOperationKHR = 0;
            let totalCostOfFundKHR = 0;

            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";
            let i = 1;
            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                            <td style="text-align: left !important;">${obj.productDoc.name}</td>
                          
                            <td style="text-align: left !important;">${moment(obj.disbursementDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${moment(obj.maturityDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                         
                            <td style="text-align: left !important;">${obj.purposeDoc && obj.purposeDoc.name || ""}</td>
                            <td>${GlobalFn.formatCurrency(obj.loanAmount, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.projectInterest, obj.currencyId)}</td>
                            <td style="display: ${displayFee}">${GlobalFn.formatCurrency(obj.projectOperation, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.feeAmount, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency((obj.feeAmount + obj.projectInterest + (obj.projectOperation || 0) + obj.loanAmount), obj.currencyId)}</td>
                             <td style="display: ${showCostOfFund}">${GlobalFn.formatCurrency((obj.costOfFundA || 0), obj.currencyId)}</td>

                                                 <td style="text-align: left !important;">${obj.currencyId}</td>
                                                    <td style="text-align: left !important;">${obj.term}</td>
                            <td style="text-align: left !important;">${obj.productDoc.rate} %</td>
                            <td style="text-align: left !important;">${obj.clientDoc.loanCycle}</td>
                            <td style="text-align: left !important;">${obj.productDoc.rateType}</td>
                                                <td style="text-align: left !important;">${obj.creditOfficerDoc.name}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.address || ""}</td>
                            
                            <td style="text-align: left !important;display: ${showBP}">${obj.bpDetail && obj.bpDetail.name || ""}</td>
                            <td style="text-align: left !important;display: ${showBP}">${obj.bpDetail && obj.bpDetail.phoneNumber || ""}</td>
                            <td style="text-align: left !important;display: ${showBP}">${obj.bpDetail && obj.bpDetail.company || ""}</td>
                            <td style="text-align: left !important;display: ${showBP}">${GlobalFn.seperateNumber(obj.bpDoc && obj.bpDoc.loanAmount || "")}</td>
                            <td style="text-align: left !important;display: ${showBP}">${obj.bpDoc && obj.bpDoc.interest || ""}</td>
                            <td style="text-align: left !important;display: ${showBP}">${obj.bpDoc && obj.bpDoc.term || ""}</td>

                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {

                        totalAmountUSD += obj.loanAmount;
                        totalFeeUSD += obj.feeAmount;
                        totalInterestUSD += obj.projectInterest;
                        totalProjectOperationUSD += obj.projectOperation || 0;
                        totalCostOfFundUSD += obj.costOfFundA || 0;
                    } else if (obj.currencyId === "KHR") {
                        totalAmountKHR += obj.loanAmount;
                        totalFeeKHR += obj.feeAmount;
                        totalInterestKHR += obj.projectInterest;
                        totalProjectOperationKHR += obj.projectOperation || 0;
                        totalCostOfFundKHR += obj.costOfFundA || 0;

                    } else if (obj.currencyId === "THB") {
                        totalAmountTHB += obj.loanAmount;
                        totalFeeTHB += obj.feeAmount;
                        totalInterestTHB += obj.projectInterest;
                        totalProjectOperationTHB += obj.projectOperation || 0;
                        totalCostOfFundTHB += obj.costOfFundA || 0;

                    }
                }
            })


            let grandTotalAmount = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalAmountUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalAmountTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalAmountKHR, exchange);
            let grandTotalFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFeeKHR, exchange);
            let grandTotalInterest = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInterestUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInterestTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInterestKHR, exchange);
            let grandTotalOperation = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalProjectOperationUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalProjectOperationTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalProjectOperationKHR, exchange);
            let grandTotalCostOfFund = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalCostOfFundUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalCostOfFundTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalCostOfFundKHR, exchange);


            dataHtml += `
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestUSD, "USD")}</td>
                    <td  style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountUSD + totalFeeUSD + totalInterestUSD + totalProjectOperationUSD, "USD")}</td>
                    <td style="display: ${showCostOfFund}">${GlobalFn.formatCurrency(totalCostOfFundUSD, "USD")}</td>
                    <td colspan="${bpSpan}"></td>
                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountKHR + totalFeeKHR + totalInterestKHR + totalProjectOperationKHR, "KHR")}</td>
                    <td style="display: ${showCostOfFund}">${GlobalFn.formatCurrency(totalCostOfFundKHR, "KHR")}</td>

                                      <td colspan="${bpSpan}"></td>

                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountTHB + totalFeeTHB + totalInterestTHB + totalProjectOperationTHB, "THB")}</td>
                   <td style="display: ${showCostOfFund}">${GlobalFn.formatCurrency(totalCostOfFundTHB, "THB")}</td>
                  <td colspan="${bpSpan}"></td>

                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotal']}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalAmount, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalInterest, companyDoc.baseCurrency)}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(grandTotalOperation, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalFee, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalAmount + grandTotalInterest + grandTotalFee + grandTotalOperation, companyDoc.baseCurrency)}</td>
                     <td style="display: ${showCostOfFund}">${GlobalFn.formatCurrency(grandTotalCostOfFund, companyDoc.baseCurrency)}</td>
                                      <td colspan="${bpSpan}"></td>

                </tr>
        `;


            data.dataHtml = dataHtml;
            return data;
        }
    },
    sb_fetchDisbursementGraphReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let selectorPlan = {};
            let data = {};


            selector.branchId = params.branchId;
            selectorConfig.branchId = params.branchId;
            selectorPlan.branchId = params.branchId;

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({status: true});


            selector.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};

            selector.disbursementDate = {$gte: params.year + "-01-01", $lte: params.year + "-12-31"};
            let companyDoc = Company.findOne({});
            selectorPlan.year = params.year;
            let targetPlan = Sb_TargetPlan.findOne(selectorPlan);
            let monthlyList = [];
            let quarterList = [];
            let semesterList = [];
            let yearlyList = [];


            let coefficientValue = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$loanAmount',
                baseCurrency: companyDoc.baseCurrency
            });
            selector.$or = [
                {repaymentId: ""},
                {repaymentId: null},
                {repaymentId: undefined},
            ];

            //Monthly
            Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $project: {
                        date: {
                            $dateFromString: {
                                dateString: "$disbursementDate"
                            }
                        },
                        loanAmount: 1,
                        currencyId: 1,
                    }
                },
                {
                    $project: {
                        month: {$month: "$date"},
                        year: {$year: "$date"},
                        loanAmount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValue.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValue.KHR, coefficientValue.THB]}
                            }
                        }
                    }
                }, {
                    $group: {
                        _id: {
                            month: "$month",
                            year: "$year"
                        },
                        loanAmount: {$sum: "$loanAmount"}
                    }
                },
                {
                    $sort: {
                        "_id.year": 1,
                        "_id.month": 1,
                    }
                }
            ], {allowDiskUse: true}).map(function (d) {
                let monthlyDoc = {};
                monthlyDoc.date = d._id.month + "/" + moment(d._id.year, "YYYY").format("YY");
                monthlyDoc.reach = d.loanAmount;
                monthlyDoc.plan = targetPlan && targetPlan.monthly || 0;
                monthlyList.push(monthlyDoc);

                let quarterDoc = {};
                quarterDoc.date = (d._id.month <= 3 ? "Quarter 1" : d._id.month <= 6 ? "Quarter 2" : d._id.month <= 9 ? "Quarter 3" : "Quarter 4") + "-" + d._id.year;
                quarterDoc.reach = d.loanAmount;
                quarterDoc.plan = targetPlan && targetPlan.quarter || 0;
                quarterList.push(quarterDoc);

                let semesterDoc = {};
                semesterDoc.date = (d._id.month <= 6 ? "Semester 1" : "Semester 2") + "-" + d._id.year;
                semesterDoc.reach = d.loanAmount;
                semesterDoc.plan = targetPlan && targetPlan.semester || 0;
                semesterList.push(semesterDoc);

                let yearDoc = {};
                yearDoc.date = d._id.year;
                yearDoc.reach = d.loanAmount;
                yearDoc.plan = targetPlan && targetPlan.yearly || 0;
                yearlyList.push(yearDoc);

            });

            monthlyList.map((d) => {
                d.date = d.date + " : " + ((d.plan || d.plan > 0) ? (Math.round((d.reach / d.plan) * 10000) / 100) : 0) + "%";
                return d;
            })

            let quarterListRaw = [];
            quarterList.reduce(function (key, val) {
                if (!key[val.date]) {
                    key[val.date] = {
                        date: val.date,
                        reach: val.reach,
                        plan: val.plan,
                    };
                    quarterListRaw.push(key[val.date]);
                } else {
                    key[val.date].reach += val.reach;
                }
                return key;
            }, {});

            quarterListRaw.map((d) => {
                d.date = d.date + " : " + ((d.plan || d.plan > 0) ? (Math.round((d.reach / d.plan) * 10000) / 100) : 0) + "%";
                return d;
            })

            let semesterListRaw = [];
            semesterList.reduce(function (key, val) {
                if (!key[val.date]) {
                    key[val.date] = {
                        date: val.date,
                        reach: val.reach,
                        plan: val.plan,
                    };
                    semesterListRaw.push(key[val.date]);
                } else {
                    key[val.date].reach += val.reach;
                }
                return key;
            }, {});

            semesterListRaw.map((d) => {
                d.date = d.date + " : " + ((d.plan || d.plan > 0) ? (Math.round((d.reach / d.plan) * 10000) / 100) : 0) + "%";
                return d;
            })


            let yearListRaw = [];
            yearlyList.reduce(function (key, val) {
                if (!key[val.date]) {
                    key[val.date] = {
                        date: val.date,
                        reach: val.reach,
                        plan: val.plan,
                    };
                    yearListRaw.push(key[val.date]);
                } else {
                    key[val.date].reach += val.reach;
                }
                return key;
            }, {});

            let yearListRawFinal = [];
            yearListRawFinal.push({plan: "Reach", amount: yearListRaw[0] && yearListRaw[0].reach || 0});
            yearListRawFinal.push({
                plan: "Un Reach",
                amount: (yearListRaw[0] && yearListRaw[0].plan || 0) - (yearListRaw[0] && yearListRaw[0].reach || 0)
            });

            data.monthly = monthlyList;
            data.quarter = quarterListRaw;
            data.semester = semesterListRaw;
            data.year = yearListRawFinal;
            return data;
        }
    },
    sb_fetchDisbursementDisplayBoxReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorPayOffToday = {};
            let selectorPayOffThisMonth = {};
            let selectorReceiveToday = {};
            let selectorReceiveThisMonth = {};
            let selectorDisbursementToday = {};
            let selectorDisbursementThisMonth = {};
            let selectorTotalDisbursement = {};
            let selectorConfig = {};
            let data = {};


            selector.branchId = params.branchId;
            selectorConfig.branchId = params.branchId;
            selectorPayOffToday.branchId = params.branchId;
            selectorPayOffThisMonth.branchId = params.branchId;
            selectorReceiveToday.branchId = params.branchId;
            selectorReceiveThisMonth.branchId = params.branchId;
            selectorDisbursementToday.branchId = params.branchId;
            selectorDisbursementThisMonth.branchId = params.branchId;
            selectorTotalDisbursement.branchId = params.branchId;

            let exchange = Exchange.findOne({status: true});
            selector.status = "Active";

            let companyDoc = Company.findOne({});
            let coefficientValue = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$schDoc.principleUnpaid',
                baseCurrency: companyDoc.baseCurrency
            });
            let coefficientValueLoanAmount = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$loanAmount',
                baseCurrency: companyDoc.baseCurrency
            });
            data.loanOutstanding = 0;
            data.numberOfLoanOutstanding = 0;
            data.totalDisbursement = 0;
            data.numberOfTotalDisbursement = 0;

            data.numPayOffToday = 0;
            data.numPayOffThisMonth = 0;
            data.receiveToday = 0;
            data.receiveThisMonth = 0;
            data.numReceiveToday = 0;
            data.numReceiveThisMonth = 0;
            data.disbursementToday = 0;
            data.disbursementThisMonth = 0;
            data.numDisbursementToday = 0;
            data.numDisbursementThisMonth = 0;

            //Loan Outstanding
            Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $lookup: {
                        from: "sb_repaymentSchedule",
                        localField: "_id",
                        foreignField: "loanId",
                        as: "schDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$schDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $match: {
                        "schDoc.isPaid": false
                    }
                },
                {
                    $project: {
                        _id: "$_id",
                        principleUnpaid: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValue.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValue.KHR, coefficientValue.THB]}
                            }
                        }
                    }
                }, {
                    $group: {
                        _id: "$_id",
                        principleUnpaid: {$sum: "$principleUnpaid"},
                    }
                }, {
                    $group: {
                        _id: null,
                        principleUnpaid: {$sum: "$principleUnpaid"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.loanOutstanding = d.principleUnpaid;
                data.numberOfLoanOutstanding = d.num;
            });

            //Pay Off Today
            selectorPayOffToday.payOffDate = moment().format("YYYY-MM-DD");
            selectorPayOffToday.status = "Pay Off";
            data.numPayOffToday = Sb_Disbursement.find(selectorPayOffToday).count();

            //Pay Off This Month
            selectorPayOffThisMonth.payOffDate = {
                $gte: moment().startOf("month").format("YYYY-MM-DD"),
                $lte: moment().endOf("month").format("YYYY-MM-DD")
            };
            selectorPayOffThisMonth.status = "Pay Off";
            data.numPayOffThisMonth = Sb_Disbursement.find(selectorPayOffThisMonth).count();


            //Receive Today
            selectorReceiveToday.repaymentDate = moment().format("YYYY-MM-DD");
            Sb_Repayment.aggregate([
                {
                    $match: selectorReceiveToday
                },
                {
                    $group: {
                        _id: null,
                        totalPaid: {$sum: "$totalPaid"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.receiveToday = d.totalPaid;
                data.numReceiveToday = d.num;
            });

            //Receive This Month
            selectorReceiveThisMonth.repaymentDate = {
                $gte: moment().startOf("month").format("YYYY-MM-DD"),
                $lte: moment().endOf("month").format("YYYY-MM-DD")
            };
            Sb_Repayment.aggregate([
                {
                    $match: selectorReceiveThisMonth
                },
                {
                    $group: {
                        _id: null,
                        totalPaid: {$sum: "$totalPaid"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.receiveThisMonth = d.totalPaid;
                data.numReceiveThisMonth = d.num;
            });

            //Disbursement Today
            selectorDisbursementToday.disbursementDate = moment().format("YYYY-MM-DD");
            selectorDisbursementToday.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};
            selectorDisbursementToday.$or = [
                {repaymentId: ""},
                {repaymentId: null},
                {repaymentId: undefined},
            ];
            Sb_Disbursement.aggregate([
                {
                    $match: selectorDisbursementToday
                },
                {
                    $project: {
                        _id: "$_id",
                        loanAmount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValueLoanAmount.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValueLoanAmount.KHR, coefficientValueLoanAmount.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: null,
                        loanAmount: {$sum: "$loanAmount"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.disbursementToday = d.loanAmount;
                data.numDisbursementToday = d.num;
            });
            //Disbursement This Month
            selectorDisbursementThisMonth.disbursementDate = {
                $gte: moment().startOf("month").format("YYYY-MM-DD"),
                $lte: moment().endOf("month").format("YYYY-MM-DD")
            };
            selectorDisbursementThisMonth.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};
            selectorDisbursementThisMonth.$or = [
                {repaymentId: ""},
                {repaymentId: null},
                {repaymentId: undefined},
            ];

            Sb_Disbursement.aggregate([
                {
                    $match: selectorDisbursementThisMonth
                },
                {
                    $project: {
                        _id: "$_id",
                        loanAmount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValueLoanAmount.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValueLoanAmount.KHR, coefficientValueLoanAmount.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: null,
                        loanAmount: {$sum: "$loanAmount"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.disbursementThisMonth = d.loanAmount;
                data.numDisbursementThisMonth = d.num;
            });
            //Disbursement This Month

            selectorTotalDisbursement.status = {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]};
            selectorTotalDisbursement.$or = [
                {repaymentId: ""},
                {repaymentId: null},
                {repaymentId: undefined},
            ];

            Sb_Disbursement.aggregate([
                {
                    $match: selectorTotalDisbursement
                },
                {
                    $project: {
                        _id: "$_id",
                        loanAmount: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientValueLoanAmount.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientValueLoanAmount.KHR, coefficientValueLoanAmount.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: null,
                        loanAmount: {$sum: "$loanAmount"},
                        num: {$sum: 1}
                    }
                }
            ], {allowDiskUse: true}).forEach((d) => {
                data.totalDisbursement = d.loanAmount;
                data.numberOfTotalDisbursement = d.num;
            });


            return data;
        }
    },
})
