import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchSaleReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;
            }

            if (params.clientType && params.clientType !== "" && params.clientType !== null) {
                selector.clientType = params.clientType;
            }

            selector.disbursementDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let companyDoc = Company.findOne({});
            if (params.cashType && params.cashType !== "") {
                selector.paymentTypeId = params.cashType;
            }
            let dataList = Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        disbursementDate: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_client",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_clientType",
                        localField: "clientType",
                        foreignField: "_id",
                        as: "clientTypeDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientTypeDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "coId",
                        foreignField: "_id",
                        as: "creditOfficerDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$creditOfficerDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_loanProduct",
                        localField: "productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }, {
                    $lookup:
                        {
                            from: "sb_purpose",
                            localField: "purposeId",
                            foreignField: "_id",
                            as: "purposeDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$purposeDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "users",
                        localField: "createdUser",
                        foreignField: "_id",
                        as: "userDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$userDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },

            ], {allowDiskUse: true});


            let dataHtml = "";


            let totalAmountUSD = 0;
            let totalPriceUSD = 0;
            let totalPaidUSD = 0;
            let totalCommissionUSD = 0;


            let totalAmountTHB = 0;
            let totalPriceTHB = 0;
            let totalPaidTHB = 0;
            let totalCommissionTHB = 0;


            let totalAmountKHR = 0;
            let totalPriceKHR = 0;
            let totalPaidKHR = 0;
            let totalCommissionKHR = 0;

            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";
            let i = 1;
            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                            <td>${obj.clientTypeDoc && obj.clientTypeDoc.name || ""}</td>
                            <td>${obj.clientTypeDoc && obj.clientTypeDoc.commission || ""}%</td>

                            <td style="text-align: left !important;">${moment(obj.disbursementDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${obj.purposeDoc && obj.purposeDoc.name || ""}</td>

                            <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(obj.price, obj.currencyId))}</td>
                            <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(obj.paid, obj.currencyId))}</td>
                            <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(obj.loanAmount, obj.currencyId))}</td>
                            <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(obj.commission, obj.currencyId))}</td>
                            <td style="text-align: left !important;">${obj.productDoc.rate} %</td>

                            <td style="text-align: left !important;">${obj.term}</td>
                            <td style="text-align: left !important;">${obj.productDoc.name}</td>
                                                        <td style="text-align: left !important;">${obj.currencyId}</td>

                            <td style="text-align: left !important;">${obj.productDoc.rateType}</td>
                        
                            <td style="text-align: left !important;">${obj.creditOfficerDoc.name}</td>
                            <td>${obj.userDoc && obj.userDoc.username || ""}</td>


                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {

                        totalAmountUSD += obj.loanAmount;
                        totalPriceUSD += obj.price;
                        totalPaidUSD += obj.paid;
                        totalCommissionUSD += obj.commission || 0;
                    } else if (obj.currencyId === "KHR") {
                        totalAmountKHR += obj.loanAmount;
                        totalPriceKHR += obj.price;
                        totalPaidKHR += obj.paid;
                        totalCommissionKHR += obj.commission || 0;
                    } else if (obj.currencyId === "THB") {
                        totalAmountTHB += obj.loanAmount;
                        totalPriceTHB += obj.price;
                        totalPaidTHB += obj.paid;
                        totalCommissionTHB += obj.commission || 0;
                    }
                }
            })


            let grandTotalAmount = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalAmountUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalAmountTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalAmountKHR, exchange);
            let grandTotalPrice = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPriceUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPriceTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPriceKHR, exchange);
            let grandTotalPaid = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalPaidUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalPaidTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalPaidKHR, exchange);
            let grandTotalCommission = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalCommissionUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalCommissionTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalCommissionKHR, exchange);


            dataHtml += `
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPriceUSD, "USD"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPaidUSD, "USD"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalAmountUSD, "USD"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalCommissionUSD, "USD"))}</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPriceKHR, "KHR"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPaidKHR, "KHR"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalAmountKHR, "KHR"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalCommissionKHR, "KHR"))}</td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>

                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPriceTHB, "THB"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalPaidTHB, "THB"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalAmountTHB, "THB"))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(totalCommissionTHB, "THB"))}</td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>
                                      <td></td>

                </tr> 
                <tr>
                    <td colspan="7" style="text-align: right">${t[language]['grandTotal']}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(grandTotalPrice, companyDoc.baseCurrency))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(grandTotalPaid, companyDoc.baseCurrency))}</td>
                    <td >${GlobalFn.seperateNumber(GlobalFn.formatCurrency(grandTotalAmount, companyDoc.baseCurrency))}</td>
                    <td>${GlobalFn.seperateNumber(GlobalFn.formatCurrency(grandTotalCommission, companyDoc.baseCurrency))}</td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>
                                        <td></td>

                </tr>
        `;


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
