import {Meteor} from 'meteor/meteor';
import {Sb_LoanConfigReact,Sb_LoanConfig} from "../../imports/collections/sb_loanConfig";

if (Meteor.isServer) {
    Meteor.publish('sb_loanConfigReact', function () {
        if (this.userId) {
            return Sb_LoanConfigReact.find({});
        }
        return this.ready();

    });
    Meteor.publish('sb_loanConfig', function () {
        if (this.userId) {
            return Sb_LoanConfig.find({});
        }
        return this.ready();

    });
}
