import {Mongo} from "meteor/mongo";

export const Sb_PaymentType = new Mongo.Collection("sb_paymentType");
Sb_PaymentType.schema = new SimpleSchema({
    name: {
        type: String,
        label: "Name",
    },
    chartAccountId: {
        type: String,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_PaymentType.attachSchema(Sb_PaymentType.schema);


