import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchPayOffReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;
            }

            selector.status = {$in: ["Pay Off"]};

            selector.payOffDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};
            let companyDoc = Company.findOne({});

            let dataList = Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        payOffDate: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_client",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "coId",
                        foreignField: "_id",
                        as: "creditOfficerDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$creditOfficerDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_loanProduct",
                        localField: "productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }, {
                    $lookup:
                        {
                            from: "sb_purpose",
                            localField: "purposeId",
                            foreignField: "_id",
                            as: "purposeDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$purposeDoc",
                        preserveNullAndEmptyArrays: true
                    }
                }

            ], {allowDiskUse: true});


            let dataHtml = "";


            let totalAmountUSD = 0;
            let totalFeeUSD = 0;
            let totalInterestUSD = 0;
            let totalProjectOperationUSD = 0;


            let totalAmountTHB = 0;
            let totalFeeTHB = 0;
            let totalInterestTHB = 0;
            let totalProjectOperationTHB = 0;


            let totalAmountKHR = 0;
            let totalFeeKHR = 0;
            let totalInterestKHR = 0;
            let totalProjectOperationKHR = 0;

            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";
            let i = 1;
            dataList.forEach((obj) => {
                if (obj) {
                    dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                            <td style="text-align: left !important;">${obj.creditOfficerDoc.name}</td>
                            <td style="text-align: left !important;">${obj.productDoc.name}</td>
                            <td style="text-align: left !important;">${obj.currencyId}</td>
                            <td style="text-align: left !important;">${obj.productDoc.rateType}</td>
                            <td style="text-align: left !important;">${moment(obj.disbursementDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${moment(obj.maturityDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${moment(obj.payOffDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>

                            <td style="text-align: left !important;">${obj.term}</td>
                            <td style="text-align: left !important;">${obj.productDoc.rate} %</td>
                            <td style="text-align: left !important;">${obj.clientDoc.loanCycle}</td>
                            <td style="text-align: left !important;">${obj.clientDoc.address}</td>
                            <td style="text-align: left !important;">${obj.purposeDoc && obj.purposeDoc.name || ""}</td>
                            <td>${GlobalFn.formatCurrency(obj.loanAmount, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.projectInterest, obj.currencyId)}</td>
                            <td style="display: ${displayFee}">${GlobalFn.formatCurrency(obj.projectOperation, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.feeAmount, obj.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency((obj.feeAmount + obj.projectInterest + (obj.projectOperation || 0) + obj.loanAmount), obj.currencyId)}</td>
                    </tr>
            
            `;
                    i++;


                    if (obj.currencyId === "USD") {

                        totalAmountUSD += obj.loanAmount;
                        totalFeeUSD += obj.feeAmount;
                        totalInterestUSD += obj.projectInterest;
                        totalProjectOperationUSD += obj.projectOperation || 0;
                    } else if (obj.currencyId === "KHR") {
                        totalAmountKHR += obj.loanAmount;
                        totalFeeKHR += obj.feeAmount;
                        totalInterestKHR += obj.projectInterest;
                        totalProjectOperationKHR += obj.projectOperation || 0;
                    } else if (obj.currencyId === "THB") {
                        totalAmountTHB += obj.loanAmount;
                        totalFeeTHB += obj.feeAmount;
                        totalInterestTHB += obj.projectInterest;
                        totalProjectOperationTHB += obj.projectOperation || 0;
                    }
                }
            })


            let grandTotalAmount = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalAmountUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalAmountTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalAmountKHR, exchange);
            let grandTotalFee = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalFeeUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalFeeTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalFeeKHR, exchange);
            let grandTotalInterest = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalInterestUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalInterestTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalInterestKHR, exchange);
            let grandTotalOperation = GlobalFn.exchange("USD", companyDoc.baseCurrency, totalProjectOperationUSD, exchange) + GlobalFn.exchange("THB", companyDoc.baseCurrency, totalProjectOperationTHB, exchange) + GlobalFn.exchange("KHR", companyDoc.baseCurrency, totalProjectOperationKHR, exchange);


            dataHtml += `
                <tr>
                    <td colspan="15" style="text-align: right">${t[language]['grandTotalUSD']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestUSD, "USD")}</td>
                    <td  style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeUSD, "USD")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountUSD + totalFeeUSD + totalInterestUSD + totalProjectOperationUSD, "USD")}</td>
                  
                </tr> 
                <tr>
                    <td colspan="15" style="text-align: right">${t[language]['grandTotalKHR']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestKHR, "KHR")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeKHR, "KHR")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountKHR + totalFeeKHR + totalInterestKHR + totalProjectOperationKHR, "KHR")}</td>
                  
                </tr> 
                <tr>
                    <td colspan="15" style="text-align: right">${t[language]['grandTotalTHB']}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalInterestTHB, "THB")}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(totalProjectOperationTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalFeeTHB, "THB")}</td>
                    <td>${GlobalFn.formatCurrency(totalAmountTHB + totalFeeTHB + totalInterestTHB + totalProjectOperationTHB, "THB")}</td>
                  
                </tr> 
                <tr>
                    <td colspan="15" style="text-align: right">${t[language]['grandTotal']}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalAmount, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalInterest, companyDoc.baseCurrency)}</td>
                    <td style="display: ${displayFee}">${GlobalFn.formatCurrency(grandTotalOperation, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalFee, companyDoc.baseCurrency)}</td>
                    <td>${GlobalFn.formatCurrency(grandTotalAmount + grandTotalInterest + grandTotalFee + grandTotalOperation, companyDoc.baseCurrency)}</td>
                  
                </tr>
        `;


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
