import {Meteor} from 'meteor/meteor';
import {Sb_DepositReact} from "../../imports/collections/sb_deposit";

if (Meteor.isServer) {
    Meteor.publish('sb_depositReact', function () {
        if (this.userId) {
            return Sb_DepositReact.find({});
        }
        return this.ready();

    });
}
