import {Meteor} from 'meteor/meteor';
import {Sb_PenaltyReact} from "../../imports/collections/sb_penalty";

if (Meteor.isServer) {
    Meteor.publish('sb_penaltyReact', function () {
        if (this.userId) {
            return Sb_PenaltyReact.find({});
        }
        return this.ready();

    });
}
