import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";

import {Sb_OpenAccount} from "../../../imports/collections/sb_openAccount";
import {Sb_Withdraw} from "../../../imports/collections/sb_withdraw";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchWithdrawReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
            }
            if (params.staffId && params.staffId !== "") {
                selector.staffId = params.staffId;
            }


            selector.withdrawDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};


            let companyDoc = Company.findOne();
            let exchange = Exchange.findOne({_id: params.exchangeId});

            let coefficientPrinciple = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$principleWithdraw',
                baseCurrency: companyDoc.baseCurrency
            });

            let coefficientInterest = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$interestWithdraw',
                baseCurrency: companyDoc.baseCurrency
            });
            let coefficientAmount = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$amountWithdraw',
                baseCurrency: companyDoc.baseCurrency
            });
            selector.type = {$ne: 'continue'};
            selector.withdrawStatus = "Success";

            let dataList = Sb_Withdraw.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        khName: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_clientSaving",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "staffId",
                        foreignField: "_id",
                        as: "staffDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$staffDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_savingProduct",
                        localField: "productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $project: {
                        type: 1,
                        withdrawDate: 1,
                        savingAcc: 1,
                        voucherNo: 1,
                        currencyId: 1,
                        clientDoc: 1,
                        staffDoc: 1,
                        productDoc: 1,
                        interestWithdraw: 1,
                        principleWithdraw: 1,
                        amountWithdraw: 1,
                        interestWithdrawBase: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientInterest.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientInterest.KHR, coefficientInterest.THB]}
                            }
                        },
                        principleWithdrawBase: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientPrinciple.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientPrinciple.KHR, coefficientPrinciple.THB]}
                            }
                        },
                        amountWithdrawBase: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientAmount.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientAmount.KHR, coefficientAmount.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: null,
                        data: {$addToSet: "$$ROOT"},
                        totalInterest: {$sum: "$interestWithdrawBase"},
                        totalPrinciple: {$sum: "$principleWithdrawBase"},
                        totalAmount: {$sum: "$amountWithdrawBase"},
                    }
                }

            ], {allowDiskUse: true});


            let dataHtml = "";
            let ind = 1;
            if (dataList[0]) {
                dataList[0].data.forEach((obj) => {
                    if (obj) {


                        dataHtml += `
                            <tr>
                                <td style="text-align: left !important;">${ind}</td>
                                <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                                <td style="text-align: left !important;">${obj.voucherNo || ""}</td>
                                <td style="text-align: center !important;">${obj.savingAcc}</td>
                                                                <td style="text-align: left !important;">${obj.staffDoc.name || ""}</td>
                                                                <td style="text-align: left !important;">${obj.currencyId || ""}</td>
                                                                <td style="text-align: left !important;">${GlobalFn.formatCurrency(obj.principleWithdraw, obj.currencyId)}</td>
                                                                <td style="text-align: left !important;">${GlobalFn.formatCurrency(obj.interestWithdraw, obj.currencyId)}</td>
                                                                <td style="text-align: left !important;">${GlobalFn.formatCurrency(obj.amountWithdraw, obj.currencyId)}</td>

                                                                <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.name || ""}</td>
                                                                <td style="text-align: left !important;">${obj.type || ""}</td>
                                                                <td style="text-align: left !important;">${GlobalFn.formatDate(obj.withdrawDate || "")}</td>
                                                                <td style="text-align: left !important;">${obj.clientDoc.phoneNumber || ""}</td>
                                                                <td style="text-align: left !important;">${obj.clientDoc.address || ""}</td>

                           

                            </tr>
                        `;
                        ind++;

                    }
                })
            }
            dataHtml += `<tr>
                <td colspan="5" style="text-align: center">${t[language]['total']}</td>
                <td>${companyDoc.baseCurrency}</td>
                <td style="text-align: left !important;">${GlobalFn.formatCurrency(dataList[0] && dataList[0].totalPrinciple || 0, companyDoc.baseCurrency)}</td>
                <td style="text-align: left !important;">${GlobalFn.formatCurrency(dataList[0] && dataList[0].totalInterest || 0, companyDoc.baseCurrency)}</td>
                <td style="text-align: left !important;">${GlobalFn.formatCurrency(dataList[0] && dataList[0].totalAmount || 0, companyDoc.baseCurrency)}</td>
                <td colspan="5"></td>


            </tr>`;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
