import {Mongo} from "meteor/mongo";

export const Sb_PenaltyClosing = new Mongo.Collection("sb_penaltyClosing");
Sb_PenaltyClosing.schema = new SimpleSchema({
    name: {
        type: String,
        label: "Name",
    },
    installmentTermLessThan: {
        type: Number,
        decimal: true,
        label: "Installment Term Less Than",
        optional: true
    },

    interestReminderCharge: {
        type: Number,
        decimal: true,

        label: "Interest Reminder Charge %",
    },

    interestReminderChargeIfOver: {
        type: Number,
        decimal: true,
        optional: true,
        label: "Interest Reminder Charge If Over %",
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_PenaltyClosing.attachSchema(Sb_PenaltyClosing.schema);


