import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";

import {Sb_OpenAccount} from "../../../imports/collections/sb_openAccount";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_Deposit} from "../../../imports/collections/sb_deposit";
import {Sb_Withdraw} from "../../../imports/collections/sb_withdraw";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchInvestOutstandingReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
            }

            if (params.staffId && params.staffId !== "") {
                selector.staffId = params.staffId;
            }


            selector.startDate = {$lte: params.dateAs};
            selector.$or = [
                 {status: "Approved"},
                 {
                     closeDate: {
                         $gt: params.dateAs,
                     }
                 }
             ];

            let companyDoc = Company.findOne();
            let exchange = Exchange.findOne({_id: params.exchangeId});


            let coefficientAmount = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$balance',
                baseCurrency: companyDoc.baseCurrency
            });


            let dataList = Sb_OpenAccount.aggregate([
                {
                    $match: selector
                },
                {
                    $sort: {
                        khName: 1
                    }
                },
                {
                    $lookup: {
                        from: "sb_clientSaving",
                        localField: "clientId",
                        foreignField: "_id",
                        as: "clientDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$clientDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_staff",
                        localField: "staffId",
                        foreignField: "_id",
                        as: "staffDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$staffDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup: {
                        from: "sb_savingProduct",
                        localField: "productId",
                        foreignField: "_id",
                        as: "productDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$productDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $project: {
                        _id: 1,
                        type: 1,
                        depositDate: 1,
                        savingAcc: 1,
                        currencyId: 1,
                        clientDoc: 1,
                        staffDoc: 1,
                        productDoc: 1,
                        balance: 1,
                        amountInvestOutstandingBase: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientAmount.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientAmount.KHR, coefficientAmount.THB]}
                            }
                        }
                    }
                },
                {
                    $group: {
                        _id: null,
                        data: {$addToSet: "$$ROOT"},
                        openAccountIdList: {$push: "$_id"},
                        totalAmount: {$sum: "$amountInvestOutstandingBase"},
                    }
                }

            ], {allowDiskUse: true});

            let selectorDeposit = {};
            selectorDeposit.depositDate = {$gt: params.dateAs};
            selectorDeposit.openAccountId = {$in: dataList[0].openAccountIdList || []};

            let depositAfterDate = Sb_Deposit.aggregate([
                {$match: selectorDeposit},
                {
                    $group: {
                        _id: "$openAccountId",
                        totalAmount: {$sum: "$amount"},
                    }
                }
            ], {allowDiskUse: true});

            let selectorWithdraw = {};
            selectorWithdraw.withdrawDate = {$gt: params.dateAs};
            selectorWithdraw.openAccountId = {$in: dataList[0].openAccountIdList || []};
            let withdrawAfterDate = Sb_Withdraw.aggregate([
                {$match: selectorWithdraw},
                {
                    $group: {
                        _id: "$openAccountId",
                        totalAmount: {$sum: "$principleWithdraw"},
                    }
                }
            ], {allowDiskUse: true});


            let dataHtml = "";
            let ind = 1;
            let totalMinusAmount = 0;
            if (dataList[0]) {
                dataList[0].data.forEach((obj) => {
                    if (obj) {
                        let findDeposit = function (element) {
                            if (element._id === obj._id) {
                                return element;
                            }
                        }
                        let depositDoc = depositAfterDate.find(findDeposit);

                        let findWithdraw = function (element) {
                            if (element._id === obj._id) {
                                return element;
                            }
                        }
                        let withdrawDoc = withdrawAfterDate.find(findWithdraw);

                        let minusAmount = (withdrawDoc && withdrawDoc.totalAmount || 0) - (depositDoc && depositDoc.totalAmount || 0);
                        totalMinusAmount += GlobalFn.exchange(obj.currencyId, companyDoc.baseCurrency, minusAmount, exchange);

                        dataHtml += `
                            <tr>
                                <td style="text-align: left !important;">${ind}</td>
                                <td style="text-align: left !important;">${obj.clientDoc.name}</td>
                                <td style="text-align: center !important;">${obj.savingAcc}</td>
                                                                <td style="text-align: left !important;">${obj.staffDoc.name || ""}</td>
                                                                <td style="text-align: left !important;">${obj.currencyId || ""}</td>
                                                                <td style="text-align: right !important;">${GlobalFn.formatCurrency(obj.balance + minusAmount, obj.currencyId)}</td>

                                                                <td style="text-align: left !important;">${obj.productDoc && obj.productDoc.name || ""}</td>
                                                                <td style="text-align: left !important;">${obj.clientDoc.phoneNumber || ""}</td>
                                                                <td style="text-align: left !important;">${obj.clientDoc.address || ""}</td>

                           

                            </tr>
                        `;
                        ind++;

                    }
                })
            }

            dataHtml += `<tr>
                <td colspan="4" style="text-align: center">${t[language]['total']}</td>
                <td>${companyDoc.baseCurrency}</td>
            
                <td style="text-align: right !important;">${GlobalFn.formatCurrency((dataList[0] && dataList[0].totalAmount || 0) + totalMinusAmount, companyDoc.baseCurrency)}</td>
                <td colspan="3"></td>


            </tr>`;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
