import {Mongo} from "meteor/mongo";

export const Sb_ClientType = new Mongo.Collection("sb_clientType");
Sb_ClientType.schema = new SimpleSchema({
    name: {
        type: String,
        label: "Name",
    },
    commission: {
        type: Number,
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_ClientType.attachSchema(Sb_ClientType.schema);


