import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_Journal} from "../../../imports/collections/acc_journal";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Exchange} from "../../../imports/collections/exchange";
import numeral from "numeral"
import math from "mathjs"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchClosingEntryReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {};

            let dataHtml = "";
            let companyDoc = Company.findOne({});
            let journalList = Acc_Journal.find({closingEntryId: params.closingEntryId}).fetch();

            let i = 1;
            journalList.forEach(function (doc) {


                dataHtml += `
                     <thead style="margin-top: 5px">`;

                if (doc.isCurrencyClosing === true) {
                    if (companyDoc.baseCurrency === doc.currencyId) {
                        dataHtml += `
                        <tr style="border: none !important;">
                            <th colspan="4" style="border: none !important; text-align: left !important;" >Opening Currency : ${doc.currencyId}</th>
                        </tr>
                     `;
                    } else {
                        dataHtml += `
                         <tr  style="border: none !important;">
                            <th colspan="4" style="border: none !important;;text-align: left !important;">Closing Currency : ${doc.currencyId}</th>
                        </tr>
                     `;
                    }
                } else {
                    dataHtml += `
                        <tr style="border: none !important;">
                            <th colspan="4" style="border: none !important; text-align: left !important;" >${doc.memo} (${doc.currencyId})</th>
                        </tr>
                     `;
                }


                dataHtml += `
                            <tr>
                                 <th>#</th>
                                 <th>Account (គណនី)</th>
                                 <th>Dr (ឥណពន្ធ)</th>
                                 <th>Cr (ឥណទាន)</th>
                            </tr>
                     </thead>
                     <tbody style="margin-bottom: 5px;">
                     `;

                let i = 1;
                doc.transaction.forEach(function (obj) {

                    let chartAccDoc = Acc_ChartAccount.findOne({_id: obj.account});

                    dataHtml += `
                    <tr>
                        <td style="text-align: left !important;">${i}</td>
                        <td style="text-align: left !important;">${chartAccDoc.code} : ${chartAccDoc.name}</td>
                        <td>${GlobalFn.formatCurrency(obj.dr, doc.currencyId)}</td>
                        <td>${GlobalFn.formatCurrency(obj.cr, doc.currencyId)}</td>
                    </tr>
                `;
                    i++;
                })
                dataHtml += `
                        <tr>
                            <td colspan="2" style="text-align: center !important;">Total</td>
                            <td>${GlobalFn.formatCurrency(doc.total, doc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(doc.total, doc.currencyId)}</td>
                        </tr>
                     </tbody>  
            `;

            })


            data.dataHtml = dataHtml;
            data.closingEntryDate = "";
            if (journalList.length > 0) {
                data.closingEntryDate = moment(journalList[0].journalDate,"YYYY-MM-DD").format("DD MMM YYYY");
            }

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
