import {Meteor} from 'meteor/meteor';
import {Acc_ClosingEntryReact} from "../../imports/collections/acc_closingEntry";

if (Meteor.isServer) {
    Meteor.publish('acc_closingEntryReact', function () {
        if (this.userId) {
            return Acc_ClosingEntryReact.find({});
        }
        return this.ready();

    });
}
