import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {Sb_CoPlan, Sb_CoPlanReact, Sb_CoPlanAudit} from "../../imports/collections/sb_coPlan"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchCoPlan({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken,coId}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        year: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        note: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            selector.coId = coId;
            data.content = Sb_CoPlan.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                    {
                        $lookup: {
                            from: "sb_staff",
                            localField: "coId",
                            foreignField: "_id",
                            as: "staffDoc"
                        }
                    },
                    {
                        $unwind: {
                            path: "$staffDoc",
                            preserveNullAndEmptyArrays: true
                        }
                    }
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_CoPlan.find(selector).count();
            return data;
        }
    },
    sb_insertCoPlan(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_CoPlan.insert(doc);
                if (id) {
                    GlobalFn.collectionReact(Sb_CoPlanReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error("Duplicate year! Error");
            }
        }

    },
    sb_updateCoPlan(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_CoPlan.findOne({_id: id});
                let isUpdated = Sb_CoPlan.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_CoPlanReact, id, Sb_CoPlanAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeCoPlan(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_CoPlan.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_CoPlanReact, doc._id, Sb_CoPlanAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findCoPlanById(id, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};

                selector._id = id;
                return Sb_CoPlan.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findCoPlanByIdList(idList, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = {$in: idList};
                return Sb_CoPlan.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchCoPlanOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }


                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {phoneNumber: {$regex: reg}},
                        {_id: q}
                    ];
                }

                return Sb_CoPlan.find(selector, {sort: {createdAt: -1}, limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + (obj.date || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_CoPlan._ensureIndex({
    coId: 1,
    year: 1,
    branchId: 1
}, {unique: 1, name: "Sb_CoPlanUnique"});