import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Sb_Disbursement} from "../../../imports/collections/sb_disbursement";
import {Exchange} from "../../../imports/collections/exchange";
import {Sb_LoanConfig} from "../../../imports/collections/sb_loanConfig";
import {Sb_Client} from "../../../imports/collections/sb_client";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchPayOffUnDisbursementReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let selectorConfig = {};
            let data = {};
            let selectorClient = {};
            let selectorClient2 = {};

            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
                selectorClient.branchId = {$in: params.branch || []};
                selectorConfig.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
                selectorClient.branchId = {$in: userDoc.branch || []};
                selectorConfig.branchId = {$in: userDoc.branch || []};
            }

            let configDoc = Sb_LoanConfig.findOne(selectorConfig);
            let exchange = Exchange.findOne({_id: params.exchangeId});


            if (params.staffId && params.staffId !== "") {
                selector.coId = params.staffId;
                selectorClient2["disbursementDoc.coId"] = params.staffId;
            }

            selector.disbursementDate = {$lte: params.dateAs};

            selector.$and = [
                {
                    $or: [
                        {status: "Active"},
                        {
                            payOffDate: {
                                $gt: params.dateAs,
                            }
                        },
                        {
                            writeOffDate: {
                                $gt: params.dateAs,
                            }
                        }
                    ]
                },
                {
                    $or: [
                        {status: {$ne: "Write Off"}},
                        {
                            writeOffDate: {
                                $gt: params.dateAs,
                            }
                        }
                    ]
                },
                {
                    status: {$nin: ["Processing", "Rejected", "Analyst Approved", "CFO Approved"]}
                }
            ];
            let companyDoc = Company.findOne({});

            let dataList = Sb_Disbursement.aggregate([
                {
                    $match: selector
                },
                {
                    $project: {
                        clientId: 1
                    }
                }

            ], {allowDiskUse: true}).map(d => d.clientId);

            selectorClient.loanCycle = {$gt: 0};
            selectorClient._id = {$nin: dataList};
            let clientList = Sb_Client.aggregate([
                {
                    $match: selectorClient
                },
                {
                    $lookup:
                        {
                            from: "sb_disbursement",
                            localField: "_id",
                            foreignField: "clientId",
                            as: "disbursementDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$disbursementDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $sort: {
                        "disbursementDoc.disbursementDate": 1
                    }
                }, {
                    $group: {
                        _id: "$_id",
                        disbursementDoc: {$last: "$disbursementDoc"},
                        name: {$last: "$name"},
                        address: {$last: "$address"},
                        phoneNumber: {$last: "$phoneNumber"},
                        loanCycle: {$last: "$loanCycle"},
                    }
                }, {
                    $lookup:
                        {
                            from: "sb_staff",
                            localField: "disbursementDoc.coId",
                            foreignField: "_id",
                            as: "staffDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$staffDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {$match: selectorClient2}
            ], {allowDiskUse: true});
            let dataHtml = "";
            let i = 1;
            let displayFee = configDoc && configDoc.isOperationFee === true ? "" : "none";

            clientList.forEach((obj) => {
                if (obj) {
                    if (obj.disbursementDoc) {
                        dataHtml += `
                    <tr>
                            <td style="text-align: center !important;">${i}</td>
                            <td style="text-align: left !important;">${obj.disbursementDoc.loanAcc}</td>
                            <td style="text-align: left !important;">${obj.name}</td>
                            <td style="text-align: left !important;">${obj.staffDoc && obj.staffDoc.name || ""}</td>
                            <td style="text-align: left !important;">${obj.disbursementDoc && obj.disbursementDoc.currencyId || ""}</td>
                            <td style="text-align: left !important;">${moment(obj.disbursementDoc.disbursementDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${moment(obj.disbursementDoc.maturityDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>
                            <td style="text-align: left !important;">${moment(obj.disbursementDoc.payOffDate, "YYYY-MM-DD").format("DD/MM/YYYY")}</td>

                       
                            <td style="text-align: left !important;">${obj.loanCycle}</td>
                            <td style="text-align: left !important;">${obj.address}</td>
                            <td>${GlobalFn.formatCurrency(obj.disbursementDoc.loanAmount, obj.disbursementDoc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.disbursementDoc.projectInterest, obj.disbursementDoc.currencyId)}</td>
                            <td style="display: ${displayFee}">${GlobalFn.formatCurrency(obj.disbursementDoc.projectOperation, obj.disbursementDoc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.disbursementDoc.feeAmount, obj.disbursementDoc.currencyId)}</td>
                    </tr>
            
            `;
                        i++;
                    }

                }
            })

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
