import {Mongo} from "meteor/mongo";

export const Sb_SavingProduct= new Mongo.Collection("sb_savingProduct");
Sb_SavingProduct.schema = new SimpleSchema({
    normalRate: {
        type: Number,
        decimal: true,
    },
    fixedRate: {
        type: Number,
        decimal: true,
    },
    rateType: {
        type: "String",
        label: "Rate Type"
    },
    productDate: {
        type: String,
        label: "Product Date",
        optional:true
    },

    status: {
        type: Boolean,
        defaultValue: false,
    },

    name: {
        type: String,
        label: "Name",
    },

    description: {
        type: String,
        label: "Description",
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_SavingProduct.attachSchema(Sb_SavingProduct.schema);


