import {Meteor} from 'meteor/meteor';
import {Sb_OpenAccountReact} from "../../imports/collections/sb_openAccount";

if (Meteor.isServer) {
    Meteor.publish('sb_openAccountReact', function () {
        if (this.userId) {
            return Sb_OpenAccountReact.find({});
        }
        return this.ready();

    });
}
