import {Mongo} from "meteor/mongo";

export const Sb_TmpDis = new Mongo.Collection("sb_tmpDis");
Sb_TmpDis.schema = new SimpleSchema({
    clientId: {
        type: String,
        label: "Client",
    },
    productId: {
        type: String,
        label: "Product",
    },
    currencyId: {
        type: String,
        label: "Currency",
    },
    loanAmount: {
        type: Number,
        decimal: true,
        label: "Amount",
    },

    paymentPerMonth: {
        type: Number,
        decimal: true,
    },

    term: {
        type: Number
    },

    disbursementDate: {
        type: String,
    },


    coId: {
        type: String,
        label: "Credit Officer",
    },


    numInstallmentPaid: {
        type: Number,
        optional: true,
        defaultValue: 0
    },

    dateToPay: {
        type: Number,
        optional: true
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_TmpDis.attachSchema(Sb_TmpDis.schema);

