import {Mongo} from "meteor/mongo";

export const Sb_ReverseAccount = new Mongo.Collection("sb_reverseAccount");
Sb_ReverseAccount.schema = new SimpleSchema({
    openAccountId: {
        type: String,
        label: "Name"
    },
    currencyId: {
        type: String,
    },
    reverseDate: {
        type: String,
    },
    penalty: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    balance: {
        type: Number,
        decimal: true,
        optional: true,
        defaultValue: 0
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_ReverseAccount.attachSchema(Sb_ReverseAccount.schema);


