import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_SavingConfig, Sb_SavingConfigReact, Sb_SavingConfigAudit} from "../../imports/collections/sb_savingConfig";
import {Branch} from "../../imports/collections/branch";
import {Sb_OpenAccount} from "../../imports/collections/sb_openAccount";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchSavingConfig({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_SavingConfig.aggregate([
                {
                    $match: selector
                }
                ,
                {
                    $sort: sortObj
                },

                {
                    $limit: options.limit
                },
                {
                    $skip: options.skip
                },

                {
                    $lookup: {
                        from: "branch",
                        localField: "branchId",
                        foreignField: "_id",
                        as: "branchDoc"
                    }
                },
                {
                    $unwind: {
                        path: "$branchDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
            ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_SavingConfig.find(selector).count();
            return data;
        }
    },
    sb_insertSavingConfig(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_SavingConfig.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_SavingConfigReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateSavingConfig(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_SavingConfig.findOne({_id: id});
                let isUpdated = Sb_SavingConfig.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_SavingConfigReact, id, Sb_SavingConfigAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removeSavingConfig(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_SavingConfig.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_SavingConfigReact, doc._id, Sb_SavingConfigAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

})

//Unique

Sb_SavingConfig._ensureIndex({
    branchId: 1
}, {unique: 1, name: "Sb_SavingConfigUnique"});