import {Meteor} from 'meteor/meteor';
import GlobalFn from "../../imports/libs/globalFn"
import {
    Sb_SavingProduct,
    Sb_SavingProductReact,
    Sb_SavingProductAudit
} from "../../imports/collections/sb_savingProduct"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchSavingProduct({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {
                    selector.$or = [{name: {$regex: reg, $options: 'mi'}}, {
                        productDate: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        rateType: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        methodType: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        rate: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        status: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }, {
                        description: {
                            $regex: reg,
                            $options: 'mi'
                        }
                    }];
                }
            }
            selector.branchId = branchId;
            data.content = Sb_SavingProduct.aggregate([
                    {
                        $match: selector
                    }
                    ,
                    {
                        $sort: sortObj
                    },

                    {
                        $limit: options.limit
                    },
                    {
                        $skip: options.skip
                    },
                ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_SavingProduct.find(selector).count();
            return data;
        }
    },
    sb_insertSavingProduct(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_SavingProduct.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_SavingProductReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updateSavingProduct(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_SavingProduct.findOne({_id: id});
                let isUpdated = Sb_SavingProduct.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_SavingProductReact, id, Sb_SavingProductAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_removeSavingProduct(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_SavingProduct.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_SavingProductReact, doc._id, Sb_SavingProductAudit, doc, "Remove");
                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findSavingProduct(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector.status = true;
                return Sb_SavingProduct.find(selector, {sort: {name: 1}}).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findSavingProductById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_SavingProduct.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_fetchSavingProductOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }

                if (q && q !== "" && q !== undefined && q !== null) {
                    q = q.replace(/[/\\]/g, '');
                    let reg = new RegExp(q, 'mi');

                    selector.$or = [
                        {name: {$regex: reg}},
                        {_id: q}
                    ];
                }
                selector.status = true;

                return Sb_SavingProduct.find(selector, {limit: 300}).fetch().map(obj => ({
                    label: (obj.name || "") + " : " + obj.rateType + " : Fixed Rate=" + (obj.fixedRate || "") + "% , Normal Rate=" + (obj.normalRate || "") + "% ",
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})


//Unique

Sb_SavingProduct._ensureIndex({
    name: 1,
    status: 1,
    rate: 1,
    rateType: 1,
    branchId: 1
}, {unique: 1, name: "Sb_SavingProductUnique"});