import {Mongo} from "meteor/mongo";

export const Sb_Withdraw = new Mongo.Collection("sb_withdraw");
Sb_Withdraw.schema = new SimpleSchema({
    clientId: {
        type: String,
    },
    openAccountId: {
        type: String,
    },
    staffId: {
        type: String,
    },
    productId: {
        type: String,
    },
    savingAcc: {
        type: String,
        optional: true
    },
    voucherNo: {
        type: String,
        optional: true
    },

    currencyId: {
        type: String,
        optional: true
    },
    amountWithdraw: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    principleWithdraw: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    interestWithdraw: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },
    //Interest Calculate Now
    rawInterest: {
        type: Number,
        decimal: true,
        defaultValue: 0,
        optional: true
    },

    note: {
        type: String,
        optional: true
    },
    type: {
        type: String,
        optional: true
    },
    withdrawDate: {
        type: String,
        optional: true
    },
    status: {
        type: Boolean,
        optional: true,
        defaultValue: false
    },
    withdrawHistory: {
        type: [Object],
        blackbox: true,
        optional: true
    },

    withdrawStatus: {
        type: String,
        optional: true,
        defaultValue: "Processing"

        //Processing
        //Analyst Approved
        //CFO Approved
        //Success
        //Rejected
    },
    stepper: {
        type: Number,
        optional: true,
    },
    analyst: {
        type: String,
        optional: true,
    },
    analystAt: {
        type: String,
        optional: true,
    },
    analystComment: {
        type: String,
        optional: true,
    },
    cfo: {
        type: String,
        optional: true,
    },
    cfoAt: {
        type: String,
        optional: true,
    },
    cfoComment: {
        type: String,
        optional: true,
    },
    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Withdraw.attachSchema(Sb_Withdraw.schema);


