import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_Journal} from "../../../imports/collections/acc_journal";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Acc_AccountType} from "../../../imports/collections/acc_accountType";
import {Exchange} from "../../../imports/collections/exchange";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchProfitLostReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let selector = {};
            let data = {};


            if (params.branch !== "" && params.branch.length > 0) {
                selector.branchId = {$in: params.branch || []};
            } else {
                let userDoc = Meteor.user();
                selector.branchId = {$in: userDoc.branch || []};
            }


            if (params.currencyId && params.currencyId !== "") {
                selector.currencyId = params.currencyId;
            }

            let exchange = Exchange.findOne({_id: params.exchangeId});
            let companyDoc = Company.findOne({});

            let coefficientDrCr = GlobalFn.exchangeCoefficient({
                exchange,
                fieldToCalculate: '$transaction.drcr',
                baseCurrency: companyDoc.baseCurrency
            });


            let selectorChartAccount = {};
            selectorChartAccount["accountDoc.accountTypeId"] = {$in: ["50", "51", "60", "61", "70"]};


            selector.journalDate = {$gte: params.dateRange[0], $lte: params.dateRange[1]};

            let dataHtml = "";

            //Range Date
            let journalList = Acc_Journal.aggregate([

                {
                    $match: selector
                },
                {
                    $project: {
                        _id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: "$transaction"
                    }

                },
                {
                    $unwind: {
                        path: "$transaction",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $lookup:
                        {
                            from: "acc_chartAccount",
                            localField: "transaction.account",
                            foreignField: "_id",
                            as: "accountDoc"
                        }
                },
                {
                    $unwind: {
                        path: "$accountDoc",
                        preserveNullAndEmptyArrays: true
                    }
                },
                {
                    $match: selectorChartAccount
                }
                ,
                {
                    $project: {
                        id: "$_id",
                        currencyId: "$currencyId",
                        journalDate: "$journalDate",
                        total: "$total",
                        voucherId: "$voucherId",
                        memo: "$memo",
                        transaction: "$transaction",
                        copyTransaction: "$copyTransaction",
                        accountDoc: "$accountDoc",
                        drcr: {
                            $cond: {
                                if: {$eq: ['$currencyId', 'USD']},
                                then: coefficientDrCr.USD,
                                else: {$cond: [{$eq: ['$currencyId', 'KHR']}, coefficientDrCr.KHR, coefficientDrCr.THB]}
                            }
                        }
                    }
                }, {
                    $project: {
                        id: 1,
                        currencyId: 1,
                        journalDate: 1,
                        total: 1,
                        voucherId: 1,
                        memo: 1,
                        transaction: 1,
                        copyTransaction: 1,
                        accountDoc: 1,
                        drcr: {$cond: [{$in: ["$accountDoc.accountTypeId", ["30", "32", "34", "36", "40", "41", "50", "51"]]}, {$multiply: ["$drcr", -1]}, "$drcr"]}
                    }
                },
                {$sort: {"accountDoc.code": -1}},
                {
                    $group: {
                        _id: {
                            accountDoc: "$accountDoc"
                        },
                        data: {$addToSet: "$$ROOT"},
                        total: {$sum: "$drcr"},
                    }
                }
            ], {allowDiskUse: true})

            let selectorProfitLostChartAccount = {};
            selectorProfitLostChartAccount.accountTypeId = {$in: ["50", "51", "60", "61", "70"]};

            let chartAccountParentList = Acc_ChartAccount.aggregate([
                {
                    $match: selectorProfitLostChartAccount
                },
                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$_id",
                        connectFromField: "_id",
                        connectToField: "subAccountOf",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "childList"
                    }
                }, {
                    $sort: {code: 1}
                }
            ])


            let chartAccountList = Acc_ChartAccount.aggregate([
                {
                    $match: selectorProfitLostChartAccount
                },
                {
                    $graphLookup: {
                        from: "acc_chartAccount",
                        startWith: "$subAccountOf",
                        connectFromField: "subAccountOf",
                        connectToField: "_id",
                        maxDepth: 3,
                        depthField: "numConnections",
                        as: "parentDoc"
                    }
                }, {
                    $sort: {code: 1}
                }
            ], {allowDiskUse: true});

            let totalIncome = 0;
            let totalCOGS = 0;
            let totalExpense = 0
            let totalOtherIncome = 0
            let totalOtherExpense = 0

            let AccountTypeList = Acc_AccountType.find({_id: {$in: ["50", "51", "60", "61", "70"]}});

            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;" colspan="2">INCOME (ចំណូល)</th>
                    </tr>
                    `;

            AccountTypeList.forEach(function (obj) {
                if (["50"].indexOf(obj._id) > -1) {
                    let accountList = chartAccountList.filter(function (newDoc) {
                        if (newDoc.accountTypeId === obj._id) {
                            return newDoc;
                        }
                    });

                    accountList.forEach(function (chartAccDoc) {

                        let findJournalDocByChartAccountId = function (element) {
                            if (element._id.accountDoc._id === chartAccDoc._id) {
                                return element;
                            }
                        }
                        let journalDoc = journalList.find(findJournalDocByChartAccountId);


                        let totalParent = 0;
                        let totalAccount = 0;


                        if (journalDoc) {
                            if (journalDoc && journalDoc.total !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                totalAccount += journalDoc.total;
                                totalIncome += journalDoc.total;
                                dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalAccount, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `;
                            }
                        } else {

                            let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                            if (childListDoc && childListDoc.childList.length > 0) {
                                let childListDocElement = childListDoc.childList.map(function (obj) {
                                    return obj._id;
                                })

                                //Rank
                                let findJournalDocByParentId = function (element) {
                                    if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                        totalParent += element.total;
                                    }
                                }
                                journalList.find(findJournalDocByParentId);
                            }
                            if (totalParent !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                if (chartAccDoc.level === 0) {
                                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `
                                } else {
                                    dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `
                                }
                            }
                        }

                    })
                }
            })


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">TOTAL INCOME (សរុបចំណូល)</th>
                        <th>${GlobalFn.formatCurrency(totalIncome, companyDoc.baseCurrency)}</th>
                    </tr>
                `;


            let cogsInc = 1;

            AccountTypeList.forEach(function (obj) {
                if (["70"].indexOf(obj._id) > -1) {
                    let accountList = chartAccountList.filter(function (newDoc) {
                        if (newDoc.accountTypeId === obj._id) {
                            return newDoc;
                        }
                    });
                    if (accountList.length > 0) {
                        if (cogsInc === 1) {
                            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;" colspan="2">COST OF GOODS SOLD (ចំណាយថ្លៃដើម)</th>
                    </tr>
                    `;
                        }
                        cogsInc++;
                    }

                    accountList.forEach(function (chartAccDoc) {

                        let findJournalDocByChartAccountId = function (element) {
                            if (element._id.accountDoc._id === chartAccDoc._id) {
                                return element;
                            }
                        }
                        let journalDoc = journalList.find(findJournalDocByChartAccountId);
                        let totalParent = 0;
                        let totalAccount = 0;


                        if (journalDoc) {
                            if (journalDoc && journalDoc.total !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                totalAccount += journalDoc.total;
                                totalCOGS += journalDoc.total;
                                dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalAccount, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `;
                            }

                        } else {
                            let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                            if (childListDoc && childListDoc.childList.length > 0) {
                                let childListDocElement = childListDoc.childList.map(function (obj) {
                                    return obj._id;
                                })

                                //Rank
                                let findJournalDocByParentId = function (element) {
                                    if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                        totalParent += element.total;
                                    }
                                }
                                journalList.find(findJournalDocByParentId);
                            }
                            if (totalParent !== 0) {

                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;


                                if (chartAccDoc.level === 0) {
                                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `
                                } else {
                                    dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `
                                }

                            }
                        }

                    })
                }
            })

            if (cogsInc > 1) {
                dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">TOTAL COST OF GOODS SOLD​ (សរុបចំណាយថ្លៃដើម)</th>
                        <th>${GlobalFn.formatCurrency(totalCOGS, companyDoc.baseCurrency)}</th>
                    </tr>
                `;
                dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">GROSS PROFIT (ចំណេញដុល)</th>
                        <th>${GlobalFn.formatCurrency(totalIncome - totalCOGS, companyDoc.baseCurrency)}</th>
                    </tr>
                `;
            }


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;" colspan="2">EXPENSE (ចំណាយ)</th>
                    </tr>
                    `;
            AccountTypeList.forEach(function (obj) {
                if (["60"].indexOf(obj._id) > -1) {
                    let accountList = chartAccountList.filter(function (newDoc) {
                        if (newDoc.accountTypeId === obj._id) {
                            return newDoc;
                        }
                    });

                    accountList.forEach(function (chartAccDoc) {

                        let findJournalDocByChartAccountId = function (element) {
                            if (element._id.accountDoc._id === chartAccDoc._id) {
                                return element;
                            }
                        }
                        let journalDoc = journalList.find(findJournalDocByChartAccountId);
                        let totalParent = 0;
                        let totalAccount = 0;


                        if (journalDoc) {
                            if (journalDoc && journalDoc.total !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                totalAccount += journalDoc.total;
                                totalExpense += journalDoc.total;
                                dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalAccount, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `;

                            }
                        } else {
                            let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                            if (childListDoc && childListDoc.childList.length > 0) {
                                let childListDocElement = childListDoc.childList.map(function (obj) {
                                    return obj._id;
                                })

                                //Rank
                                let findJournalDocByParentId = function (element) {
                                    if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                        totalParent += element.total;
                                    }
                                }
                                journalList.find(findJournalDocByParentId);
                            }

                            if (totalParent !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                if (chartAccDoc.level === 0) {
                                    dataHtml += `<th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                            </tr>
                        `
                                } else {
                                    dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `
                                }
                            }
                        }

                    })
                }
            })

            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">TOTAL EXPENSE (សរុបចំណាយ)</th>
                        <th>${GlobalFn.formatCurrency(totalExpense, companyDoc.baseCurrency)}</th>
                    </tr>
                `;
            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">NET OPERATING INCOME (ចំណេញក្រោយដកចំណាយប្រតិបត្តការ)</th>
                        <th>${GlobalFn.formatCurrency(totalIncome - totalCOGS - totalExpense, companyDoc.baseCurrency)}</th>
                    </tr>
                `;
            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;" colspan="2">OTHER INCOME (ចំណូលផ្សេងៗ)</th>
                    </tr>
                `;


            AccountTypeList.forEach(function (obj) {
                if (["51"].indexOf(obj._id) > -1) {
                    let accountList = chartAccountList.filter(function (newDoc) {
                        if (newDoc.accountTypeId === obj._id) {
                            return newDoc;
                        }
                    });

                    accountList.forEach(function (chartAccDoc) {

                        let findJournalDocByChartAccountId = function (element) {
                            if (element._id.accountDoc._id === chartAccDoc._id) {
                                return element;
                            }
                        }
                        let journalDoc = journalList.find(findJournalDocByChartAccountId);
                        let totalParent = 0;
                        let totalAccount = 0;


                        if (journalDoc) {
                            if (journalDoc.total !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                totalAccount += journalDoc.total;
                                totalOtherIncome += journalDoc.total;
                                dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalAccount, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `;
                            }
                        } else {
                            let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                            if (childListDoc && childListDoc.childList.length > 0) {
                                let childListDocElement = childListDoc.childList.map(function (obj) {
                                    return obj._id;
                                })

                                //Rank
                                let findJournalDocByParentId = function (element) {
                                    if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                        totalParent += element.total;
                                    }
                                }
                                journalList.find(findJournalDocByParentId);
                            }
                            if (totalParent !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                if (chartAccDoc.level === 0) {
                                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `
                                } else {
                                    dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `
                                }
                            }
                        }

                    })
                }
            })


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">TOTAL OTHER INCOME (សរុបចំណូលផ្សេងៗ)</th>
                        <th>${GlobalFn.formatCurrency(totalOtherIncome, companyDoc.baseCurrency)}</th>
                    </tr>
                `;


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;" colspan="2">OTHER EXPENSE (ចំណាយផ្សេងៗ)</th>
                    </tr>
                `;


            AccountTypeList.forEach(function (obj) {
                if (["61"].indexOf(obj._id) > -1) {
                    let accountList = chartAccountList.filter(function (newDoc) {
                        if (newDoc.accountTypeId === obj._id) {
                            return newDoc;
                        }
                    });

                    accountList.forEach(function (chartAccDoc) {

                        let findJournalDocByChartAccountId = function (element) {
                            if (element._id.accountDoc._id === chartAccDoc._id) {
                                return element;
                            }
                        }
                        let journalDoc = journalList.find(findJournalDocByChartAccountId);
                        let totalParent = 0;
                        let totalAccount = 0;


                        if (journalDoc) {
                            if (journalDoc.total !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                totalAccount += journalDoc.total;
                                totalOtherExpense += journalDoc.total;
                                dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalAccount, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `;
                            }
                        } else {
                            let childListDoc = chartAccountParentList.find((o) => o._id === chartAccDoc._id);

                            if (childListDoc && childListDoc.childList.length > 0) {
                                let childListDocElement = childListDoc.childList.map(function (obj) {
                                    return obj._id;
                                })

                                //Rank
                                let findJournalDocByParentId = function (element) {
                                    if (childListDocElement.indexOf(element._id.accountDoc._id) > -1) {
                                        totalParent += element.total;
                                    }
                                }
                                journalList.find(findJournalDocByParentId);
                            }
                            if (totalParent !== 0) {
                                dataHtml += `
                    <tr class="rowClick" id="${chartAccDoc._id}">
                                <td style="text-align: left !important;">${GlobalFn.space(12) + GlobalFn.space(chartAccDoc.level * 6) + chartAccDoc.code} : ${chartAccDoc.name}</td>`;

                                if (chartAccDoc.level === 0) {
                                    dataHtml += `
                        <th>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</th>
                    </tr>
                `
                                } else {
                                    dataHtml += `
                        <td>${GlobalFn.formatCurrency(totalParent, companyDoc.baseCurrency) + GlobalFn.space(chartAccDoc.level * 15)}</td>
                    </tr>
                `
                                }
                            }
                        }

                    })
                }
            })


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">TOTAL OTHER EXPENSE (សរុបចំណាយផ្សេងៗ)</th>
                        <th>${GlobalFn.formatCurrency(totalOtherExpense, companyDoc.baseCurrency)}</th>
                    </tr>
                `;


            dataHtml += `
                    <tr>
                        <th style="text-align: left !important;">NET INCOME (ចំណេញសុទ្ធ)</th>
                        <th>${GlobalFn.formatCurrency(totalIncome - totalCOGS - totalExpense - totalOtherExpense + totalOtherIncome, companyDoc.baseCurrency)}</th>
                    </tr>
                `;


            data.dataHtml = dataHtml;
            return data;
        }
    }
})
