import {Mongo} from "meteor/mongo";

export const Sb_Holiday = new Mongo.Collection("sb_holiday");
Sb_Holiday.schema = new SimpleSchema({
    year: {
        type: String,
        label: "EscapeDay Date",
    },
    dateList: {
        type: [String],
        label: "Date List",
    },

    description: {
        type: String,
        label: "Description",
        optional: true
    },

    branchId: {
        type: String
    },
    createdAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return moment().toDate();
            }
        }
    },
    updatedAt: {
        type: Date,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return moment().toDate();
            }
        }
    },
    createdUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isInsert) {
                return Meteor.userId();
            }
        }
    },
    updatedUser: {
        type: String,
        optional: true,

        autoValue() {
            if (this.isUpdate) {
                return Meteor.userId();
            }
        }
    }
});
Sb_Holiday.attachSchema(Sb_Holiday.schema);


