import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../imports/libs/globalFn"
import _ from 'lodash'

import {Sb_PenaltyClosing, Sb_PenaltyClosingReact, Sb_PenaltyClosingAudit} from "../../imports/collections/sb_penaltyClosing";
import {Branch} from "../../imports/collections/branch";

let secret = Meteor.settings.private.secret;
Meteor.methods({
    sb_fetchPenaltyClosing({q, filter, sort, options = {limit: 10, skip: 0}, branchId, accessToken}) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {
                content: [],
                countContent: 0,
            };
            let selector = {};
            let sortObj = {};
            if (sort.sortBy !== "") {
                let nameSort = sort.sortBy;
                sortObj[nameSort] = sort.sortDesc === true ? 1 : -1;
            } else {
                sortObj = {createdAt: -1};
            }
            if (!!q) {
                let reg = new RegExp(q);
                if (!!filter) {
                    selector[filter] = {$regex: reg, $options: 'mi'}
                } else {

                    selector.$or = [{name: {$regex: reg, $options: 'mi'}},
                        {installmentTermLessThan: {$regex: reg, $options: 'mi'}},
                        {interestReminderCharge: {$regex: reg, $options: 'mi'}},
                        {interestReminderChargeIfOver: {$regex: reg, $options: 'mi'}},
                    ];
                }
            }
            selector.branchId = branchId;


            data.content = Sb_PenaltyClosing.aggregate([
                {
                    $match: selector
                }
                ,
                {
                    $sort:sortObj
                },

                {
                    $limit: options.limit
                },
                {
                    $skip: options.skip
                }
            ],
                {
                    allowDiskUse: true
                });
            data.countContent = Sb_PenaltyClosing.find(selector).count();
            return data;
        }
    },
    sb_insertPenaltyClosing(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let id = Sb_PenaltyClosing.insert(doc);

                if (id) {
                    GlobalFn.collectionReact(Sb_PenaltyClosingReact, id);
                }
                return id;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }

    },
    sb_updatePenaltyClosing(id, doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let oldDoc = Sb_PenaltyClosing.findOne({_id: id});
                let isUpdated = Sb_PenaltyClosing.update({_id: id}, {$set: doc});
                if (isUpdated) {
                    GlobalFn.collectionReact(Sb_PenaltyClosingReact, id, Sb_PenaltyClosingAudit, oldDoc, "Update");
                }
                return isUpdated;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_removePenaltyClosing(doc, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let isRemoved = Sb_PenaltyClosing.remove({_id: doc._id});

                if (isRemoved) {
                    GlobalFn.collectionReact(Sb_PenaltyClosingReact, doc._id, Sb_PenaltyClosingAudit, doc, "Remove");

                }
                return isRemoved;
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findPenaltyClosing(branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_PenaltyClosing.find(selector).fetch();

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },
    sb_findPenaltyClosingById(id, branchId, accessToken) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                selector._id = id;
                return Sb_PenaltyClosing.findOne(selector);

            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    },

    sb_fetchPenaltyClosingOption(q, accessToken, branchId) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            try {
                let selector = {};
                if (Array.isArray(branchId)) {
                    selector.branchId = {$in: branchId};
                } else {
                    selector.branchId = branchId;
                }
                return Sb_PenaltyClosing.find(selector).fetch().map(obj => ({
                    label: (obj.name || ""),
                    value: obj._id
                }));
            } catch (e) {
                throw new Meteor.Error(e.message);
            }
        }
    }
})

//Unique

Sb_PenaltyClosing._ensureIndex({
    name: 1,
    interestReminderCharge: 1,
    installmentTermLessThan: 1,
    branchId: 1
}, {unique: 1, name: "Sb_PenaltyClosingUnique"});