    Sb_Disbursement.rawCollection().createIndex({createdAt: 1});

    //Sb_Repayment
    Sb_Repayment.rawCollection().createIndex({branchId: 1});
    Sb_Repayment.rawCollection().createIndex({clientId: 1});
    Sb_Repayment.rawCollection().createIndex({disbursementId: 1});
    Sb_Repayment.rawCollection().createIndex({type: 1});
    Sb_Repayment.rawCollection().createIndex({repaymentDate: 1});
    Sb_Repayment.rawCollection().createIndex({createdAt: 1});


