    Sb_RepaymentSchedule.rawCollection().createIndex({branchId: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({loanId: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({clientId: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({productId: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({currencyId: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({installment: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({date: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({isPaid: 1});
    Sb_RepaymentSchedule.rawCollection().createIndex({createdAt: 1});

