    Acc_Journal.rawCollection().createIndex({branchId: 1});
    Acc_Journal.rawCollection().createIndex({voucherId: 1});
    Acc_Journal.rawCollection().createIndex({currencyId: 1});
    Acc_Journal.rawCollection().createIndex({createdAt: 1});

    //Sb_Withdraw
    Sb_Withdraw.rawCollection().createIndex({branchId: 1});
    Sb_Withdraw.rawCollection().createIndex({clientId: 1});
    Sb_Withdraw.rawCollection().createIndex({openAccountId: 1});
    Sb_Withdraw.rawCollection().createIndex({staffId: 1});
    Sb_Withdraw.rawCollection().createIndex({productId: 1});
    Sb_Withdraw.rawCollection().createIndex({savingAcc: 1});
    Sb_Withdraw.rawCollection().createIndex({currencyId: 1});
    Sb_Withdraw.rawCollection().createIndex({withdrawDate: 1});
    Sb_Withdraw.rawCollection().createIndex({createdAt: 1});

    //Sb_OpenAccount
    Sb_OpenAccount.rawCollection().createIndex({branchId: 1});
    Sb_OpenAccount.rawCollection().createIndex({clientId: 1});
    Sb_OpenAccount.rawCollection().createIndex({staffId: 1});
    Sb_OpenAccount.rawCollection().createIndex({productId: 1});
    Sb_OpenAccount.rawCollection().createIndex({savingAcc: 1});
    Sb_OpenAccount.rawCollection().createIndex({currencyId: 1});
    Sb_OpenAccount.rawCollection().createIndex({startDate: 1});
    Sb_OpenAccount.rawCollection().createIndex({createdAt: 1});


