import {Meteor} from 'meteor/meteor';
import {Accounts} from "meteor/accounts-base";
import GlobalFn from "../../../imports/libs/globalFn"
import _ from 'lodash'
import {t} from "../../../imports/libs/constant";

import {Company} from "../../../imports/collections/company";
import {Branch} from "../../../imports/collections/branch";

import {Acc_FixedAsset} from "../../../imports/collections/acc_fixedAsset";
import {Acc_ChartAccount} from "../../../imports/collections/acc_chartAccount";
import {Exchange} from "../../../imports/collections/exchange";
import numeral from "numeral"
import math from "mathjs"

let secret = Meteor.settings.private.secret;
Meteor.methods({
    acc_fetchFixedAssetScheduleReport(params, accessToken, language) {
        if ((Meteor.userId() && accessToken === secret) || accessToken === secret) {
            let data = {};

            let fixedAssetDoc = Acc_FixedAsset.findOne({_id: params.fixedAssetId});
            let accountDoc = Acc_ChartAccount.findOne({_id: fixedAssetDoc.account})

            let dataHtml = "";
            if (fixedAssetDoc) {
                if (fixedAssetDoc.transaction.length > 0) {
                    let cumDeprec = 0;
                    let bvEndYear = fixedAssetDoc.value;
                    dataHtml += `
                    <tr>
                        <td colspan="7">${accountDoc.code} : ${accountDoc.name}  = ${GlobalFn.formatCurrency(fixedAssetDoc.value, fixedAssetDoc.currencyId)} ${GlobalFn.getCurrencySymbol(fixedAssetDoc.currencyId)}</td>
                    </tr>
                `;

                    fixedAssetDoc.transaction.forEach((obj) => {

                        cumDeprec += math.round(obj.perYear, 3);
                        bvEndYear -= math.round(obj.perYear, 3);
                        dataHtml += `
                        <tr>
                            <td>${obj.year}</td>
                            <td>${GlobalFn.formatCurrency(obj.perMonth, fixedAssetDoc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(obj.perYear, fixedAssetDoc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(cumDeprec, fixedAssetDoc.currencyId)}</td>
                            <td>${GlobalFn.formatCurrency(bvEndYear, fixedAssetDoc.currencyId)}</td>
                            <td>${obj.maxMonth}</td>
                            <td>${obj.month}</td>
                        </tr>
                    `;
                    })
                }
            }
            data.dataHtml = dataHtml;
            data.fixedAssetDoc = fixedAssetDoc;

            data.dataHtml = dataHtml;
            return data;
        }
    }
})
